import React, { useState } from 'react';
import styles from './Presets.module.scss';
import { Box, IconButton, List, ListItem, ListItemButton, ListItemIcon, Typography } from '@mui/material';
import { observer } from 'mobx-react-lite';
import onCheckBox from '../../../../assets/portsMenu/noBorderCheckBox.png';
import offCheckBox from '../../../../assets/portsMenu/emptyCheckBox.png';
import { CustomIcon, logServerResponse } from '../../../../helpers/helpers';
import SettingsIcon from '../AdditionalControlPanel/IconConponents/SettingsIcon';
import AddPresetButton from './AddPresetButton/AddPresetButton';
import { useEffect } from 'react';
import ModalEditPreset from './ModalEditPreset/ModalEditPreset';
import connectsState from '../../../../store/connectsState';
import viewsState from '../../../../store/viewsState';
import { prepareZonesData } from '../../../../api/prepareData/prepareZonesData';
import { apiMainGet, apiMainPatch, apiMainPost } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const MENU_STATE_DEFAULT = 'MENU_STATE_DEFAULT';
const MENU_STATE_EDIT = 'MENU_STATE_EDIT';

const defFunction = () => {};
const getSelectedId = (items = []) => items.find(({ isActive }) => isActive)?.id || null;
const getSelectedPreset = (items = [], itemId = null) => items.find(({ id }) => id === itemId);

export default observer(({ items = [], actions = {} }) => {
	const [selectedPresetId, setSelectedPresetId] = useState(getSelectedId(items));
	const [isDisabled, setDisabled] = useState(false);
	const [menuState, setMenuState] = useState(MENU_STATE_DEFAULT);
	const [isModalEdit, setModalEdit] = useState(false);
	const [zones, setZones] = useState([]);

	const menu = {
		[MENU_STATE_DEFAULT]: {
			settingButton: (
				<IconButton className={styles.iconButton} onClick={() => handleClickSettings(MENU_STATE_EDIT)}>
					<SettingsIcon />
				</IconButton>
			),

			presetItem(item) {
				const { id, name, isActive } = item;

				const inlineListItem = {
					display: 'flex',
					justifyContent: 'space-between',

					padding: '6px 21px 6px 10px',
				};

				const { presName, listItemIcon } = styles;
				const icon = isActive ? onCheckBox : offCheckBox;

				const isSame = id === selectedPresetId;
				const resAction = isSame ? defFunction : () => handleChangeActivePreset(id);

				return (
					<ListItemButton key={uuidv4()} style={inlineListItem} disabled={isDisabled} onClick={resAction}>
						<Typography className={presName}>{name}</Typography>

						<ListItemIcon className={listItemIcon}>
							<Box className={styles.switchItem}>
								<CustomIcon icon={icon} name={'Selected Preset'} size={31} />
							</Box>
						</ListItemIcon>
					</ListItemButton>
				);
			},
		},
		[MENU_STATE_EDIT]: {
			settingButton: (
				<IconButton className={styles.iconButton} onClick={() => handleClickSettings(MENU_STATE_DEFAULT)}>
					<SettingsIcon />
				</IconButton>
			),

			presetItem(item) {
				const { id, name, isActive } = item;

				const inlineListItem = {
					height: 45,

					display: 'flex',
					justifyContent: 'space-between',

					padding: '6px 21px 6px 10px',
				};

				const { presName, listItemIcon, editText, settingsItem } = styles;
				const resTextStyle = `${editText} ${isActive && styles.active}`;

				return (
					<ListItem key={uuidv4()} style={inlineListItem} className={settingsItem}>
						<Typography className={presName}>{name}</Typography>

						<ListItemIcon className={`${listItemIcon} ${styles.settings}`}>
							<span className={resTextStyle} onClick={() => handleClickEdit(id)}>
								edit
							</span>
						</ListItemIcon>
					</ListItem>
				);
			},
		},
	};

	useEffect(() => {
		if (isModalEdit) return;

		const url = connectsState.linkAisZonePoints;
		const controller = new AbortController();

		getZones(url, controller);

		return () => {
			controller.abort();
		};
	}, [isModalEdit]);

	const getZones = (url, controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const handleClickSettings = (menu = '') => {
		setMenuState(menu);
	};

	const handleChangeActivePreset = (presId = null) => {
		const { linkChangeActivePreset } = connectsState;
		const { selectedCameraId, cameras } = viewsState;

		const camIndex = cameras.findIndex(({ id }) => id === selectedCameraId);
		if (camIndex === -1) return;

		const { cameraKey } = cameras[camIndex];
		const url = `${linkChangeActivePreset}${cameraKey}/select_preset/${presId}/`;

		setActivePreset(url);
	};

	const setActivePreset = (url = '') => {
		apiMainPost(url).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Новый активный пресет - установлен';
			const errorMsg = 'Не удалось установить новый активный пресет';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			setTimeout(() => {
				setDisabled(false);
			}, 2000);

			setDisabled(true);
		});
	};

	const presetDelete = () => {
		try {
			const { cameras, selectedCameraId } = viewsState;
			const { linkPresetDelete } = connectsState;

			const { cameraKey } = cameras.find(({ id }) => id === selectedCameraId);
			const url = `${linkPresetDelete}${cameraKey}/del_preset/${selectedPresetId}/`;

			apiMainPost(url).then(({ error }) => {
				const { isError } = error;

				const seccessMsg = 'Удаление пресета - успешно';
				const errorMsg = 'Не удалось удалить пресет';

				logServerResponse(isError, seccessMsg, errorMsg);
			});
		} catch (error) {
			console.error(error.message);
		}
	};

	const presetTitle = () => {
		const inlineTitleStyle = {
			fontSize: 18,
			fontWeight: 500,
			fontFamily: 'Inter, sans-serif',
		};

		return (
			<Box className={styles.titleBox}>
				<Typography className={styles.title} sx={inlineTitleStyle}>
					Presets
				</Typography>

				{menu[menuState].settingButton}
			</Box>
		);
	};

	const handleClickEdit = (presId = null) => {
		setSelectedPresetId(presId);
		setModalEdit(true);
	};

	const modalEditPreset = () => {
		const modalPositionStyle = {
			position: 'absolute',
			top: '50%',
			left: '50%',
			transform: 'translate(-50%, -50%)',
		};

		const preset = getSelectedPreset(items, selectedPresetId);

		const handleSave = (preset = { name: '', zoneId: null }) => {
			const { linkPresetEdit } = connectsState;
			const url = `${linkPresetEdit}${selectedPresetId}/`;

			const parameters = { name: preset.name };

			apiMainPatch(url, parameters).then(({ error }) => {
				const { isError } = error;

				const seccessMsg = 'Изменение названия пресета - успешно';
				const errorMsg = 'Не удалось изменить название пресета';

				logServerResponse(isError, seccessMsg, errorMsg);

				if (isError) return;

				actions.changeName(selectedPresetId, preset.name);
			});
		};

		const handleDelete = () => {
			presetDelete(selectedPresetId);
		};

		return (
			<ModalEditPreset
				isShow={isModalEdit}
				setShow={setModalEdit}
				positionStyle={modalPositionStyle}
				defText={preset.name}
				zones={zones}
				title={'Edit'}
				label={'Preset name'}
				btnNameOk={'Change name'}
				handleOk={(name = '') => handleSave(name)}
				btnCancelStyle={{ color: '#E20000' }}
				btnNameCancel={'Delete preset'}
				handleCancel={handleDelete}
				errorMsg={'You cannot create a preset without a name'}
			/>
		);
	};

	return (
		<Box className={styles.presetMenu}>
			<div>
				{presetTitle()}

				<List className={styles.list} disablePadding>
					{items.map((item) => menu[menuState].presetItem(item))}
				</List>
			</div>

			<div className={styles.confirmButtonsPos}>
				<AddPresetButton addAction={actions.addPreset} />
			</div>

			{isModalEdit && modalEditPreset()}
		</Box>
	);
});
