import { ListItem } from '@mui/material';
import styles from './ItemTable.module.scss';
import React from 'react';

export const buttons = {
	icons: {
		color: 'var(--bgButtonIcon)',
		'&:active': {
			color: '#FF992C',
		},
		'&:hover': {
			color: '#FF992C',
		},
	},
};

const ItemTable = ({ item = {} }) => {
	const background = item.isWorking ? '#12B76A' : '#FF0000';

	return (
		<ListItem>
			<div className={styles.tableText}>
				<div className={styles.imgConditional} style={{ background }} />
				<div>{item.name}</div>
				<div>{item.ip}</div>
			</div>
		</ListItem>
	);
};

export default ItemTable;
