import React, { useState, useEffect } from 'react';
import styles from './Translation.module.scss';
import { createTheme, ThemeProvider } from '@mui/material';
import { useTheme } from '../../Theme/darkTheme';
import LowerMenu from './LowerMenu/LowerMenu';
import viewsState from '../../store/viewsState';
import connectsState from '../../store/connectsState';
import CardsPlayers from './CardsPlayers/CardsPlayers';
import ModalViews from '../../helpers/ModalViews/ModalViews';
import ItemPlayerModal from './ItemPlayerModal/ItemPlayerModal';
import { observer } from 'mobx-react-lite';
import { prepareStreamsData } from '../../api/prepareData/prepareStreamsData';
import { logServerResponse } from '../../helpers/helpers';
import { apiMainGet } from '../../api/mainApi';

const Translation = observer(() => {
	const { showWindows, isFullScreenView, streams, selectTranslation, setFullScreenView, setShowWindows } = viewsState;
	const { linkGetStreams } = connectsState;

	const [currentPage, setCurrentPage] = useState(1);
	const [sizeItem, setSizeItem] = useState(6);

	const lastPlayerIndex = currentPage * showWindows;
	const firstPlayerIndex = lastPlayerIndex - showWindows;

	const lastPage = Math.ceil(streams.length / showWindows);
	const [editedStreams, setEditedStreams] = useState([...streams]);
	const [isShowModal, setShowModal] = useState(false);

	const mode = useTheme();

	const darkTheme = createTheme({
		palette: {
			mode: mode,
		},
	});

	useEffect(() => {
		if (!linkGetStreams) return;

		const controller = new AbortController();

		getStreams(linkGetStreams, controller);

		return () => {
			controller.abort();
		};
	}, [linkGetStreams]);

	const getStreams = (url = '', controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных стримов - успешно';
			const errorMsg = 'Не удалось получить данные стримов';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const streams = prepareStreamsData(data);

			viewsState.setStreams(streams);
			setEditedStreams(JSON.parse(JSON.stringify(streams)));
		});
	};

	const handleChangeStreams = (playerId, selectId) => {
		const oldIndex = editedStreams.findIndex(({id}) => id === playerId);
		const newIndex = editedStreams.findIndex(({id}) => id === selectId);

		const oldPlayer = {...editedStreams[oldIndex]};
		editedStreams[oldIndex] = {...editedStreams[newIndex]};
		editedStreams[newIndex] = oldPlayer;

		setEditedStreams([...editedStreams]);

		const newPlayers = editedStreams.slice(firstPlayerIndex, lastPlayerIndex);

		return newPlayers;
	};

	const handleEsc = (event) => {
		if (event.keyCode === 27) {
			setFullScreenView(false);
		}
	};

	useEffect(() => {
		window.addEventListener('keydown', handleEsc);
		return () => window.removeEventListener('keydown', handleEsc);
	}, []);

	const handleFullScreen = () => {
		setFullScreenView(true);
	};

	const handleViews = (numViewCameras) => {
		setShowWindows(numViewCameras);
		setCurrentPage(1);
		const size = 12 / Math.sqrt(numViewCameras);
		setSizeItem(size);
	};

	const changePage = (n = null) => {
		setCurrentPage((prev) => prev + n);
	};

	return (
		<ThemeProvider theme={darkTheme}>
			<div className={styles.parent}>
				<CardsPlayers
					players={editedStreams.slice(firstPlayerIndex, lastPlayerIndex)}
					sizeItem={sizeItem}
					isFullScreen={isFullScreenView}
					setShowModal={setShowModal}
					handleChangeStriams={(playerId = null, selectId = null) => handleChangeStreams(playerId, selectId)}
				/>

				{!isFullScreenView && (
					<LowerMenu
						currentPage={currentPage}
						lastPage={lastPage}
						showWindows={showWindows}
						handleViews={handleViews}
						changePage={(n = null) => changePage(n)}
						fullScreen={handleFullScreen}
					/>
				)}

				<ModalViews isModal={isShowModal} setShowModal={setShowModal}>
					<ItemPlayerModal
						translation={selectTranslation}
						setShowModal={setShowModal}
					/>
				</ModalViews>
			</div>
		</ThemeProvider>
	);
});

export default Translation;
