import React, { useState, useEffect } from 'react';
import styles from './EditZone.module.scss';
import ArrowBackIosOutlinedIcon from '@mui/icons-material/ArrowBackIosOutlined';
import { Box, ListItemText } from '@mui/material';
import { styled } from '@mui/material';
import pen from '../../../../assets/portsMenu/pen.png';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import portState from '../../../../store/portState';
import zonesState from '../../../../store/zonesState';
import connectsState from '../../../../store/connectsState';
import DeleteZone from './DeleteZone/DeleteZone';
import { checkAllSelected, logServerResponse } from '../../../../helpers/helpers.js';
import { observer } from 'mobx-react-lite';
import { prepareZonesData } from '../../../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../../../api/prepareData/prepareSectorsData';
import { apiMainGet, apiMainPatch } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const ModArrow = styled(ArrowBackIosOutlinedIcon)({
	marginRight: '20px',
	cursor: 'pointer',
	color: '#5F5F5F',

	'&:hover': {
		color: '#FF992C',
	},
});

const EditZone = observer(() => {
	const { zones, editZone, zoneTypes, selectedSectorId, setVisibleZone, setEditZone, setZones, setSectors } = zonesState;
	const { shipsClasses, portMenuList, setCreateZone } = portState;

	const [isEditInput, setEditInput] = useState(false);
	const [tempNameZone, setTempNameZone] = useState('');
	const [isDeleteZone, setDeleteZone] = useState(false);
	const [typesArray, setTypesArray] = useState(editZone.blacklist);
	const [isAllTypesSelected, setIsAllTypesSelected] = useState(false);

	useEffect(() => {
		setIsAllTypesSelected(checkAll());
	}, [typesArray]);

	const resultCheck = checkAllSelected(portMenuList) && checkAllSelected(zones);

	const acceptEdit = () => {
		setEditInput(false);
		setEditZone({ ...editZone, name: tempNameZone });
	};

	const closeEditMenu = () => {
		setVisibleZone(selectedSectorId, editZone.id, resultCheck);
		setEditZone({});
	};

	const closeDeleteMenu = () => {
		setDeleteZone(false);
	};

	const closeEdit = () => {
		setEditInput(false);
	};

	const getZones = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const updateZone = (url, zone) => {
		apiMainPatch(url, zone).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Обновление зоны - успешно';
			const errorMsg = 'Не удалось обновить зону';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const handleUpdateZone = (zone) => {
		const { linkZones, linkFullSectors } = connectsState;

		updateZone(`${linkZones}${editZone.id}/`, zone);

		setEditZone({});

		setTimeout(() => {
			getFullSectors(linkFullSectors);
			getZones(linkZones);
		}, 500);
	};

	const checkAll = () => {
		const numSelItems = typesArray.length;
		const numItems = shipsClasses.length;

		return numSelItems === numItems;
	};

	const handleAllTypes = () => {
		setIsAllTypesSelected((prev) => !prev);

		if (isAllTypesSelected) {
			setTypesArray([]);
		} else {
			setTypesArray(shipsClasses.map(({ id }) => id));
		}
	};

	const handleSelectClass = (id) => {
		const index = typesArray.findIndex((item) => item === id);

		const isChangeShowType = index !== -1;
		if (isChangeShowType) {
			setTypesArray(typesArray.filter((item) => item !== id));
		} else {
			setTypesArray([...typesArray, id]);
		}
	};

	const zoneEditsMenu = () => {
		return (
			<div className={styles.mainEdits}>
				<div className={styles.nameInput}>Prohibited ship classes</div>
				<Box className={styles.ListCameras}>
					<div className={styles.allSelect} onClick={handleAllTypes}>
						Select All
						{isAllTypesSelected && <CheckIcon style={{ color: '#FF992C' }} />}
					</div>

					{shipsClasses.map(({ id, name }) => {
						const index = typesArray.findIndex((item) => item === id);
						const isTypeSelected = index !== -1;

						return (
							<div key={uuidv4()} className={styles.List} value={name} onClick={() => handleSelectClass(id)}>
								<ListItemText value={name} primary={name} />
								{isTypeSelected && <CheckIcon style={{ color: '#FF992C' }} />}
							</div>
						);
					})}
				</Box>

				<div className={styles.nameInput}>Zone type</div>
				<select
					className={styles.fieldText}
					value={editZone.type}
					onChange={(e) => setEditZone({ ...editZone, type: e.target.value })}
				>
					{zoneTypes.map(({ id, name }) => (
						<option key={uuidv4()} value={id}>
							{name}
						</option>
					))}
				</select>

				<div className={styles.editSize} onClick={() => setCreateZone(true)}>
					Change zone
				</div>

				<br />

				<div className={styles.deleteEdit} onClick={() => setDeleteZone(true)}>
					Delete zone
				</div>
			</div>
		);
	};

	return isDeleteZone ? (
		<DeleteZone close={closeDeleteMenu} />
	) : (
		<>
			<div className={styles.header}>
				<ModArrow onClick={() => closeEditMenu()} />
				Zone editing
			</div>

			{!isEditInput ? (
				<div className={styles.editName}>
					{editZone.name}
					<img className={styles.pen} src={pen} onClick={() => setEditInput(true)} />
				</div>
			) : (
				<div className={styles.edit}>
					<input
						className={styles.fieldTextEdit}
						maxLength="16"
						onChange={(e) => setTempNameZone(e.target.value)}
					/>
					<CheckIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={acceptEdit} />
					<CloseIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={closeEdit} />
				</div>
			)}

			{zoneEditsMenu()}

			<div className={styles.button}>
				<button className={styles.modBtn} onClick={() => handleUpdateZone({ ...editZone, blacklist: typesArray })}>
					Save Changes
				</button>
			</div>
		</>
	);
});

export default EditZone;
