import React, { useState } from 'react';
import styles from './ReconnectTime.module.scss';
import { Box, Button, List, ListItemButton, Typography } from '@mui/material';
import { observer } from 'mobx-react-lite';
import RadioIcon from '../AdditionalControlPanel/IconConponents/RadioIcon';
import viewsState from '../../../../store/viewsState';
import { useEffect } from 'react';
import connectsState from '../../../../store/connectsState';
import { logServerResponse } from '../../../../helpers/helpers';
import { apiMainPatch, apiMainPost } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const btnData = {
	true: {
		name: 'Patrol is Active',
		postFix: styles.on,
	},

	false: {
		name: 'Patrol Off',
		postFix: styles.off,
	},
};

export default observer(({ items = [], action = () => {} }) => {
	const { reconnectMenu, confirmButtons, save } = styles;
	const { selectedCameraId, cameras } = viewsState;

	const [isMove, setIsMove] = useState(false);
	const [isDisabled, setIsDisabled] = useState(false);

	useEffect(() => {
		const index = cameras.findIndex(({ id }) => id === selectedCameraId);
		const startIsMove = index === -1 ? false : cameras[index].isPatrol;

		setIsMove(startIsMove);
	}, [selectedCameraId]);

	const handleChangePresetMove = () => {
		const { selectedCameraId, cameras } = viewsState;
		const index = cameras.findIndex(({ id }) => id === selectedCameraId);

		if (index === -1) return;
		const { cameraKey } = cameras[index];

		const { linkPartolStart } = connectsState;

		const isStatus = !isMove;
		const data = { is_patrol_active: isStatus };
		const action = isStatus ? 'start_patrol' : 'stop_patrol';

		const url = `${linkPartolStart}${cameraKey}/${action}/`;

		apiMainPost(url, data).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Удаление устройства - успешно';
			const errorMsg = 'Не удалось удалить устройство';

			logServerResponse(isError, seccessMsg, errorMsg);
			setIsMove(() => isStatus);
		});
	};

	const handleSelectReconnectTime = (itemId = null) => {
		const { selectedCameraId, cameras } = viewsState;
		const camIndex = cameras.findIndex(({ id }) => id === selectedCameraId);
		if (camIndex === -1) return;

		const { cameraKey } = cameras[camIndex];

		const itemIndex = items.findIndex(({ id }) => id === itemId);
		if (itemIndex === -1) return;

		const { linkCameras } = connectsState;
		const url = `${linkCameras}${cameraKey}/`;
		const data = { time_switch_presets: items[itemIndex].num };

		apiMainPatch(url, data).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Патрулирование устройства - изменено';
			const errorMsg = 'Не удалось изменить патрулирование устройства';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			setTimeout(() => {
				setIsDisabled(false);
			}, 2000);

			setIsDisabled(true);

			action(itemId);
		});
	};

	const { name, postFix } = btnData[isMove];

	return (
		<Box className={reconnectMenu}>
			<Box>
				<Box className={styles.titleBox}>
					<span className={styles.title}>Patrol time</span>
				</Box>

				<List className={styles.list} disablePadding>
					{items.map(({ id, name, isActive }) => {
						const inlineListItem = {
							display: 'flex',
							justifyContent: 'flex-start',

							padding: '8px 16px',
						};

						const { roundIndicator, recName } = styles;
						const color = isActive ? 'hsl(31, 100%, 60%)' : 'var(--colorText)';

						return (
							<ListItemButton
								key={uuidv4()}
								style={inlineListItem}
								disabled={isDisabled}
								onClick={() => handleSelectReconnectTime(id)}
							>
								<div className={roundIndicator}>
									<RadioIcon color={color} isSelected={isActive} />
								</div>

								<Typography className={recName}>{name}</Typography>
							</ListItemButton>
						);
					})}
				</List>
			</Box>

			<div className={confirmButtons}>
				<Button className={`${save} ${postFix}`} onClick={handleChangePresetMove}>
					{name}
				</Button>
			</div>
		</Box>
	);
});
