import React, { useState, useEffect } from 'react';
import styles from './ListMenu.module.scss';
import ItemMenu from './ItemMenu/ItemMenu';
import portState from '../../../../../store/portState';
import ShowAllButton from './ShowAllButton/ShowAllButton.js';
import { observer } from 'mobx-react-lite';
import { checkAllSelected } from '../../../../../helpers/helpers.js';
import zonesState from '../../../../../store/zonesState';
import EmptyMessage from '../../../../../helpers/EmptyMessage/EmptyMessage.js';
import { v4 as uuidv4 } from 'uuid';
import { PORT_MENU_TITLE_SECTORS, PORT_MENU_TITLE_SHIPS } from '../../../../../helpers/defaultData';

const ListMenu = observer(({ isZones = false }) => {
	const { portMenuTitle, portMenuList, isViewAll, setShowShipsStore, setSelectedShipsMmsi } = portState;
	const { zones, setShowZonesStore, setShowSectorsStore, setAllZones } = zonesState;

	const [isSelectAll, setSelectAll] = useState(false);
	const [handleItem, setHandleItem] = useState(false);

	const isEmpty = !portMenuList.length;
	const resultStyle = isZones ? styles.itemsMenuZones : styles.itemsMenu;

	useEffect(() => {
		const objShow = {};
		
		if (portMenuTitle === PORT_MENU_TITLE_SECTORS) {
			portMenuList.forEach(({ id, isShow }) => (objShow[id] = isShow));
			setShowSectorsStore(objShow);

			const objZones = {};
			zones.forEach(({ id, isShow }) => (objZones[id] = isShow));
			setShowZonesStore(objZones);
		}
		if (portMenuTitle === PORT_MENU_TITLE_SHIPS) {
			portMenuList.forEach(({ mmsi, isShow }) => (objShow[mmsi] = isShow));
			setShowShipsStore(objShow);

			let selectedShip = [];
			portMenuList.forEach(({ mmsi, isShow }) => {
				isShow && selectedShip.push(mmsi);
			});

			setSelectedShipsMmsi(selectedShip);
		}
	}, [handleItem, isSelectAll]);

	useEffect(() => {
		if (portMenuTitle === PORT_MENU_TITLE_SECTORS) {
			const resultCheck = checkAllSelected(portMenuList) && checkAllSelected(zones);

			setSelectAll(resultCheck);
		} else {
			setSelectAll(checkAllSelected(portMenuList));
		}
	}, [portMenuTitle, handleItem, isViewAll, portMenuList]);

	const handleSelectAll = () => {
		setSelectAll((prev) => !prev);

		portMenuList.forEach((item) => (item.isShow = !isSelectAll));

		if (portMenuTitle === PORT_MENU_TITLE_SECTORS) {
			setAllZones(!isSelectAll);
		}
	};

	const someFunction = () => {
		const isMoreThenOne = !!portMenuList.length;

		return (
			<>
				{isMoreThenOne && <ShowAllButton isSelectAll={isSelectAll} action={handleSelectAll} />}

				{portMenuList.map((item) => (
					<ItemMenu
						key={uuidv4()}
						item={item}
						isZones={isZones}
						setSelectAll={setSelectAll}
						setHandleItem={setHandleItem}
					/>
				))}
			</>
		);
	};

	return <div className={resultStyle}>{isEmpty ? <EmptyMessage /> : someFunction()}</div>;
});

export default ListMenu;
