import { Box } from '@mui/material';
import React, { useEffect, useState } from 'react';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom';
import CameraViewPage from '../CameraViewPage/CameraViewPage';
import styles from './MonitorPage.module.scss';
import RadarView from '../../components/Radar/RadarView';
import cameraImage from '../../assets/portsMenu/camera.png';
import radarImage from '../../assets/portsMenu/radar.png';
import viewsState from '../../store/viewsState';
import radarsState from '../../store/radarsState';
import SearchMenu from '../../components/SearchMenu/SearchMenu';
import Header from '../../components/Header/Header';
import connectsState from '../../store/connectsState';
import { observer } from 'mobx-react-lite';
import { prepareCamerasFullData } from '../../api/prepareData/prepareCamerasData';
import { logServerResponse } from '../../helpers/helpers';
import { apiMainGet } from '../../api/mainApi';

export default observer(() => {
	const { path, url } = useRouteMatch();

	const [isLoad, setIsLoad] = useState(true);

	useEffect(() => {
		if (!isLoad) return;

		const url = connectsState.linkCamerasList;
		const controller = new AbortController();
		
		getCameras(url, controller);

		return () => {
			controller.abort();
		};
	}, [isLoad, connectsState.linkCamerasList]);

	const getCameras = (url, controller) => {
		apiMainGet(url, controller).then(({ error, data }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных камер - успешно';
			const errorMsg = 'Не удалось получить данные камер';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) {
				viewsState.setMonitorCameras([]);
				return;
			}

			const { setMonitorCameras } = viewsState;
			
			const cameras = prepareCamerasFullData(data);
			setMonitorCameras(cameras);
			
			setIsLoad(false);
		});
	};

	const cameraSelectId = (camId = null) => {
		viewsState.setSelectCameraId(camId);
	};

	const camerasGetData = () => viewsState.cameras;

	const radarSelectId = (radarId = null) => {
		radarsState.setSelectedRadarId(radarId);
	};
	const radarsSaveData = (radars = []) => {
		radarsState.setRadars(radars);
	};
	const radarsGetData = () => radarsState.radars;

	const leftMenuButtons = [
		{
			id: 0,
			title: 'Select a Camera',
			name: 'Cameras',
			icon: cameraImage,
			path: `${url}/cameraview`,
			url: 'cameras',
			curDataUrl: connectsState.linkCameraFullInfo,
			actions: {
				selectItemId: (camId = null) => cameraSelectId(camId),
				saveData: () => {},
				getData: () => camerasGetData(),
				getSelectedId: () => viewsState.selectedCameraId,
			},
		},
		{
			id: 1,
			title: 'Select a Radar',
			name: 'Radars',
			icon: radarImage,
			path: `${url}/radarview`,
			url: 'radars',
			curDataUrl: '',
			actions: {
				selectItemId: (radarId = null) => radarSelectId(radarId),
				saveData: (radars = []) => radarsSaveData(radars),
				getData: () => radarsGetData(),
				getSelectedId: () => radarsState.selectedRadarId,
			},
		},
	];

	return (
		<Box className={styles.monitorPage}>
			<Header />

			<Box className={`${styles.menu01} ${styles.left}`}>
				<SearchMenu buttons={leftMenuButtons} />
			</Box>

			<Switch>
				<Route
					path={`${path}/cameraview`}
					component={() => <CameraViewPage isShowRightMenu={!!viewsState.cameras.length} />}
				/>
				<Route path={`${path}/radarview`} component={RadarView} />
				<Route exact path={path}>
					<Redirect to={`${path}/cameraview`} />
				</Route>
			</Switch>
		</Box>
	);
});
