import { Box, Button, IconButton, InputAdornment, styled, TextField, Typography } from '@mui/material';
import React, { useState } from 'react';
import styles from './ModalAddPreset.module.scss';
import { CustomIcon } from '../../../../../helpers/helpers';
import clearIcon from '../../../../../assets/portsMenu/ClearIcon.png';
import viewsState from '../../../../../store/viewsState';

const defaultPreset = { id: null, name: '', isActive: false };

const CssTextField = styled(TextField)({
	'& label.Mui-focused': {
		backgroundColor: 'transparent',
	},
	'& .MuiInputBase-root': {
		color: 'var(--colorText)',
		fontWeight: 400,
	},
	'& .MuiOutlinedInput-root': {
		'& fieldset': {
			borderColor: '#c9c9c9',
		},
		'&:hover fieldset': {
			borderColor: '#c9c9c9',
		},
		'&.Mui-focused fieldset': {
			borderColor: '#c9c9c9',
		},
	},
});

export default () => {
	const [presetName, setPresetName] = useState('');
	const [isError, setError] = useState(false);

	const textError = isError ? 'You cannot create a preset without a name' : '';
	const isShowClearIcon = Boolean(presetName.length);

	const handleChangePresetName = (e) => {
		setPresetName(() => e.target.value);
	};

	const handleClearTextField = () => {
		setPresetName('');
	};

	const handleCreatePreset = () => {
		if (!presetName.length) {
			setError(true);
			return;
		}

		const { selectedCameraId } = viewsState;
		const { getIncPresetId, addPreset } = viewsState;

		const newPreset = { ...defaultPreset, id: getIncPresetId(), name: presetName };
		addPreset(selectedCameraId, newPreset);

		handleCanselCreatePreset();
	};

	const handleCanselCreatePreset = () => {
		viewsState.setVisibleModalAddPreset(false);
		setPresetName('');
		setError(false);
	};

	return (
		<Box className={styles.container}>
			<Typography className={styles.modalTitle}>Create a preset</Typography>

			<Box className={styles.contentPosition}>
				<Box mt={'18px'}>
					<Typography className={styles.inputLabel}>Preset name</Typography>
					<CssTextField
						value={presetName}
						variant="outlined"
						size="medium"
						margin="dense"
						autoComplete="off"
						fullWidth
						onChange={handleChangePresetName}
						error={isError}
						helperText={textError}
						InputProps={{
							endAdornment: isShowClearIcon && (
								<InputAdornment position="end">
									<IconButton
										className={styles.iconButton}
										aria-label="Clear Input"
										edge="end"
										onClick={handleClearTextField}
									>
										<CustomIcon icon={clearIcon} name={'clear icon'} size={32} />
									</IconButton>
								</InputAdornment>
							),
						}}
					/>
				</Box>

				<Box className={styles.buttonBox}>
					<Button className={styles.create} fullWidth onClick={handleCreatePreset}>
						Create Preset
					</Button>
					<Button className={styles.cansel} fullWidth onClick={handleCanselCreatePreset}>
						Cancel
					</Button>
				</Box>
			</Box>
		</Box>
	);
};
