import React, { useState } from 'react';
import styles from './ReportHead.module.scss';
import { IconButton } from '@mui/material';
import ArrowDownIcon from '../../Archive/Cards/Card/Static/ArrowDownIcon';
import CheckIcon from '@material-ui/icons/Check';
import { v4 as uuidv4 } from 'uuid';

const ReportHead = ({ names, action }) => {
	const [selectedItem, setSelectedItem] = useState(0);
	const [isShowList, setShowList] = useState(false);

	const selectedName = names[selectedItem];
	const color = `${isShowList ? '#FF992C' : 'var(--bgThemeTitle)'}`;
	const resStyle = `${styles.listArchive} ${names.length === 3 ? styles.right : styles.left}`;

	const handleShowList = () => {
		setShowList((prevShowList) => !prevShowList);
	};

	const handler = function (i, index) {
		setSelectedItem(index);
		action(index);
		setShowList(false);
	};

	return (
		<div className={styles.select}>
			<div style={{ color: `${color}` }} onClick={handleShowList}>
				{selectedName.name}
			</div>

			<IconButton onClick={handleShowList}>
				<ArrowDownIcon color={color} />
			</IconButton>

			{isShowList && (
				<div className={resStyle}>
					{names.map(({ id, name }) => {
						const isSelected = selectedItem === id;

						return (
							<div key={uuidv4()} className={styles.listItem} onClick={() => handler(name, id)}>
								{name}
								{isSelected && <CheckIcon style={{ color: '#FF992C' }} />}
							</div>
						);
					})}
				</div>
			)}
		</div>
	);
};

export default ReportHead;
