export const PORT_MENU_TITLE_SHIPS = 'Vessel selection';
export const PORT_MENU_TITLE_CAMERAS = 'Camera control';
export const PORT_MENU_TITLE_RADARS = 'Radar selection';
export const PORT_MENU_TITLE_AISES = 'Choice AIS';
export const PORT_MENU_TITLE_SECTORS = 'Sector Management';

const getCopy = (data) => JSON.parse(JSON.stringify(data));

const idCounter = {
	recItemId: 0,
	recTimeId: 0,
	presetsId: 0,
};

const recognition = [
	{ id: idCounter.recItemId++, name: 'Ships', isSelected: false },
	{ id: idCounter.recItemId++, name: 'People', isSelected: false },
	{ id: idCounter.recItemId++, name: 'Pollution', isSelected: false },
	{ id: idCounter.recItemId++, name: 'Foreign objects', isSelected: false },
];

export const defaultReconnectTime = [
	{ id: idCounter.recTimeId++, name: '5 minutes', num: 5 * 60, isActive: true, isDisabled: false },
	{ id: idCounter.recTimeId++, name: '10 minutes', num: 10 * 60, isActive: false, isDisabled: false },
	{ id: idCounter.recTimeId++, name: '15 minutes', num: 15 * 60, isActive: false, isDisabled: false },
];

const controlMenu = {
	recognition,
	reconnectTime: defaultReconnectTime,
	presets: [],
};

const videoResolution = ['720', '480', '360'];

export const getDefaultCamera = () => {
	const camera = {
		id: null,
		name: '',
		isWorking: false,
		isPatrol: false,
		cameraKey: '',
		urls: videoResolution,
		controlMenu,
		loc: [0, 0],
		isShow: true,
		strims: { 360: '', 480: '', 720: ''},
		sector: 'Default Sectors',
		zone: 'Default Zones',
		isSelected: true
	};

	return getCopy(camera);
};
