export const prepareRecordsData = (records = []) => {
	return records.map((record) => {
		const { id, camera_id, camera_name, name, nginx_path, start_date, thumbnail_url, rec_duration } = record;

		return {
			id,
			camId: camera_id,
			camName: camera_name,
			name,
			path: nginx_path,
			date: start_date,
			img: thumbnail_url,
			duration: rec_duration,
			isDelete: false,
		};
	});
};
