import React, { useState } from 'react';
import styles from './VisibleBox.module.scss';
import arrowDown from '../../../../../assets/images/ArrowDown.png';
import arrowUp from '../../../../../assets/images/ArrowUp.png';
import { observer } from 'mobx-react-lite';
import portState from '../../../../../store/portState';
import zonesState from '../../../../../store/zonesState';
import AllSelectedOnMap from './AllSelectedOnMap/AllSelectedOnMap';
import { v4 as uuidv4 } from 'uuid';

const VisibleBox = observer(() => {
	const { portMenuList } = portState;
	const { zones, setAllZones, setAllSectors } = zonesState;

	const [isVisibleOnMap, setVisibleOnMap] = useState(false);

	const imageHidden = isVisibleOnMap ? arrowDown : arrowUp;

	const handleVisible = () => {
		setVisibleOnMap((prev) => !prev);
	};

	const visibleItems = [
		{ id: 1, title: 'Zones', items: zones, action: (check) => setAllZones(check) },
		{ id: 2, title: 'Sectors', items: portMenuList, action: (check) => setAllSectors(check) },
	];

	const showAllSelectedOnMap = ({ title, items, action }) => (
		<AllSelectedOnMap key={uuidv4()} title={title} items={items} action={action} />
	);

	const boxHidden = () => {
		return <div className={styles.visibleBox}>{visibleItems.map(showAllSelectedOnMap)}</div>;
	};

	return (
		<>
			{isVisibleOnMap && boxHidden()}

			<button className={styles.btnViewZones} onClick={handleVisible}>
				Enable Display
				<div className={styles.boxHidden}>
					<img className={styles.iconHidden} src={imageHidden} />
				</div>
			</button>
		</>
	);
});

export default VisibleBox;
