import React from 'react';
import styles from './ArrowCameraControlView.module.scss';
import { Box, Grid } from '@mui/material';
import arrowIcon from '../../../../assets/portsMenu/arrowNew.png';
import ArrowButton from '../ArrowButton/ArrowButton';

const defaultActions = {
	aTop: () => {},
	aRight: () => {},
	aBottom: () => {},
	aLeft: () => {},
};

export default ({ actions = defaultActions, isDisabled = false }) => {
	const size = 30;

	const { gridContainer, gridItem, center } = styles;
	const { aTop, aRight, aBottom, aLeft, aStopMes } = actions;
	
	const createActions = (down) => ({ up: () => aStopMes(), down });

	return (
		<Grid container className={gridContainer} spacing={1}>
			<Grid item xs={12} className={gridItem}>
				<ArrowButton 
					icon={arrowIcon} 
					alt={'Top Arrow'} 
					size={size} 
					direction={'top'} 
					isDisabled={isDisabled}
					actions={createActions(() => aTop('up'))} 
				/>
			</Grid>

			<Grid item xs={4} className={gridItem}>
				<ArrowButton
					icon={arrowIcon}
					alt={'Left Arrow'}
					size={size}
					direction={'left'}
					isDisabled={isDisabled}
					actions={createActions(() => aLeft('left'))} 
				/>
			</Grid>
			<Grid item xs={4} className={gridItem}>
				<Box className={center} />
			</Grid>
			<Grid item xs={4} className={gridItem}>
				<ArrowButton
					icon={arrowIcon}
					alt={'Right Arrow'}
					size={size}
					direction={'right'}
					isDisabled={isDisabled}
					actions={createActions(() => aRight('right'))}
				/>
			</Grid>

			<Grid item xs={12} className={gridItem}>
				<ArrowButton
					icon={arrowIcon}
					alt={'Bottom Arrow'}
					size={size}
					direction={'bottom'}
					isDisabled={isDisabled}
					actions={createActions(() => aBottom('down'))}
				/>
			</Grid>
		</Grid>
	);
};
