import React, { useEffect, useState } from 'react';
import './TimeSelectedPath.css';
import { Slider, styled } from '@mui/material';
import 'primereact/resources/primereact.css';
import 'primeicons/primeicons.css';
import 'primeflex/primeflex.css';
import { Calendar } from 'primereact/calendar';
import { addLocale } from 'primereact/api';
import imgCalendar from '../../../assets/calendar.png';
import close from '../../../assets/images/close.png';
import { observer } from 'mobx-react-lite';
import portState from '../../../store/portState';

const TimeSlider = styled(Slider)({
	width: '90%',
	'& .MuiSlider-track': {
		height: '20%',
		color: '#FFCA8C',
	},
	'& .MuiSlider-rail': {
		height: '8px',
		color: 'white',
		opacity: '1',
	},
	'& .MuiSlider-thumb': {
		color: '#FF992C',
	},
});

const subtractDay = (date, day) => {
	const dateTime = date.setDate(date.getDate() - day);

	return new Date(dateTime);
};

const TimeSelectedPath = observer(() => {
	const { calendarDates, calendarSliderTimes, setCalendarDates, setCalendarSliderTimes, setFormatDates } = portState;

	const [defaultCalendar, setDefaultCalendar] = useState(calendarDates);
	const [defaultSlider, setDefaultSlider] = useState(calendarSliderTimes);
	const [isVisibleSlider, setVisibleSlider] = useState(false);

	useEffect(() => {
		setVisibleSlider(calendarDates[1] && calendarDates[0]?.getTime() !== calendarDates[1]?.getTime());
	}, [calendarDates]);

	addLocale('en', {
		firstDayOfWeek: 1,
		dayNamesMin: ['su', 'mo', 'tu', 'we', 'th', 'fr', 'sa'],
		monthNames: ['January', 'February', 'March', 'April', 'May', 'June', 'July', 'August', 'September', 'October', 'November', 'December'],
		monthNamesShort: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep', 'Oct', 'Nov', 'Dec'],
	});

	const deleteDates = () => {
		setFormatDates([]);
		setCalendarDates([]);
		setCalendarSliderTimes([]);
		setDefaultCalendar([subtractDay(new Date(), 365), new Date()]);
	};

	const handleSlider = (e, value) => {
		setCalendarSliderTimes(value);
		setCalendarDates([new Date(value[0]), new Date(value[1])]);
		editFormatDate([value[0], value[1]]);
	};

	const calculateDate = (date, days) => {
		const minCal = date.setDate(date.getDate() - days);
		const maxCal = date.setDate(date.getDate() + 6);

		setDefaultCalendar([new Date(minCal), new Date(maxCal)]);
		setDefaultSlider([new Date(minCal).getTime(), new Date(maxCal).getTime()]);
	};

	const handleCalendar = (e) => {
		const nextTime = e.value[1] ? e.value[1].getTime() : e.value[0].getTime();
		calculateDate(new Date(e.value[0]), 3);
		setCalendarDates(e.value);
		setCalendarSliderTimes([e.value[0].getTime(), nextTime]);
		if (e.value[1]) {
			editFormatDate([e.value[0], e.value[1]]);
			setDefaultCalendar(e.value);
			setDefaultSlider([e.value[0].getTime(), e.value[1].getTime()]);
		}
	};

	const editFormatDate = (dates) => {
		const resultDate = [];
		dates.map((date) => {
			const dateObj = new Date(date);
			const year = dateObj.getUTCFullYear();
			const month = dateObj.getUTCMonth() + 1;
			const day = dateObj.getUTCDate() + 1;

			resultDate.push(month.toString().length ? `${year}-0${month}-${day}` : `${year}-${month}-${day}`);
		});
		setFormatDates(resultDate);
	};

	return (
		<div className="containerTime">
			{isVisibleSlider && (
				<div className="slider">
					<TimeSlider
						value={calendarSliderTimes.length ? calendarSliderTimes : []}
						onChange={handleSlider}
						disableSwap
						max={defaultSlider[1]}
						min={defaultSlider[0]}
					/>
				</div>
			)}

			<div className="dataRange">
				<img className="imgCalendar" src={imgCalendar} />

				<Calendar
					value={calendarDates.length ? calendarDates : []}
					onChange={handleCalendar}
					maxDate={defaultCalendar[1]}
					minDate={defaultCalendar[0]}
					selectionMode="range"
					readOnlyInput
					locale="en"
					dateFormat="dd.mm.yy"
				/>

				{!!calendarDates.length && (
					<div className="closing">
						<img className="closeIcon" src={close} onClick={deleteDates} />
					</div>
				)}
			</div>
		</div>
	);
});

export default TimeSelectedPath;
