import React, { useState } from 'react';
import styles from './SelectCameras.module.scss';
import IconButton from '@mui/material/IconButton';
import { Box, ClickAwayListener, InputBase } from '@mui/material';
import CheckIcon from '@material-ui/icons/Check';
import { SearchOutlined } from '@material-ui/icons';
import ArrowRightIcon from '../Card/Static/ArrowRightIcon';
import eventArchiveState from '../../../../store/eventArchiveState';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const SelectCameras = observer(({ numberCard }) => {
	const availableDevices = eventArchiveState.availableDevices[numberCard];
	const devices = eventArchiveState.selectedDevices[numberCard];

	const [userFilter, setUserFilter] = useState('');
	const [selectedDevices, setSelectedDevices] = useState(availableDevices);
	const [isShowList, setShowList] = useState(false);

	const rows = eventArchiveState.eventsArchive[numberCard];

	const isFieldAndArrExist = rows.length && availableDevices[0] !== undefined;
	const title = isFieldAndArrExist ? 'All Devices' : 'No Devices';

	const isSelectAll = devices.length === availableDevices.length;

	const addNewDevices = (newDevices = []) => {
		eventArchiveState.setSelectedDevices(newDevices, numberCard);
		eventArchiveState.setModDevices(numberCard);
	};

	const handleVisibleList = () => {
		setShowList((prevShowList) => !prevShowList);
	};

	const handleSelectDevice = (device) => {
		const indexDevice = selectedDevices.indexOf(device);
		const isDeviceExist = indexDevice > -1;

		const newDevices = !isDeviceExist
			? [...selectedDevices, device]
			: [...selectedDevices.slice(0, indexDevice), ...selectedDevices.slice(indexDevice + 1)];

		setSelectedDevices(newDevices);
		addNewDevices(newDevices);
	};

	const handleSelectAll = () => {
		const newDevices = isSelectAll ? [] : availableDevices;

		setSelectedDevices(newDevices);
		addNewDevices(newDevices);
	};

	return (
		<div>
			<IconButton onClick={handleVisibleList}>
				<ArrowRightIcon />
			</IconButton>

			{isShowList && (
				<ClickAwayListener onClickAway={handleVisibleList}>
					<Box className={styles.listArchive}>
						<Box className={styles.search}>
							<SearchOutlined />

							<InputBase
								className={styles.searchText}
								sx={{ ml: 1, flex: 1, top: 4 }}
								type="text"
								placeholder="Search"
								value={userFilter}
								onChange={(e) => setUserFilter(e.target.value)}
							/>
						</Box>

						<div className={styles.listDevice} onClick={handleSelectAll}>
							{title}
							{isSelectAll && <CheckIcon style={{ color: '#FF992C', marginLeft: 10 }} />}
						</div>

						{!!availableDevices[0] &&
							availableDevices
								.filter((itemList) => itemList.toLowerCase().includes(userFilter.toLowerCase()))
								.sort((a, b) => a.localeCompare(b))
								.map((itemList) => {
									const sdIndex = devices.findIndex((item) => item === itemList);
									const isShowIcon = sdIndex > -1 || isSelectAll;

									return (
										<div
											key={uuidv4()}
											className={styles.listDevice}
											onClick={() => handleSelectDevice(itemList)}
										>
											<div className={styles.point} />
											{itemList}
											{isShowIcon && <CheckIcon style={{ color: '#FF992C', marginLeft: 10 }} />}
										</div>
									);
								})}
					</Box>
				</ClickAwayListener>
			)}
		</div>
	);
});

export default SelectCameras;
