import React, { useEffect, useState } from 'react';
import { Grid } from '@mui/material';
import styles from './CardsPlayers.module.scss';
import ItemPlayer from './ItemPlayer/ItemPlayer';
import { observer } from 'mobx-react';
import { v4 as uuidv4 } from 'uuid';

const CardsPlayers = observer(({ players, sizeItem, isFullScreen, setShowModal, handleChangeStriams }) => {
	const spacing = players.length === 4 ? 1 : 0.5;
	const mainSize = players.length === 1 ? 12 : sizeItem;
	const fullStyle = isFullScreen ? styles.pageFull : styles.page;

	const [editPlayers, setEditPlayers] = useState([]);

	useEffect(() => {
		setEditPlayers(players);
	}, [players]);

	const handleChangePlayers = (playerId, selectId) => {
		setEditPlayers(handleChangeStriams(playerId, selectId));
	};

	return (
		<div className={fullStyle}>
			<Grid container rowSpacing={spacing} columnSpacing={{ xs: 1, sm: spacing }}>

				{editPlayers.map((player) => (
					<Grid key={uuidv4()} item xs={mainSize}>
						<ItemPlayer
							player={player}
							setShowModal={setShowModal}
							handleChangePlayers={handleChangePlayers}
						/>
					</Grid>
				))}
			</Grid>
		</div>
	);
});

export default CardsPlayers;
