import React, { useState, useEffect } from 'react';
import { IconButton, Box, Slider, styled, ListItemButton, ClickAwayListener } from '@mui/material';
import { VolumeOff, VolumeUp, Replay10, VolumeDown, Forward10 } from '@mui/icons-material';
import PauseOutlinedIcon from '@mui/icons-material/PauseOutlined';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import FullscreenIcon from '@mui/icons-material/Fullscreen';
import SettingsRoundedIcon from '@mui/icons-material/SettingsRounded';
import styles from './Controls.module.scss';
import viewsState from '../../../../../store/viewsState';
import { observer } from 'mobx-react-lite';
import connectsState from '../../../../../store/connectsState';
import { logServerResponse } from '../../../../../helpers/helpers';
import { apiMainPost } from '../../../../../api/mainApi';

const TimeSlider = styled(Slider)({
	width: '95%',
	display: 'flex',
	borderRadius: '0px',
	height: '8px',
	color: '#F07C00',
	margin: '0 auto',
	padding: '5px 0',
	marginBottom: '10px',

	'& .MuiSlider-track': {
		border: 'none',
	},

	'& .MuiSlider-rail': {
		color: 'white',
		opacity: '0.5',
	},

	'& .MuiSlider-thumb': {
		color: 'white',
	},
});

const VolumeSlider = styled(Slider)({
	width: '12px',
	height: '60px',

	color: '#FF992C',

	'& .MuiSlider-rail': {
		color: '#CFCFCF',
		opacity: '0.7',
	},

	'& .MuiSlider-thumb': {
		width: '14px',
		height: '14px',
		color: 'white',
	},
});

const buttons = {
	icons: {
		fontSize: '25px',
		color: '#FFFFFF',

		'&:active': {
			color: '#FF992C',
		},

		'&:hover': {
			color: '#FF992C',
		},
	},
	time: {
		display: 'flex',
		alignItems: 'center',
		margin: '8px 0',
		fontSize: '18px',
		color: 'white',
	},
	iconsTime: {
		fontSize: '25px',
		color: '#FFFFFF',

		'&:hover': {
			color: '#FF992C',
		},
	},
};

const Controls = observer(
	({
		player,
		playing,
		played,
		onPlayPause,
		isMuted,
		duration,
		onVolumeChange,
		volume,
		onVolumeSeekDown,
		onSeek,
		onSeekMouseDown,
		onSeekMouseUp,
		elapsedTime,
		onRewind,
		onFastForward,
		setShowModal,
	}) => {
		const { showWindows, setSelectTranslation } = viewsState;

		const [isSettings, setSettings] = useState(false);
		const [isVolume, setVolume] = useState(false);
		const [recordTime, setRecordTime] = useState('15');
		const [isShowTimeRecord, setShowTimeRecord] = useState(false);
		const [isSmall, setSmall] = useState(false);
		const btnStyle = (isActive) => (isActive ? buttons.icons['&:active'] : buttons.icons);

		useEffect(() => {
			handleSize();
		}, [showWindows]);

		const handleChangeSettingsView = () => {
			setSettings((prev) => !prev);

			if (!isSettings) return;

			const { linkChangeTimeRecords } = connectsState;
			changeRecordTime(linkChangeTimeRecords);
		};

		const changeRecordTime = (url) => {
			const parameters = {
				id: player.id,
				new_record_time: recordTime,
			};

			apiMainPost(url, parameters).then(({ error }) => {
				const { isError } = error;

				const seccessMsg = 'Время записи изменено';
				const errorMsg = 'Не удалось изменить время записи';

				logServerResponse(isError, seccessMsg, errorMsg);
			});
		};

		const handleVolume = () => {
			setVolume((prev) => !prev);
		};

		const handleModal = () => {
			setShowModal(true);
			setSelectTranslation({ name: player.name, url: player.urls[0] });
		};

		const handleSize = () => {
			setSmall(showWindows === 16);
		};

		const handleRecordTime = (i) => {
			setRecordTime(i);
			setShowTimeRecord(false);
		};

		const controlIcons = () => {
			return (
				<>
					<IconButton sx={btnStyle(isVolume)} onClick={handleVolume}>
						{isVolume && (
							<div className={styles.sliderVolume}>
								<VolumeSlider
									orientation="vertical"
									value={volume * 100}
									onChange={onVolumeChange}
									onChangeCommitted={onVolumeSeekDown}
								/>
							</div>
						)}

						{isMuted ? <VolumeOff /> : volume > 0.5 ? <VolumeUp /> : <VolumeDown />}
					</IconButton>

					<IconButton sx={btnStyle(isSettings)} onClick={handleChangeSettingsView}>
						<SettingsRoundedIcon />
					</IconButton>
				</>
			);
		};

		return (
			<div className={styles.controls}>
				<div className={styles.controlPlay}>
					<div className={styles.timeContrl}>
						<IconButton sx={buttons.iconsTime} onClick={onRewind}>
							<Replay10 />
						</IconButton>
						<IconButton sx={buttons.iconsTime} onClick={onPlayPause}>
							{playing ? <PauseOutlinedIcon /> : <PlayArrowIcon />}
						</IconButton>
						<IconButton sx={buttons.iconsTime} onClick={onFastForward}>
							<Forward10 />
						</IconButton>
						<Box sx={buttons.time}>
							{elapsedTime} / {duration}
						</Box>
					</div>

					{isSettings && !isSmall && (
						<ClickAwayListener onClickAway={handleChangeSettingsView}>
							<div className={styles.settings}>
								<div className={styles.set}>
									Recording time
									<div onClick={() => setShowTimeRecord(true)}>
										<div className={styles.recordTime}>{recordTime}</div>
									</div>
									{isShowTimeRecord && (
										<div className={styles.selectTime}>
											<ListItemButton onClick={() => handleRecordTime('15')}>15</ListItemButton>
											<ListItemButton onClick={() => handleRecordTime('30')}>30</ListItemButton>
											<ListItemButton onClick={() => handleRecordTime('60')}>60</ListItemButton>
										</div>
									)}
								</div>
							</div>
						</ClickAwayListener>
					)}

					<div>
						{controlIcons()}

						<IconButton sx={buttons.icons} onClick={handleModal}>
							<FullscreenIcon />
						</IconButton>
					</div>
				</div>

				<TimeSlider
					min={0}
					max={100}
					value={played * 100}
					onChange={onSeek}
					onMouseDown={onSeekMouseDown}
					onChangeCommitted={onSeekMouseUp}
				/>
			</div>
		);
	}
);

export default Controls;
