import React, { useState, useEffect } from 'react';
import { Grid, styled } from '@mui/material';
import styles from './PlayersArchive.module.scss';
import ItemArchive from './ItemArchive/ItemArchive';
import viewsState from '../../../store/viewsState';
import connectsState from '../../../store/connectsState';
import { DataGrid } from '@mui/x-data-grid';
import { observer } from 'mobx-react-lite';
import ModalConfirm from './ModalConfirm/ModalConfirm';
import { prepareRecordsData } from '../../../api/prepareData/prepareRecordsData';
import { logServerResponse } from '../../../helpers/helpers';
import { apiMainGet, apiMainPost } from '../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const columnsArchive = [
	{ field: 'id', headerName: 'id', flex: 0.3 },
	{ field: 'img', flex: 0.5, headerName: 'Image', renderCell: (params) => <img src={params.value} /> },
	{ field: 'name', headerName: 'Name', flex: 1 },
	{ field: 'camName', headerName: 'Camera', flex: 0.5 },
	{ field: 'duration', headerName: 'Duration', flex: 0.5 },
	{ field: 'date', headerName: 'Date', flex: 1 },
];

const StyledDataGrid = styled(DataGrid)({
	height: '100%',
	width: '100%',
	fontFamily: 'Inter',
	fontSize: '14px',
	border: 'none',
	background: 'var(--bgTheme)',
	color: 'var(--colorText)',

	'&.MuiDataGrid-root': {
		borderRadius: '0px',
		'& .MuiDataGrid-columnSeparator--sideRight': {
			display: 'none',
		},
		'& .MuiDataGrid-cell': {
			border: 'none',
			display: 'flex',
			justifyContent: 'center',
		},
		'& .MuiDataGrid-columnHeaderTitleContainer': {
			height: '100%',
			justifyContent: 'center',
		},
		'& .MuiDataGrid-columnHeaderDraggableContainer': {
			display: 'block',
		},
	},
	'& .MuiDataGrid-columnHeaders': {
		backgroundColor: 'var(--bgTheme)',
	},
	'& .MuiDataGrid-footerContainer': {
		display: 'none',
	},
	'& .MuiCheckbox-root.Mui-checked': {
		color: '#FF992C',
	},
	'& .MuiIconButton-sizeSmall': {
		color: 'var(--colorText)',
	},
});

const PlayersArchive = observer(({ isTable, setShowModal }) => {
	const {
		cameras,
		recordings,
		removedItemsArchive,
		sortNameRecords,
		reversSortRecords,
		clearRemovedItemsArchive,
		setConfirmDeleteRecords,
		setSelectRecord,
		setRecordings,
		setRemovedItemsTableArchive,
	} = viewsState;
	const { linkGetRecords } = connectsState;

	const [filterData, setFilterData] = useState([]);
	const [isFetchRecords, setFetchRecords] = useState(false);
	const [currentPage, setCurrentPage] = useState(1);
	const [isFinalyPage, setFinalyPage] = useState(false);

	useEffect(() => {
		sortedRecordings(filterData, sortNameRecords, reversSortRecords);
	}, [sortNameRecords, reversSortRecords]);

	useEffect(() => {
		const selCamNames = cameras
			.filter(({ isSelected }) => isSelected)
			.map(({ name }) => name);

		filterRecordings(recordings, selCamNames);
	}, [cameras, recordings, removedItemsArchive]);

	useEffect(() => {
		if (!linkGetRecords) return;

		const controller = new AbortController();

		getRecords(`${linkGetRecords}${currentPage}`, controller);

		return () => {
			controller.abort();
			setRecordings([]);
			clearRemovedItemsArchive();
		};
	}, [linkGetRecords, isFetchRecords]);

	const filterRecordings = (arr = [], cameras = []) => {
		const newRecordings = JSON.parse(JSON.stringify(arr)).filter((record) => cameras.includes(record.camName));

		setFilterData(newRecordings);
	};

	const sortedRecordings = (arr = [], type = 'default', rev = 1) => {
		const newArray = JSON.parse(JSON.stringify(arr));

		const sortTypes = {
			date: (arr) => {
				return arr.sort((a, b) => (new Date(a[type]) < new Date(b[type]) ? 1 * rev : -1 * rev));
			},
			duration: (arr) => {
				return arr.sort((a, b) => (a[type] < b[type] ? 1 * rev : -1 * rev));
			},
			default: () => {}
		};

		setFilterData(sortTypes[type](newArray));
	};

	const getRecords = (url = '', controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;
			
			const seccessMsg = 'Получение записей - успешно';
			const errorMsg = 'Не удалось получить записи';

			logServerResponse(isError, seccessMsg, errorMsg);
			
			if (isError) return;

			const records = prepareRecordsData(data);
			
			const recordsPerPage = 30;
			const isLastPage  = records.length < recordsPerPage;
			
			setFinalyPage(isLastPage);
			setRecordings([...recordings, ...records]);
			setFetchRecords(false);
			setCurrentPage((prev) => prev + 1);
		});
	};

	const getRecordsRepeat = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;
			
			const seccessMsg = 'Получение записей - успешно';
			const errorMsg = 'Не удалось получить записи';

			logServerResponse(isError, seccessMsg, errorMsg);
			
			if (isError) return;

			const records = prepareRecordsData(data);

			setRecordings(records);
			setCurrentPage(2);
		});
	};

	const handleTableItem = ({ row: { camName, path } }) => {
		setShowModal(true);
		setSelectRecord({ name: camName, url: path });
	};

	const scrollHandler = (e) => {
		const { scrollHeight, scrollTop } = e.target;
		const { innerHeight } = window;

		const height = scrollHeight - (scrollTop + innerHeight);
		const isCorrectHeight = !isFinalyPage && height < -100;

		if (isCorrectHeight) setFetchRecords(true);
	};

	const handleRemoveRecords = () => {
		deleteRecords();
		clearRemovedItemsArchive();
		setConfirmDeleteRecords(false);
		setRecordings([]);

		setTimeout(() => {
			const url = `${linkGetRecords}1`;
			getRecordsRepeat(url);
		}, 500);
	};

	const deleteRecords = () => {
		const url = connectsState.linkDelRecords;
		const parameters = removedItemsArchive;

		apiMainPost(url, parameters).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Запись успешно удалена';
			const errorMsg = 'Не удалось удалить запись';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const handleModal = () => {
		setConfirmDeleteRecords(false);
	};

	const handleSelectRows = (recordsId = []) => {
		if (recordsId.length) {
			setRemovedItemsTableArchive(recordsId);
		} else {
			clearRemovedItemsArchive();
		}
	};

	return (
		<div className={styles.page} onScroll={scrollHandler}>
			{isTable ? (
				<StyledDataGrid
					checkboxSelection
					rows={filterData}
					columns={columnsArchive}
					selectionModel={removedItemsArchive}
					rowHeight={100}
					onRowClick={handleTableItem}
					onSelectionModelChange={handleSelectRows}
				/>
			) : (
				<Grid container rowSpacing={1} columnSpacing={{ xs: 1, sm: 0.5 }}>
					{filterData.map((record) => (
						<Grid key={uuidv4()} item xs={2} sx={{ height: '26%' }}>
							<ItemArchive record={record} setShowModal={setShowModal} />
						</Grid>
					))}
				</Grid>
			)}

			<ModalConfirm handleModal={handleModal} deleteRecords={handleRemoveRecords} />
		</div>
	);
});

export default PlayersArchive;
