import React from 'react';
import { Box, IconButton } from '@mui/material';
import styles from './AdditionalControlPanel.module.scss';
import { useState } from 'react';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

export default observer(({ buttonPanel = [], menuIndex = 0 }) => {
	const { additionalControlPanel, iconButton, iconButtonBox } = styles;

	const [selected, setSelected] = useState(menuIndex);

	const handleSelectedItem = (index = null, name = '') => {
		setSelected(index);
		buttonPanel[index].action(name);
	};

	return (
		<Box className={additionalControlPanel}>
			{buttonPanel.map(({ name, icon, isDisabled }, index) => {
				const isSelected = selected === index;
				const selectedColor = `${isSelected ? '31, 100%, 60%' : '0, 0%, 40%'}`;
				const color = `hsl(${isDisabled ? '0, 0%, 70%' : selectedColor})`;

				return (
					<Box key={uuidv4()} className={iconButtonBox}>
						<IconButton
							className={iconButton}
							onClick={() => handleSelectedItem(index, name)}
							disabled={isDisabled}
						>
							{icon(color)}
						</IconButton>
					</Box>
				);
			})}
		</Box>
	);
});
