import zonesState from '../../store/zonesState';

export const prepareSectorsData = (sectors = []) => {
	return sectors.map((sector) => {
		return {
			...sector,
			isShow: !!zonesState.showSectorsStore?.[sector.id],
			zones: sector.zones.map((zone) => ({ ...zone, isShow: !!zonesState.showZonesStore?.[zone.id] })),
		};
	});
};
