import React, { useState, useEffect } from 'react';
import styles from './AccountCards.module.scss';
import { Box, ClickAwayListener } from '@mui/material';
import PersonCard from './PersonCard/PersonCard';
import CustomTable from '../Card/CustomTable/CustomTable';
import ThemeBtn from '../../../../Theme/ThemeBtn';
import viewsState from '../../../../store/viewsState';
import CardOfInfoData from '../Card/CardOfInfoData/CardOfInfoData';
import authState from '../../../../store/authState';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const EditProfile = ({ showPdf, setEditProfile }) => {
	const handleLogOut = () => {
		authState.setAutorize(false);
	};

	return (
		<ClickAwayListener onClickAway={() => setEditProfile(false)}>
			<div className={styles.themeCard}>
				<div className={styles.guideBtn}>
					<ThemeBtn />
				</div>
				<div onClick={showPdf} className={styles.guideBtn}>
					User guide
				</div>
				<div className={styles.guideBtn} onClick={handleLogOut}>
					Sign out
				</div>
			</div>
		</ClickAwayListener>
	);
};

const AccoundCards = observer(({ showPdf = () => {}, openModalAdd = () => {}, anotherAdd = () => {} }) => {
	let id = 0;
	const { cameras } = viewsState;

	const [isEditeProfile, setEditeProfile] = useState(false);
	const [dataCameras, setDataCameras] = useState([]);

	const actionsCamera = {
		add: () => openModalAdd(0),
		update: (e, i) => openModalAdd(e, i),
	};
	const actionsAnother = {
		add: () => anotherAdd(),
		update: () => anotherAdd(),
	};
	const customTableNumber = [
		{ id: id++, title: 'Devices', data: dataCameras, actions: actionsCamera },
		{ id: id++, title: 'Sensors', data: [], actions: actionsAnother },
		{ id: id++, title: 'AIS', data: [], actions: actionsAnother },
		{ id: id++, title: 'Radar', data: [], actions: actionsAnother },
		{ id: id++, title: 'Sound', data: [], actions: actionsAnother },
		{ id: id++, title: 'Station for analytics', data: [], actions: actionsAnother },
	];

	useEffect(() => {
		setDataCameras(cameras.map(({ id, ip, name, isWorking }) => ({ id, name, ip, isWorking })));
	}, [cameras]);

	const handleChangeVisible = () => {
		setEditeProfile((prevEditProfile) => !prevEditProfile);
	};

	return (
		<Box className={styles.page}>
			<div className={styles.gridContainer}>
				<div className={styles.cardPersonBox}>
					<div className={styles.head}>
						<p className={styles.title}>Personal information</p>
						<div className={styles.btnTitle} style={{ color: '#FF992C' }} onClick={() => handleChangeVisible()}>
							SETTINGS
						</div>
						{isEditeProfile && <EditProfile showPdf={showPdf} setEditProfile={setEditeProfile} />}
					</div>
					<div className={styles.wrapper}>
						<PersonCard />
					</div>
				</div>
				<div className={styles.cardsBox}>
					<CardOfInfoData title="Duty Log" description="DUTY COMMAND NOT SPECIFIED BY OPERATOR" />
					<CardOfInfoData title="Directory" description="NOT INDICATED" />
				</div>
			</div>
			<div className={styles.gridBox}>
				{customTableNumber.map((customTableInf) => {
					const { title, data, actions } = customTableInf;

					return <CustomTable key={uuidv4()} title={title} dataCameras={data} actions={actions} />;
				})}
			</div>
		</Box>
	);
});

export default AccoundCards;
