import React, { useRef, useEffect, useState } from 'react';
import { CircularProgress, styled } from '@mui/material';
import ReactPlayer from 'react-player';
import Controls from './Controls/Controls';
import SelectCameras from './SelectCameras/SelectCameras';
import styles from './PlayerViews.module.scss';
import { observer } from 'mobx-react-lite';

const Format = (seconds) => {
	if (isNaN(seconds)) return '00:00:00';

	const date = new Date(seconds * 1000);
	const hh = date.getUTCHours().toString().padStart(2, '0');
	const mm = date.getUTCMinutes().toString().padStart(2, '0');
	const ss = date.getUTCSeconds().toString().padStart(2, '0');

	const min = hh ? mm.toString().padStart(2, '0') : mm;
	return `${hh}:${min}:${ss}`;
};

const ModLoaded = styled(CircularProgress)({
	position: 'absolute',
	top: '0',
	right: '0',
	bottom: '0',
	left: '0',
	margin: 'auto',
	color: '#FF992C',
});

const PlayerViews = observer(({ idPlayer, name, url, quality, setShowModal, setHandleQuality }) => {
	const playerRef = useRef(null);
	const [isPlaying, setPlaying] = useState(true);
	const [isMuted, setMuted] = useState(false);
	const [volume, setVolume] = useState(0);
	const [isSeek, setSeek] = useState(false);
	const [played, setPlayed] = useState(0);
	const [isLive, setIsLive] = useState(true);
	const [isShowControls, setShowControls] = useState(true);
	const [currentCamera, setCurrentCamera] = useState(name);
	const [elapsedTime, setElapsedTime] = useState('00:00');
	const [durationTime, setDurationTime] = useState('00:00');
	const [isBuffering, setIsBuffering] = useState(true);

	useEffect(() => {
		setElapsedTime(Format(playerRef.current?.getCurrentTime() ?? '00:00'));
	}, [durationTime]);

	const handleReady = () => {
		setIsBuffering(false);
	};

	const handleBuffer = () => {
		setIsBuffering(true);
	};

	const actionCamera = (name) => {
		setCurrentCamera(name);
	};

	const openControls = () => {
		setShowControls(true);
	};

	const closeControls = () => {
		setShowControls(false);
	};

	const handlePlayPause = () => {
		setPlaying(!isPlaying);
	};

	const handleVolumeChange = (e, value) => {
		setVolume(value / 100);
		setMuted(!value);
	};

	const handleMute = () => {
		setMuted(!isMuted);
	};

	const handleVolumeSeekDown = (e, value) => {
		setSeek(false);
		setVolume(value / 100);
	};

	const handleProgress = (change) => {
		if (isSeek) return;

		setDurationTime(Format(change.loadedSeconds));
		setPlayed(change.played);
	};

	const handleSeekChange = (e, value) => {
		if (value > 95) return;

		setIsLive(false);
		setPlayed(value / 100);
	};

	const handleSeekMouseUp = (e, value) => {
		setSeek(false);
		playerRef.current.seekTo(value / 100, 'fraction');
	};

	const handleSeekMouseDown = () => {
		setSeek(true);
	};

	const handleRewind = () => {
		playerRef.current.seekTo(playerRef.current.getCurrentTime() - 10);
	};

	const handleFastForward = () => {
		playerRef.current.seekTo(playerRef.current.getCurrentTime() + 10);
	};

	const handleLifeTime = () => {
		setIsLive(true);
		playerRef.current.seekTo(0.95, 'fraction');
	};

	return (
		<div className={styles.block} onMouseMove={openControls} onMouseLeave={closeControls}>
			<SelectCameras
				item={currentCamera}
				isLive={isLive}
				handleLifeTime={handleLifeTime}
				action={(name) => actionCamera(name)}
			/>

			{isBuffering && <ModLoaded size={60} />}

			<ReactPlayer
				width={'100%'}
				height={'100%'}
				className={styles.video}
				ref={playerRef}
				playing={isPlaying}
				muted={isMuted}
				url={url}
				volume={volume}
				seeking={isSeek.toString()}
				onProgress={handleProgress}
				onBuffer={handleBuffer}
				onReady={handleReady}
			/>

			{isShowControls && (
				<Controls
					idCamera={idPlayer}
					playing={isPlaying}
					played={played}
					isMuted={isMuted}
					volume={volume}
					duration={durationTime}
					elapsedTime={elapsedTime}
					quality={quality}
					onRewind={handleRewind}
					onFastForward={handleFastForward}
					onPlayPause={handlePlayPause}
					onVolumeChange={handleVolumeChange}
					onMute={handleMute}
					onVolumeSeekDown={handleVolumeSeekDown}
					onSeek={handleSeekChange}
					onSeekMouseDown={handleSeekMouseDown}
					onSeekMouseUp={handleSeekMouseUp}
					setShowModal={setShowModal}
					handleQuality={setHandleQuality}
				/>
			)}
		</div>
	);
});

export default PlayerViews;
