import { defaultReconnectTime, getDefaultCamera } from '../../helpers/defaultData';
import viewsState from '../../store/viewsState';

export const prepareCamerasFullData = (cameras = []) => {
	const defCamera = getDefaultCamera();

	return cameras.map((camera) => {
		const {
			id,
			is_alive,
			name,
			cameraKey,
			latitude,
			longitude,
			stream_address_360,
			stream_address_480,
			stream_address_720,
			stream_address_ai,
		} = camera;

		const strims = {
			360: stream_address_360,
			480: stream_address_480,
			720: stream_address_720,
			ai: stream_address_ai,
		};

		const loc = [latitude ?? 0, longitude ?? 0];

		return {
			...defCamera,
			id,
			name,
			cameraKey,
			loc,
			isWorking: is_alive,
			strims,
		};
	});
};

export const prepareCamerasSingleData = (camera = {}) => {
	const defCamera = getDefaultCamera();

	const {
		id,
		cameraKey,
		is_alive,
		is_patrol_active,
		name,
		presets,
		stream_address_360,
		stream_address_480,
		stream_address_720,
		stream_address_ai,
		time_switch_presets,
	} = camera;

	const reconnectTime = defaultReconnectTime.map((item) => ({ ...item, isActive: false }));

	if (time_switch_presets > 0 && time_switch_presets <= reconnectTime[0].num) reconnectTime[0].isActive = true;
	if (time_switch_presets > reconnectTime[0].num && time_switch_presets <= reconnectTime[1].num)
		reconnectTime[1].isActive = true;
	if (time_switch_presets > reconnectTime[1].num && time_switch_presets <= reconnectTime[2].num)
		reconnectTime[2].isActive = true;

	return ({
		...defCamera,
		id,
		cameraKey,
		isWorking: is_alive,
		isPatrol: is_patrol_active,
		name,
		controlMenu: {
			...defCamera.controlMenu,
			presets: createCorrectPresets(presets),
			reconnectTime,
		},
		strims: {
			360: stream_address_360,
			480: stream_address_480,
			720: stream_address_720,
			ai: stream_address_ai,
		},
		reconnectTime: time_switch_presets,
	});
};

const createPolygons = (polygons = []) => {
	return polygons.map((polygon) => {
		const { id, cvPoints, name, startSize, type } = polygon;

		const { r, g, b, a } = type.rgba;
		const attributes = {
			type: type.id,
			fillColor: `rgba(${r}, ${g}, ${b}, ${a})`,
			rgba: type.rgba,
		};

		return { id, name, attributes, startSize, points: cvPoints };
	});
};

const createCorrectPresets = (presets = []) => {
	const activeIndex = presets.findIndex((item) => item.is_active);
	const isActiveExist = activeIndex !== -1;

	viewsState.setSelectPresetId(isActiveExist ? presets[activeIndex].id : null);
	viewsState.setIsPresetSelected(isActiveExist);

	return presets.map(({ id, name, polygons, zone_name, is_active }) => {
		return { id, name, polygons: createPolygons(polygons), zoneName: zone_name, isActive: is_active };
	});
};