import React from 'react';
import styles from './ArrowButton.module.scss';
import { Box, IconButton } from '@mui/material';
import { CustomIcon } from '../../../../helpers/helpers';

const defActions = {
	down: () => {},
	up: () => {},
};

export default ({ icon = '', alt = '', size = null, direction = '', isDisabled = false, actions = defActions }) => {
	const { up, down } = actions;
	const arrowDirection = `${styles.icon} ${styles[direction]}`;

	const handleClick = () => {
		down();
	};

	const sendStop = () => {
		up();
	};

	return (
		<Box className={styles.arrowButton}>
			<IconButton className={styles.iconButton} onMouseDown={handleClick} onMouseUp={sendStop} disabled={isDisabled}>
				<Box className={arrowDirection}>
					<CustomIcon icon={icon} name={alt} size={size} />
				</Box>
			</IconButton>
		</Box>
	);
};
