import React from 'react';
import styles from './CreateNewObjBtn.module.scss';

const CreateNewObjBtn = ({reset, handleAddObject}) => {
	return (
		<div className={styles.confirm}>
			<button
				className={styles.resetBtn}
				onClick={reset}
			>
				Reset
			</button>
			<button
				className={styles.modBtn}
				onClick={handleAddObject}
			>
				Confirm
			</button>
		</div>
	);
};

export default CreateNewObjBtn;
