import React from 'react';
import styles from './PopoverItem.module.scss';
import { Box, ListItem, ListItemIcon, ListItemText } from '@mui/material';
import { NavLink } from 'react-router-dom';
import { selectColorConditional } from '../../../../helpers/helpers';
import notifications from '../../../../store/notificationsState';

export const PopoverItem = ({ event , handleClosePopover = () => {}}) => {
	const { id = '', type_event: typeEvent = '', date = '' } = event;

	const conditional = selectColorConditional(typeEvent);

	const handleSelectItem = (eventId = null) => {
		notifications.setView(eventId, false);
		handleClosePopover();
	};

	return (
		<div>
			<ListItem className={styles.item}>
				<ListItemIcon className={styles.listItemIcon}>
					<Box
						className={styles.conditionalStyle}
						bgcolor={conditional}
					/>
				</ListItemIcon>
				<NavLink
					className={styles.navLink}
					to={'/Archive'}
				>
					<ListItemText
						className={styles.descriptionText}
						onClick={() => handleSelectItem(id)}>
						{ typeEvent }
					</ListItemText>
					<ListItemText
						className={styles.dateText}
						onClick={() => handleSelectItem(id)}
					>
						{date}
					</ListItemText>
				</NavLink>
			</ListItem>
		</div>
	);
};
