import {makeAutoObservable} from 'mobx';

class cardState{
	isOpenModal = false;
	isOpenMap = false;

	constructor() {
		makeAutoObservable(this);
	}

	changeOpenModal = () => {
		this.isOpenModal = !this.isOpenModal;
	};
	changeOpenMap = (newCondition) => {
		newCondition
			? this.isOpenMap = !this.isOpenMap
			: this.isOpenMap = newCondition;
	};
}

export default new cardState();