const checkAllFilters = (item = {}, filters = [], userFilters = []) => {
	const isShow = filters.reduce((prevIsShow, curFilter) => {
		const { filter, isSelected, nested } = curFilter;
		
		let isTestShow = true;

		for (let i = 0; i < userFilters.length; ++i) {
			const isCorrectFilter = item[userFilters[i]] === filter;

			if (isCorrectFilter) isTestShow &= isSelected && checkAllFilters(item, nested, userFilters);
		}

		return prevIsShow && isTestShow;
	}, true);

	return isShow;
};

export const getFilteredData = (data = [], filters = [], userFilters = []) => data.filter((item) => checkAllFilters(item, filters, userFilters));

export const creareNestedFilters = (data = [], filterTypes = [], correctNames = {}) => {
	let id = 0;

	const getUniqueFilterNames = (data = [], filter = '') => {
		const uniqueFilters = {};

		data.forEach((item) => (uniqueFilters[item[filter]] = correctNames[item[filter]] || item[filter]));

		return uniqueFilters;
	};

	const getFilters = (data = [], nextFilter = '') => {
		const uniqueFilters = getUniqueFilterNames(data, nextFilter);
		const filterNames = Object.keys(uniqueFilters);
		const filters = filterNames.map((key) => {
			const index = filterTypes.findIndex((filter) => filter === nextFilter);
			const nestedNextFilter = filterTypes[index + 1] || '';

			const filteredData = data.filter((item) => item[nextFilter] === key);
			const nested = nestedNextFilter ? getFilters(filteredData, nestedNextFilter) : [];

			return {
				id: id++,
				name: correctNames[key] || key,
				filter: key,
				isSelected: true,
				nested,
			};
		});

		const sortedFilters = filters.sort((a, b) => (a.name > b.name ? 1 : -1));

		return sortedFilters;
	};

	const getCopy = (data) => JSON.parse(JSON.stringify(data));

	return filterTypes.length ? getCopy(getFilters(data, filterTypes[0])) : data;
};
