import React, {useEffect, useState} from 'react';
import styles from './CardBig.module.scss';
import ControlIcons from '../ControlIcons/ControlIcons';
import FullTable from './FullTable/FullTable';
import Chart from './../Charts/Chart';
import Map from './Map/Map';
import TableCard from '../TableCard/TableCard';
import cardState from '../../../../store/cardState';
import {observer} from 'mobx-react-lite';
import eventArchiveState from '../../../../store/eventArchiveState';
import { columns, nameCards } from '../../data';

const CardBig = observer(({ numberCard, data }) => {
	const { rows, userFilter, availableDevices, modDevices } = data;
	const [points, setPoints] = useState({});
	const [ship, setShip] = useState({});

	const [isTable, setTable] = useState(true);
	const [currentPage, setCurrentPage] = useState(1);

	const rowsPerPage = 5;
	const lastRowIndex = currentPage * rowsPerPage;
	const currentRows = rows.slice(0, lastRowIndex);
	const newRows = currentRows.filter(row => userFilter.includes(row.deviceName));

	const position = [64.5284, 40.54095];

	const tableWithMap = {
		columns: columns[numberCard],
		rows: newRows,
		numberCard,
	};

	useEffect(() => {
		rows.forEach((row) => {
			const { deviceName } = row;
			const deviceExist = availableDevices.indexOf(deviceName) > -1;

			if (!deviceExist) availableDevices.push(deviceName);
		});

		eventArchiveState.setAvailableDevices(availableDevices, numberCard);
		if (!modDevices) eventArchiveState.setSelectedDevices(availableDevices, numberCard);
	}, [rows]);

	const handleChangeTable = () => {
		setTable(prevIsTable => !prevIsTable );
	};

	const changePage = (num) => {
		setCurrentPage(prevPage => prevPage + num);
	};

	const handleScroll = (event) => {
		const { scrollHeight, scrollTop, clientHeight } = event.currentTarget;
		const scrollBottom = scrollHeight - scrollTop - clientHeight;

		if (scrollBottom < 50) changePage(1);
	};

	const handleSelect = ({ points, ship }) => {
		setPoints(points);
		setShip(ship);
	};

	const mapWithTable = () => {
		return (
			<div className={styles.mapWithTable}>
				<div className={styles.wrapper} onScroll={handleScroll}>
					<TableCard
						table={tableWithMap}
						action={(selectedEvent) => handleSelect(selectedEvent)}
					/>
				</div>

				<div className={styles.map}>
					<Map points={points} ship={ship} position={position} />
				</div>
			</div>
		);
	};

	return (
		<div className={styles.cardField}>
			<div className={styles.modalHead}>
				<p className={styles.nameModal}>{nameCards[numberCard].name}</p>
				<div className={styles.icons}>
					<ControlIcons
						changeTable={handleChangeTable}
						isTable={isTable}
						numberCard={numberCard}
					/>
				</div>
			</div>

			{rows.length
				? cardState.isOpenMap
					? mapWithTable()
					: isTable
						? <FullTable numberCard={numberCard} userFilter={userFilter} rows={rows}/>
						: <div className={styles.chart}>
							<Chart typeChart={nameCards[numberCard].typeChart}/>
						</div>
				: <p className={styles.noData}>No events</p>
			}
		</div>
	);
});

export default CardBig;