import React from 'react';
import styles from './Map.module.scss';
import {MapContainer, TileLayer, ScaleControl, Polygon} from 'react-leaflet';
import ShipOnMap from './ShipOnMap/ShipOnMap';
import 'leaflet/dist/leaflet.css';

const Map = ({ points = {}, ship = {}, position }) => {
	const { zonePoints, sectorPoints } = points;

	return (
		<MapContainer
			center={position}
			zoom={13}
			scrollWheelZoom={true}
			className={styles.container}
			zoomControl={false}
			minZoom={11}
			maxZoom={16}
		>
			<TileLayer url='http://82.148.28.212:8080/static/map/{z}/{x}/{y}.png' />
			<ScaleControl metric={true} imperial={false} position='bottomright' />

			{!!zonePoints &&
				<div>
					<Polygon di positions={zonePoints} color='green' />
					<Polygon di positions={sectorPoints} color='white' />
				</div>
			}
			<ShipOnMap ship={ship} />
		</MapContainer>
	);
};

export default Map;
