import React from 'react';
import { Redirect, Route, Switch } from 'react-router-dom';
import MapPage from '../pages/MapPage/MapPage';
import RadarMap from '../components/Radar/RadarMap/RadarMap';
import ArchivePage from '../pages/ArchivePage/ArchivePage';
import ViewPage from '../pages/ViewPage/ViewPage';
import Account from '../components/Account/Account';
import MonitorPage from '../pages/MonitorPage/MonitorPage';

export default () => {
	const { pathname } = window.location;
	
	return (
		<Switch>
			<Route path='/port' component={MapPage} />
			<Route path='/radar' component={RadarMap} />
			<Route path='/archive' component={ArchivePage} />
			<Route path='/views' component={ViewPage} />
			<Route path='/account' component={Account} />
			<Route path='/monitor' component={MonitorPage} />

			<Route exact path={pathname}>
				<Redirect to={'/port'} />
			</Route>
		</Switch>
	);
};