import React, {useEffect, useState} from 'react';
import styles from './FullTable.module.scss';
import VideoButtons from '../../VideoButtons/VideoButtons';
import TableCard from '../../TableCard/TableCard';
import {columnsFull} from '../../../data';
import {observer} from 'mobx-react-lite';

const FullTable = observer(({ numberCard, userFilter, rows }) => {
	const [currentPage, setCurrentPage] = useState(1);
	const [rowsPerPage] = useState(3);

	const newRows = rows.filter(row => userFilter.includes(row.deviceName));
	const lastRowIndex = currentPage * rowsPerPage;
	const firstRowIndex = lastRowIndex - rowsPerPage;
	const lastPage = Math.ceil(newRows.length / rowsPerPage);
	const currentRows = newRows.slice(firstRowIndex, lastRowIndex);

	useEffect(() => {
		setCurrentPage(1);
	}, [userFilter]);

	const changePage = (num) => {
		setCurrentPage(prevPage => prevPage + num);
	};

	const table = {
		columns: columnsFull[numberCard],
		rows: currentRows,
		numberCard,
	};

	return (
		<div className={styles.fullTable}>
			<TableCard table={table}/>
			<div className={styles.footer}>
				<VideoButtons
					currentPage={currentPage}
					lastPage={lastPage}
					handleChangePage={(n = null) => changePage(n)}
				/>
			</div>
		</div>
	);
});

export default FullTable;