import React from 'react';
import { useThemeTwo } from './useTheme';
import './btntheme.scss';
import useDarkMode from 'use-dark-mode';
import { useTheme } from './darkTheme';

const ThemeBtn = () => {
	const darkMode = useDarkMode(true);
	const theme = useThemeTwo();
	const setMode = useTheme();

	return (
		<div className={`btn-theme ${theme}`} onClick={darkMode.toggle}>
			{theme === 'dark-mode' ? (
				<div style={{ fontSize: '18px', color: 'white' }}>DARK THEME</div>
			) : (
				<div style={{ fontSize: '18px', color: 'black' }}>LIGHT THEME</div>
			)}
			{setMode === 'dark' ? (
				<div style={{ display: 'none' }}>light</div>
			) : (
				<div style={{ display: 'none' }}>dark</div>
			)}
		</div>
	);
};
export default ThemeBtn;
