import React from 'react';
import styles from './ModalHelp.module.scss';
import { Modal } from '@mui/material';
import close from '../../../../../assets/portsMenu/close.png';

const ModalHelp = ({ isModal, handleModal }) => {
	return (
		<Modal open={isModal} onClose={handleModal} closeAfterTransition>
			<div className={styles.container}>
				<div className={styles.header}>
					Help
					<img className={styles.closeIcon} src={close} onClick={handleModal} />
				</div>

				<div className={styles.content}>
					<p>When you click on the map, points appear that will form a zone or sector (depending on the selected item).</p> 
					<p>To delete the last created point, click on the (back) button.</p> 
					<p>To completely delete the rendered object, click the RESET button.</p> 
					<p>After the object is created, click CONFIRM and select the appropriate items. Next, click on the CREATE button, after which it is displayed on the map.</p> 
					<p>To display sectors and zones during object creation, use the ENABLE DISPLAY menu.</p>
				</div>
			</div>
		</Modal>
	);
};

export default ModalHelp;
