import React, { useState, useEffect } from 'react';
import styles from './SelectCameras.module.scss';
import workImg from '../../../../../assets/images/workImg.png';
import { Box, ClickAwayListener, InputBase, List, ListItemButton, ListItemText } from '@mui/material';
import { SearchOutlined } from '@material-ui/icons';
import CheckIcon from '@material-ui/icons/Check';
import viewsState from '../../../../../store/viewsState';
import { observer } from 'mobx-react-lite';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import { v4 as uuidv4 } from 'uuid';

const SelectCameras = observer(({ item, isLive, handleLifeTime, action = () => {} }) => {
	const { streams, setSelectTranslation } = viewsState;

	const [selectCamera, setSelectCamera] = useState(item);
	const [isOpenList, setOpenList] = useState(false);
	const [selectItems, setSelectItems] = useState([]);
	const [searchTerm, setSearchTerm] = useState('');
	const [searchResults, setSearchResults] = useState([]);

	const color = isOpenList ? '#ff992c' : 'var(--colorText)';

	useEffect(() => {
		setSelectItems(streams.map(({ name, urls }) => ({ name, url: urls[0] })));
		
		const results = selectItems.filter(({ name }) => name.toLowerCase().includes(searchTerm));
		setSearchResults(results);
	}, [searchTerm, streams, isOpenList]);

	const handleClose = () => setOpenList(false);

	const handleSelect = (item) => {
		setSelectCamera(item.name);
		setOpenList(false);
		action(item.name);
		setSelectTranslation(item);
	};

	const handleChange = (event) => {
		setSearchTerm(event.target.value);
	};

	const moreIcon = (Icon) => {
		return <Icon sx={{ height: '20px', width: '25px' }} />;
	};

	return (
		<div className={styles.choose}>
			<div style={{ color }} className={styles.chooseCamera} onClick={() => setOpenList((prev) => !prev)}>
				<div className={styles.selectHeader}>
					<img className={styles.workImg} src={workImg} />
					<div className={styles.nameCamera}>{selectCamera}</div>
					{moreIcon(isOpenList ? ExpandMoreIcon : ExpandLessIcon)}
				</div>
			</div>

			{isOpenList && (
				<ClickAwayListener onClickAway={handleClose}>
					<Box className={styles.ListCameras}>
						<List>
							<ListItemButton className={styles.SearchCameras}>
								<SearchOutlined />

								<InputBase
									className={styles.selectHeader}
									type='text'
									placeholder='Search'
									value={searchTerm}
									onChange={handleChange}
								/>
							</ListItemButton>

							{searchResults.map((item, index) => {
								const camIndex = selectItems.findIndex(({ name }) => name === selectCamera);
								const isSelected = camIndex === index;

								return (
									<div
										key={uuidv4()}
										className={styles.List}
										value={item.name}
										onClick={() => handleSelect(item)}
									>
										<img className={styles.workImg} src={workImg} />
										<ListItemText value={item.name} primary={item.name} />
										<div className={styles.edit}>delete</div>

										{isSelected && <CheckIcon style={{ color: '#FF992C' }} />}
									</div>
								);
							})}
						</List>
					</Box>
				</ClickAwayListener>
			)}

			<div className={`${styles.lifeClick} ${isLive && styles.live}`} onClick={handleLifeTime}>
				Real Time
			</div>
		</div>
	);
});

export default SelectCameras;
