import React, { useEffect, useState } from 'react';
import styles from './CreateZone.module.scss';
import zonesState from '../../../../../store/zonesState';
import portState from '../../../../../store/portState';
import { observer } from 'mobx-react-lite';
import CreateNewObjBtn from '../CreateNewObjBtn/CreateNewObjBtn.js';
import VisibleBox from '../VisibleBox/VisibleBox';
import EditPointsPanel from '../EditPointsPanel/EditPointsPanel';
import ReturnIcon from '../../../../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/ReturnIcon';

const CreateZone = observer(({ handleShowInstruction, setModalCreate, handleClose }) => {
	const { pointsZone, editZone, setEditZone, setPointsZone } = zonesState;
	const { isConfirmCreateZone } = portState;

	const [isEditCoords, setEditCoords] = useState(false);
	const [coords, setCoords] = useState({ lat: 0, lng: 0 });

	useEffect(() => {
		if (!pointsZone.length) return;

		const endPointZone = pointsZone.length - 1;
		const [lat, lng] = pointsZone[endPointZone];

		setCoords({ lat: lat.toFixed(5), lng: lng.toFixed(5) });
	}, [pointsZone]);

	const handleBackPoint = () => {
		setPointsZone(pointsZone.slice(0, -1));
	};

	const reset = () => {
		setPointsZone([]);
	};

	const editZoneMap = () => {
		setEditZone({ ...editZone, aisPoints: pointsZone });
		handleClose();
	};

	const handleAddZone = () => {
		Number.isInteger(editZone.id) ? editZoneMap() : setModalCreate(true);
	};

	const coordsInputChange = (e) => {
		const { name, value } = e.target;

		setCoords((prevCoords) => ({
			...prevCoords,
			[name]: value,
		}));
	};

	const coordsFormatChange = (newCoords, isFormatChange = false) => {
		const callFormat = {
			true: () => saveEndPoint(newCoords),
			false: () => setCoords(newCoords)
		};

		callFormat[isFormatChange]();
	};

	const saveEndPoint = (point = coords) => {
		const { lat, lng } = point;
		const newPoint = [+lat, +lng];
		const dataPoints = pointsZone.slice();

		const lastIndex = -1;
		dataPoints.splice(lastIndex, 1, newPoint);

		setPointsZone(dataPoints);
		setEditCoords(false);
	};

	const endPointZone = pointsZone.length - 1;
	const [defLat, defLng] = pointsZone[endPointZone] ?? [0, 0];

	const data = {
		isEditView: isEditCoords,
		coords: {
			true: {
				lat: coords.lat,
				lng: coords.lng,
			},
			false: {
				lat: defLat.toFixed(5),
				lng: defLng.toFixed(5),
			},
		},
	};

	return (
		<div className={styles.lower}>
			{!!pointsZone.length && (
				<>
					<div className={styles.iconButton} onClick={handleBackPoint}>
						<ReturnIcon />
					</div>

					<EditPointsPanel
						data={data}
						actions={{
							save: saveEndPoint,
							change: coordsInputChange,
							changeFormat: coordsFormatChange,
							setEditView: (boolean) => setEditCoords(boolean),
						}}
					/>
				</>
			)}

			<VisibleBox />

			{isConfirmCreateZone && <CreateNewObjBtn reset={reset} handleAddObject={handleAddZone} />}

			<div className={styles.question} onClick={handleShowInstruction}>
				?
			</div>
		</div>
	);
});

export default CreateZone;
