import React, { useState } from 'react';
import styles from './ArchiveButtons.module.scss';
import { ClickAwayListener } from '@mui/material';
import CheckIcon from '@material-ui/icons/Check';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';
import NorthIcon from '@mui/icons-material/North';
import SouthIcon from '@mui/icons-material/South';
import viewsState from '../../../../store/viewsState';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const itemsSort = [
	{ id: 1, name: 'To size', value: 'duration', isSelected: false },
	{ id: 2, name: 'By date', value: 'date', isSelected: false },
];

const Sort = observer(() => {
	const { setReversSortRecords, setSortNameRecords } = viewsState;

	const [isIncrease, setIncrease] = useState(false);
	const [selected, setSelected] = useState('By date');
	const [isShowFilter, setShowFilter] = useState(false);
	const color = isShowFilter ? '#ff992c' : 'var(--colorText)';
	const sortColor = isIncrease ? '#ff992c' : 'var(--colorText)';

	const handleSelect = (name, value) => (e) => {
		e.stopPropagation();

		setSelected(name);
		setSortNameRecords(value);
		setShowFilter(false);
	};
	const handleCloseFilter = () => {
		setShowFilter(false);
	};

	const handleIncrease = () => {
		setIncrease((prev) => !prev);
		setReversSortRecords(isIncrease ? 1 : -1);
	};

	const increaseIcon = (Icon) => {
		return (
			<Icon
				sx={{ cursor: 'pointer', width: '20px', marginRight: '10px', color: `${sortColor}` }}
				onClick={handleIncrease}
			/>
		);
	};

	const sortIcon = (Icon) => {
		const inlineStyles = { height: '20px', width: '25px' };

		return <Icon sx={inlineStyles} onClick={handleCloseFilter} />;
	};

	const handleChangeFilterVisible = () => {
		setShowFilter((prevIsShowFilter) => !prevIsShowFilter);
	};

	return (
		<div className={styles.container}>
			<div className={styles.rightField}>Сортировать:</div>

			{increaseIcon(isIncrease ? NorthIcon : SouthIcon)}

			<div className={styles.selectHeader} style={{ color: `${color}` }} onClick={handleChangeFilterVisible}>
				{selected}
				{sortIcon(isShowFilter ? ExpandLessIcon : ExpandMoreIcon)}
			</div>

			{isShowFilter && (
				<ClickAwayListener onClickAway={handleCloseFilter}>
					<div className={styles.listArchive}>
						<div className={styles.containerFilter}>
							{itemsSort.map(({ name, value }) => {
								const isSelected = selected === name;

								return (
									<div
										key={uuidv4()}
										className={styles.btnListFilter}
										onClick={handleSelect(name, value)}
									>
										<div>{name}</div>
										{isSelected && <CheckIcon style={{ color: '#FF992C' }} />}
									</div>
								);
							})}
						</div>
					</div>
				</ClickAwayListener>
			)}
		</div>
	);
});

export default Sort;
