import { makeAutoObservable } from 'mobx';
import { makePersistable } from "mobx-persist-store";

const today = new Date();

class portState {
	ships = [];
	shipsClasses = [];
	selectedShipMmsi = '';
	selectedShipsMmsi = [];
	isViewAll = false;
	isCreateZone = false;
	isCreateSector = false;
	activeMenu = '';
	isConfirmCreateZone = false;
	isConfirmCreateSector = false;
	pathsShip = [];
	predictsShip = [];
	portMenuTitle = '';
	portMenuList = [];
	showShipsStore = {};
	selectedDays = false;
	calendarDates = [today, today];
	calendarSliderTimes = [today.getTime(), today.getTime()];
	formatDates = [];
	centerMap = [37.0329, 27.4261];

	constructor() {
		makeAutoObservable(this);

		makePersistable(this, {
			name: "PortStore",
			properties: [
				"showShipsStore"
			],
			storage: window.localStorage,
		})
	}

	setShips = (arr) => {
		this.ships = arr;
	};

	setShipsClasses = (arr) => {
		this.shipsClasses = arr;
	};

	setChangeShips = (ship) => {
		const index = this.ships.findIndex(({mmsi}) => mmsi === ship.mmsi)

		if(index === -1){
			this.ships.push(ship);
		}else{
			const {last_event, shipClass_name} = ship;
			const newShip = {
				...ship,
				event: last_event,
				type: shipClass_name,
				isShow: !!this.showShipsStore?.[ship.mmsi],
			};
			this.ships[index] = newShip;
		}
	}

	setSelectedShipMmsi = (ships) => {
		this.selectedShipMmsi = ships
	};

	setSelectedShipsMmsi = (ships) => {
		this.selectedShipsMmsi = ships
	};

	setAllShips = (view) => {
		this.ships.forEach((ship) => {
			ship.isShow = view
		});
	};

	setShowShipsStore = (obj) => {
		this.showShipsStore = obj
	};
	
	setViewAll = () => {
		this.isViewAll = !this.isViewAll;
	};
	setCreateZone = (view) => {
		this.isCreateZone = view;
	};
	setCreateSector = (view) => {
		this.isCreateSector = view;
	};
	setActiveMenu = (name) => {
		this.activeMenu = name;
	};
	setConfirmCreateZone = (boolean) => {
		this.isConfirmCreateZone = boolean;
	};
	setConfirmCreateSector = (boolean) => {
		this.isConfirmCreateSector = boolean;
	};
	setPathsShip = (i) => {
		this.pathsShip = i;
	};
	setPredictsShip = (i) => {
		this.predictsShip = i;
	};
	setPortMenuTitle = (name) => {
		this.portMenuTitle = name;
	};
	setPortMenuList = (arr) => {
		this.portMenuList = arr;
	};
	setSelectedDays = (boolean) => {
		this.selectedDays = boolean;
	};
	setCalendarDates = (dates) => {
		this.calendarDates = dates;
	};
	setCalendarSliderTimes = (times) => {
		this.calendarSliderTimes = times;
	};
	setFormatDates = (dates) => {
		this.formatDates = dates;
	};
	setCenterMap = (center) => {
		this.centerMap = center;
	}
}

export default new portState();
