import React, { useState } from 'react';
import styles from './CameraViewControlPanel.module.scss';
import { Box } from '@mui/material';
import ArrowCameraControlView from './ArrowCameraControlView/ArrowCameraControlView';
import Multimedia from './Multimedia/Multimedia';
import viewsState from '../../../store/viewsState';
import connectsState from '../../../store/connectsState';
import { logServerResponse } from '../../../helpers/helpers';
import { apiMainPost } from '../../../api/mainApi';

export default () => {
	const { selectedCameraId, cameras } = viewsState;

	const [isDisabled, setDisabled] = useState(false);

	const arrowAction = (text = '') => {
		const index = cameras.findIndex(({ id }) => id === selectedCameraId);
		if (index === -1) return;

		const { linkCameras } = connectsState;
		const { cameraKey } = cameras[index];

		const url = `${linkCameras}${cameraKey}/sendptz/${text}/`;

		apiMainPost(url).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = `Команда ${text} успешно отправлена`;
			const errorMsg = 'Не удалось отправить команду';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const stopSendMessage = () => {
		const index = cameras.findIndex(({ id }) => id === selectedCameraId);
		if (index === -1) return;

		const { linkCameras } = connectsState;
		const { cameraKey } = cameras[index];

		const url = `${linkCameras}${cameraKey}/sendptz/stop/`;

		apiMainPost(url).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Команда stop успешно отправлена';
			const errorMsg = 'Не удалось отправить команду';

			logServerResponse(isError, seccessMsg, errorMsg);
			
			if (isError) return;

			viewsState.setIsPresetSelected(false);
		});

		setTimeout(() => {
			setDisabled(false);
		}, 2000);
	};

	const arrowCameraActions = {
		aTop: (text = '') => arrowAction(text),
		aRight: (text = '') => arrowAction(text),
		aBottom: (text = '') => arrowAction(text),
		aLeft: (text = '') => arrowAction(text),
		aStopMes: () => stopSendMessage(),
	};

	const multimediaAction = () => {};

	const multimediaActions = {
		aPlus: (text = '') => arrowAction(text),
		aMinus: (text = '') => arrowAction(text),
		aVolume: (text = '') => multimediaAction(text),
		aStopMes: () => stopSendMessage(),
	};
	
	return (
		<Box className={styles.cameraViewControlPanel}>
			<Box className={styles.container}>
				<Box className={styles.cross}>
					<ArrowCameraControlView 
						actions={arrowCameraActions}
						isDisabled={isDisabled} 
						setDisabled={setDisabled}
					/>
				</Box>
				<Box className={styles.multimedia}><Multimedia actions={multimediaActions} /></Box>
			</Box>
		</Box>
	);
};
