import React, { useRef, useState } from 'react';
import ReactPlayer from 'react-player';
import Controls from './Controls/Controls';
import SelectCameras from './SelectCameras/SelectCameras';
import styles from './PlayerViews.module.scss';
import { observer } from 'mobx-react-lite';

const Format = (seconds) => {
	if (isNaN(seconds)) return '00:00:00';

	const date = new Date(seconds * 1000);
	const hh = date.getUTCHours().toString().padStart(2, '0');
	const mm = date.getUTCMinutes().toString().padStart(2, '0');
	const ss = date.getUTCSeconds().toString().padStart(2, '0');

	const min = hh ? mm.toString().padStart(2, '0') : mm;
	return `${hh}:${min}:${ss}`;
};

const PlayerViews = observer(({ name, url, isModal, setShowModal }) => {
	const playerRef = useRef(null);
	const [isPlaying, setPlaying] = useState(true);
	const [isMuted, setMuted] = useState(false);
	const [volume, setVolume] = useState(0);
	const [isSeek, setSeek] = useState(false);
	const [played, setPlayed] = useState(0);
	const [duration, setDuration] = useState(0);
	const [isShowControls, setShowControls] = useState(isModal);

	const openControls = () => {
		setShowControls(true);
	};

	const closeControls = () => {
		setShowControls(false);
	};

	const handlePlayPause = () => {
		setPlaying(!isPlaying);
	};

	const handleVolumeChange = (e, value) => {
		setVolume(value / 100);
		setMuted(!value);
	};

	const handleVolumeSeekDown = (e, value) => {
		setSeek(false);
		setVolume(value / 100);
	};

	const handleProgress = (change) => {
		setPlayed(change.played);
	};

	const handleTime = (time) => {
		setDuration(time);
	};

	const handleSeekChange = (e, value) => {
		setPlayed(value / 100);
	};

	const handleSeekMouseUp = (e, value) => {
		setSeek(false);
		playerRef.current.seekTo(value / 100, 'fraction');
	};

	const handleSeekMouseDown = () => {
		setSeek(true);
	};

	const handleRewind = () => {
		playerRef.current.seekTo(playerRef.current.getCurrentTime() - 10);
	};

	const handleFastForward = () => {
		playerRef.current.seekTo(playerRef.current.getCurrentTime() + 10);
	};

	const elapsedTime = Format(playerRef && playerRef.current ? playerRef.current.getCurrentTime() : '00:00');

	const durationTime = Format(duration ? duration : '00:00');

	return (
		<div onMouseEnter={openControls} onMouseLeave={closeControls} className={styles.block}>
			<SelectCameras item={name} />

			<ReactPlayer
				width={'100%'}
				height={'100%'}
				className={styles.video}
				ref={playerRef}
				playing={isPlaying}
				muted={isMuted}
				url={url}
				volume={volume}
				onProgress={!isSeek && handleProgress}
				onDuration={handleTime}
			/>

			{isShowControls && (
				<Controls
					playing={isPlaying}
					played={played}
					isMuted={isMuted}
					duration={durationTime}
					volume={volume}
					elapsedTime={elapsedTime}
					onPlayPause={handlePlayPause}
					onVolumeChange={handleVolumeChange}
					onVolumeSeekDown={handleVolumeSeekDown}
					onSeek={handleSeekChange}
					onSeekMouseDown={handleSeekMouseDown}
					onSeekMouseUp={handleSeekMouseUp}
					onRewind={handleRewind}
					onFastForward={handleFastForward}
					setShowModal={setShowModal}
				/>
			)}
		</div>
	);
});

export default PlayerViews;
