import { makeAutoObservable } from 'mobx';
import { makePersistable } from 'mobx-persist-store';

class zonesState {
	zones = [];
	sectors = [];
	selectedSectorId = 0;
	editZone = {};
	editSector = {};
	pointsZone = [];
	pointsSector = [];
	showZonesStore = {};
	showSectorsStore = {};
	sectorTypes = [];
	zoneTypes = [];

	constructor() {
		makeAutoObservable(this);

		makePersistable(this, {
			name: 'ZonesStore',
			properties: ['showZonesStore', 'showSectorsStore'],
			storage: window.localStorage,
		});
	}

	setZones = (zones) => {
		this.zones = zones;
	};

	setSectors = (sectors) => {
		this.sectors = sectors;
	};

	setSelectedSectorId = (id) => {
		this.selectedSectorId = id;
	};

	setShowZonesStore = (visibleZones) => {
		this.showZonesStore = visibleZones;
	};

	setShowSectorsStore = (visibleSectors) => {
		this.showSectorsStore = visibleSectors;
	};

	setAllSectors = (isShow) => {
		this.sectors.forEach((sector) => (sector.isShow = isShow));
	};

	setAllZones = (isShow) => {
		this.zones.forEach((zone) => (zone.isShow = isShow));

		this.sectors.forEach(({ zones }) => {
			zones.forEach((zone) => (zone.isShow = isShow));
		});
	};

	setVisibleSector = (idSector, isShow = false) => {
		const index = this.sectors.findIndex(({ id }) => id === idSector);
		if (index === -1) return;

		this.sectors[index].isShow = isShow;
	};

	setVisibleZone = (idSector, idZone, isShow = false) => {
		const index = this.zones.findIndex(({ id }) => id === idZone);
		if (index === -1) return;

		this.zones[index].isShow = isShow;

		const sectorIndex = this.sectors.findIndex(({ id }) => id === idSector);
		if (sectorIndex === -1) return;

		const zoneIndex = this.sectors[sectorIndex].zones.findIndex(({ id }) => id === idZone);
		if (zoneIndex === -1) return;

		this.sectors[sectorIndex].zones[zoneIndex].isShow = isShow;
	};

	setEditSector = (sector) => {
		this.editSector = sector;
	};

	setEditZone = (zone) => {
		this.editZone = zone;
	};

	setPointsZone = (points) => {
		this.pointsZone = points;
	};

	setPointsSector = (points) => {
		this.pointsSector = points;
	};

	setZoneTypes = (type) => {
		this.zoneTypes = type;
	};

	setSectorsTypes = (type) => {
		this.sectorTypes = type;
	};
}

export default new zonesState();
