import React from 'react';
import { Box } from '@mui/material';

export const IconSquares = ({ width = 20, height = 20, color = '#656565' }) => {
	return (
		<Box marginRight="12px" marginLeft="12px" marginTop="6px">
			<svg width={width} height={height} viewBox="0 0 20 20" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M8 1H1V8H8V1Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
				<path d="M19 1H12V8H19V1Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
				<path d="M19 12H12V19H19V12Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
				<path d="M8 12H1V19H8V12Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
			</svg>
		</Box>
	);
};
