import React from 'react';
import { Icon } from '@mui/material';
import warnSound from '../assets/sounds/preryivistyiy-zvuk.wav';
import regularSound from '../assets/sounds/uvedomlenie-o-soobschenii.wav';

export const objToArr = (data = {}) => {
	const items = [];
	const keys = Object.keys(data);

	const { length } = data[keys[0]] ?? { length: 0 };

	for (let i = 0; i < length; i++) {
		const item = {};

		for (const key of keys) {
			item[key] = data[key][i];
		}

		items.push(item);
	}

	return items;
};

export const selectColorConditional = (type = '') => {
	const colors = {
		'Задектированно движение': '#FF992C',
		'Разлив топлива': '#E30000',
		'Произошло столкновение': '#E30000',
		Столкновение: '#E30000',
		'Задектирован ультрафиолет': '#E30000',
		default: '#12B76A',
	};

	return colors[type] ?? colors.default;
};
export const selectSound = (type = '') => {
	const sounds = {
		'Задектированно движение': regularSound,
		'Разлив топлива': warnSound,
		'Произошло столкновение': warnSound,
		Столкновение: warnSound,
		'Задектирован ультрафиолет': warnSound,
		default: regularSound,
	};

	return sounds[type] ?? sounds.default;
};

export const CustomIcon = ({ icon, name, size = 14 }) => (
	<Icon sx={{ fontSize: size }}>
		<img src={icon} alt={name} />
	</Icon>
);

export const checkAllSelected = (items = []) => {
	const numSelItems = items.filter(({ isShow }) => isShow).length;
	const numItems = items.length;

	return numSelItems === numItems;
};

export const checkNegativeServerAns = (status = null) => {
	const positivesAns = { 200: false, 201: false, 204: false };

	return positivesAns[status] === undefined;
};

export const transformCoordsToDMS = (number) => {
	const deg = Math.floor(number);
	const minutesDecimal = (number - deg) * 60;
	const min = Math.floor(minutesDecimal);
	const sec = Math.round((minutesDecimal - min) * 60);

	return { deg, min, sec };
};

export const transformCoordsToDecimal = (coords) => {
	try {
		const { deg, min, sec } = coords;

		const sign = deg < 0 ? -1 : 1;
		const decimal = sign * (Math.abs(deg) + min / 60 + sec / 3600);

		return parseFloat(decimal.toFixed(5));
	} catch (e) {
		console.error(e.message);
	}

	return 0;
};

export const logServerResponse = (isError = false, seccessMsg = '', errorMsg = '') => {
	const erMsg = { true: errorMsg, false: seccessMsg };

	const isStop = true;
	if (isStop) return;

	return console.log(erMsg[isError]);
};
