import React, { useEffect, useState } from 'react';
import styles from './CreateSector.module.scss';
import zonesState from '../../../../../store/zonesState';
import { observer } from 'mobx-react-lite';
import portState from '../../../../../store/portState';
import CreateNewObjBtn from '../CreateNewObjBtn/CreateNewObjBtn';
import VisibleBox from '../VisibleBox/VisibleBox';
import EditPointsPanel from '../EditPointsPanel/EditPointsPanel';
import ReturnIcon from '../../../../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/ReturnIcon';

const CreateSector = observer(({ handleShowInstruction, setModalCreate, handleClose }) => {
	const { pointsSector, editSector, setEditSector, setPointsSector } = zonesState;
	const { isConfirmCreateSector } = portState;

	const [isEditCoords, setEditCoords] = useState(false);
	const [coords, setCoords] = useState({ lat: 0, lng: 0 });

	useEffect(() => {
		if (!pointsSector.length) return;

		const endPointSector = pointsSector.length - 1;
		const [lat, lng] = pointsSector[endPointSector];

		setCoords({ lat: lat.toFixed(5), lng: lng.toFixed(5) });
	}, [pointsSector]);

	const handleBackPoint = () => {
		setPointsSector(pointsSector.slice(0, -1));
	};

	const reset = () => {
		setPointsSector([]);
	};

	const editZoneMap = () => {
		setEditSector({ ...editSector, aisPoints: pointsSector });
		handleClose();
	};

	const handleAddSector = () => {
		Number.isInteger(editSector.id) ? editZoneMap() : setModalCreate(true);
	};

	const coordsInputChange = (e) => {
		const { name, value } = e.target;

		setCoords((prevCoords) => ({
			...prevCoords,
			[name]: value,
		}));
	};

	const coordsFormatChange = (newCoords, isFormatChange = false) => {
		const callFormat = {
			true: () => saveEndPoint(newCoords),
			false: () => setCoords(newCoords)
		};

		callFormat[isFormatChange]();
	};

	const saveEndPoint = (point = coords) => {
		const { lat, lng } = point;
		const newPoint = [+lat, +lng];
		const dataPoints = pointsSector.slice();

		const lastIndex = -1;
		dataPoints.splice(lastIndex, 1, newPoint);

		setPointsSector(dataPoints);
		setEditCoords(false);
	};

	const endPointSector = pointsSector.length - 1;
	const [defLat, defLng] = pointsSector[endPointSector] ?? [0, 0];

	const data = {
		isEditView: isEditCoords,
		coords: {
			true: {
				lat: coords.lat,
				lng: coords.lng,
			},
			false: {
				lat: defLat.toFixed(5),
				lng: defLng.toFixed(5),
			},
		},
	};

	return (
		<div className={styles.lower}>
			{!!pointsSector.length && (
				<>
					<div className={styles.iconButton} onClick={handleBackPoint}>
						<ReturnIcon />
					</div>

					<EditPointsPanel
						data={data}
						actions={{
							save: saveEndPoint,
							change: coordsInputChange,
							changeFormat: coordsFormatChange,
							setEditView: (boolean) => setEditCoords(boolean),
						}}
					/>
				</>
			)}

			<VisibleBox />

			{isConfirmCreateSector && <CreateNewObjBtn reset={reset} handleAddObject={handleAddSector} />}

			<div className={styles.question} onClick={handleShowInstruction}>
				?
			</div>
		</div>
	);
});

export default CreateSector;
