import React from 'react';
import { useState } from 'react';
import styles from './LowerMenu.module.scss';
import { useHistory } from 'react-router-dom';
import FolderOutlinedIcon from '@mui/icons-material/FolderOutlined';
import { iconsArchive } from '../data';
import { SelectedBtnArch } from './SelectedBtnArch/SelectedBtnArch';
import Sort from './ArchiveButtons/Sort';
import Filter from './ArchiveButtons/Filter';
import viewsState from '../../../store/viewsState';
import { observer } from 'mobx-react-lite';
import {IconVideo} from './IconsLowerMenu/IconVideo';

const LowerMenu = observer(({ isShowSorted, indexIcon, handleTableArchive }) => {
	const { removedItemsArchive } = viewsState;
	const history = useHistory();

	const buttonHoverColor = '#FF992C';
	const buttonColor = '#CFCFCF';

	const [styleColorArchive, setStyleColorArchive] = useState(buttonColor);
	const [styleColorIcon, setStyleColorIcon] = useState(buttonColor);

	const handleEnterColorIcon = () => {
		setStyleColorIcon(buttonHoverColor);
	};
	const handleLeaveColorIcon = () => {
		setStyleColorIcon(buttonColor);
	};
	const handleEnter = () => {
		setStyleColorArchive(buttonHoverColor);
	};
	const handleLeave = () => {
		setStyleColorArchive(buttonColor);
	};

	const changeTranslation = () => {
		history.push('/views/translation');
	};

	const archiveFilter = () => {
		return (
			<div className={styles.rightArchive}>
				<div className={styles.selectFilter}>
					{isShowSorted && <Sort />}

					<Filter />
				</div>
			</div>
		);
	};

	return (
		<div className={styles.parent}>
			<div className={styles.leftContainer}>
				<div
					className={styles.btnIconsArchive}
					onClick={handleTableArchive}
					onMouseEnter={handleEnter}
					onMouseLeave={handleLeave}
				>
					{iconsArchive[indexIcon]?.icon(styleColorArchive)}
				</div>

				<div
					className={styles.stylesButton}
					onClick={changeTranslation}
					onMouseEnter={handleEnterColorIcon}
					onMouseLeave={handleLeaveColorIcon}
				>
					<IconVideo color={styleColorIcon}/>

					<div>BROADCASTS</div>
				</div>
				<div className={styles.stylesButtonArchActive}>
					<FolderOutlinedIcon />

					<div>ARCHIVE</div>
				</div>
			</div>

			{!!removedItemsArchive.length && <SelectedBtnArch />}

			{archiveFilter()}
		</div>
	);
});

export default LowerMenu;
