import React from 'react';
import PlayerViews from '../PlayerViews/PlayerViews.jsx';
import styles from './ItemPlayer.module.scss';
import { observer } from 'mobx-react-lite';

const ItemPlayer = observer(({ player, setShowModal, handleChangePlayers }) => {
	return (
		<div className={styles.item}>
			<PlayerViews player={player} setShowModal={setShowModal} handleChangePlayers={handleChangePlayers} />
		</div>
	);
});

export default ItemPlayer;
