import React, { useState } from 'react';
import styles from './CoordsMouseMap.module.scss';
import { useMapEvents } from 'react-leaflet';
import 'leaflet/dist/leaflet.css';

const CoordsMouseMap = () => {
	const [coords, setCoords] = useState({ lat: 0.0, lng: 0.0 });

	const MapEvents = () => {
		useMapEvents({
			mousemove({ latlng: { lat, lng } }) {
				setCoords({ lat, lng });
			},
		});
		return <></>;
	};

	return (
		<>
			<div className={styles.coords}>
				<div>Lat: {coords.lat.toFixed(5)}</div>
				<div>Lng: {coords.lng.toFixed(5)}</div>
			</div>

			<MapEvents />
		</>
	);
};

export default CoordsMouseMap;
