import React, { useState } from 'react';
import {
	Box,
	Divider,
	Icon,
	IconButton,
	InputAdornment,
	List,
	ListItemButton,
	ListItemIcon,
	ListItemText,
	styled,
	TextField,
	Typography,
	Autocomplete,
} from '@mui/material';
import styles from './SearchMenu.module.scss';
import closeImg from '../../assets/portsMenu/close.png';
import searchImg from '../../assets/portsMenu/search.png';
import selectedCameraImg from '../../assets/portsMenu/NewSelectedItemIcon.png';
import { useRef } from 'react';
import { useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import SortButton from '../../helpers/SortButton/SortButton';

import SortedIcon from '../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/SortedIcon';
import { creareNestedFilters, getFilteredData } from '../../helpers/FiltersHelp';
import EmptyMessage from '../../helpers/EmptyMessage/EmptyMessage';
import { observer } from 'mobx-react-lite';
import viewsState from '../../store/viewsState';
import { prepareCamerasSingleData } from '../../api/prepareData/prepareCamerasData';
import { apiMainGet } from '../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const CustomIcon = ({ icon, name, size = 42 }) => (
	<Icon sx={{ fontSize: size }}>
		<img src={icon} alt={name} />
	</Icon>
);

const MuiAutocomplete = styled(Autocomplete)({
	'.MuiAutocomplete-inputRoot': {
		'& .MuiAutocomplete-endAdornment': {
			top: '50%',

			transform: 'translate(0, -50%)',
		},
	},
	'.MuiFormControl-root': {
		'& .Mui-focused .MuiOutlinedInput-notchedOutline': {
			borderColor: '#FF992C',
		},
	},
	'& .MuiButtonBase-root.MuiAutocomplete-clearIndicator': {
		color: 'var(--bgThemeButtonIcon)',
	},
});

const CustomTextField = styled(TextField)({
	'& label.Mui-focused': {
		backgroundColor: 'transparent',
	},
	'& .MuiInputBase-root': {
		padding: '5px 8px 5px 12px',

		borderColor: 'var(--bgThemeBorder)',
		borderRadius: 0,

		fontFamily: 'Inter',
		color: 'var(--colorText)',
		backgroundColor: 'var(--bgTheme)',
	},
	'& .MuiOutlinedInput-root': {
		'& fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
		'&:hover fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
		'&.Mui-focused fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
	},
});

const inlineStyles = {
	textFieldStyle: {
		padding: '5px 8px 5px 12px',
		borderRadius: 0,

		fontFamily: 'Inter',
		color: 'var(--colorText)',

		backgroundColor: 'var(--bgTheme)',
	},
};

export default observer(({ buttons = [], classModule = 'left' }) => {
	const searchMenuRef = useRef();
	const history = useHistory();

	const { isUpdateData } = viewsState;

	const [isOpen, setOpen] = useState(false);
	const [active, setActive] = useState(null);
	const [activeItemId, setActiveItemId] = useState(null);
	const [data, setData] = useState([]);
	const [menuIndex, setMenuIndex] = useState(0);

	const [displayedFilters, setDisplayedFilters] = useState([]);

	const { getData } = buttons[menuIndex].actions;
	const userFilters = ['sector', 'zone', 'name'];

	useEffect(() => {
		if (!buttons.length) return;

		const { selectItemId, getSelectedId } = buttons[menuIndex].actions;
		const data = getData();

		if (!data.length) {
			setDefaultSettings(selectItemId);
			return;
		}

		const filters = creareNestedFilters(data, userFilters);
		setDisplayedFilters(filters);
		setFilteredList(filters, data);

		const selectedId = getSelectedId();
		const index = data.findIndex(({ id }) => id === selectedId);
		const correctIndex = index === -1 ? 0 : index;
		const { id } = data[correctIndex];

		setActiveItemId(id);
		selectItemId(id);
	}, [menuIndex, getData()]);

	useEffect(() => {
		const { curDataUrl } = buttons[menuIndex];

		const isUrlExist = !!curDataUrl;
		const isItemIdExist = !!activeItemId;
		const isCorrect = isUrlExist && isItemIdExist;

		if (!isCorrect) return;
		const data = getData();

		const itemIndex = data.findIndex(({ id }) => id === activeItemId);
		if (itemIndex === -1) return;

		const { cameraKey } = data[itemIndex];

		const url = `${curDataUrl}${cameraKey}/`;
		const controller = new AbortController();

		getCameraData(url, controller);

		return () => {
			controller.abort();
		};
	}, [activeItemId, isUpdateData]);

	const getCameraData = (url, controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			if (error.isError) return;

			const camera = prepareCamerasSingleData(data);
			viewsState.setCurCameraData(activeItemId, camera);
		});
	};

	const setDefaultSettings = (selectItemId) => {
		setActiveItemId(null);
		selectItemId(null);
	};

	const setFilteredList = (filters = [], curData = buttons[menuIndex].actions.getData()) => {
		setData(getFilteredData(curData, filters, userFilters));
	};

	const handleChangeVisible = (index = null, isActive = true) => {
		const { getData } = buttons[index].actions;

		const data = getData();

		setActive(() => (isActive ? null : index));
		setOpen(!isActive);
		setData(data);
		setMenuIndex(index);

		const { pathname } = history.location;
		const { path } = buttons[index];

		if (pathname !== path) history.push(path);
	};

	const handleSelectedItem = (id) => {
		buttons[active].actions.selectItemId(id);
		setActiveItemId(id);
	};

	const handleCloseMenu = () => {
		setActive(null);
		setOpen(false);
		setData([]);
	};

	const list = () => {
		const { container, titleBox, iconButton, divider, list, workIndicator, item, itemText, itemIcon, menuTitle } =
			styles;

		const { title } = buttons[active];
		const { clientHeight } = searchMenuRef.current;

		const isEmpty = !data.length;
		const maxHeight = `calc(${clientHeight}px - 146px)`;

		const filterList = (e, value) => {
			const newData = value ? [value] : buttons[active].actions.getData();
			setData(newData);

			const filters = creareNestedFilters(newData, userFilters);
			setDisplayedFilters(filters);
			setFilteredList(filters, newData);
		};

		return (
			<Box className={container}>
				<Box className={titleBox}>
					<Typography className={menuTitle}>{title}</Typography>

					<IconButton className={iconButton} onClick={handleCloseMenu}>
						<CustomIcon name={'Close image'} icon={closeImg} size={22} />
					</IconButton>
				</Box>

				<Divider className={divider} />

				<Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
					<MuiAutocomplete
						disablePortal
						id="combo-box-demo"
						key={uuidv4()}
						options={buttons[active].actions.getData()}
						sx={{ mt: 2, mb: 2, width: '100%' }}
						forcePopupIcon={false}
						getOptionLabel={(option) => option.name}
						renderInput={(params) => {
							return (
								<Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
									<CustomTextField
										{...params}
										variant="outlined"
										fullWidth
										placeholder="Поиск"
										InputProps={{
											...params.InputProps,
											style: inlineStyles.textFieldStyle,
											startAdornment: (
												<InputAdornment position="start">
													<CustomIcon name={'Search image'} icon={searchImg} size={15} />
												</InputAdornment>
											),
										}}
									/>
								</Box>
							);
						}}
						onChange={filterList}
					/>

					<SortButton
						title={'Test'}
						icon={<SortedIcon />}
						displayedFilters={displayedFilters}
						setDisplayedFilters={setDisplayedFilters}
						action={(filters = [], data = data) => setFilteredList(filters, data)}
					/>
				</Box>

				{isEmpty ? (
					<EmptyMessage />
				) : (
					<List sx={{ maxHeight }} className={list}>
						{data.map(({ id, name, isWorking }) => {
							const backgroundColor = isWorking ? '#12B76A' : '#F04438';
							const isActiveItem = activeItemId === id;

							return (
								<ListItemButton
									key={uuidv4()}
									className={item}
									selected={isActiveItem}
									onClick={() => handleSelectedItem(id)}
								>
									<Box className={workIndicator} sx={{ backgroundColor }} />

									<ListItemText
										disableTypography
										primary={<Typography className={itemText}>{name}</Typography>}
									/>

									{isActiveItem && (
										<ListItemIcon className={itemIcon}>
											<CustomIcon name={'Selected Camera'} icon={selectedCameraImg} size={14} />
										</ListItemIcon>
									)}
								</ListItemButton>
							);
						})}
					</List>
				)}
			</Box>
		);
	};

	return (
		<Box className={styles.searchMenu} ref={searchMenuRef}>
			<Box className={`${styles.btnPos} ${styles[classModule]}`}>
				{buttons.map(({ name, icon }, index) => {
					const isActive = active === index;
					const iconButtonClass = `${styles.iconButton} ${isActive && styles.active}`;

					return (
						<IconButton
							key={uuidv4()}
							className={iconButtonClass}
							disableRipple
							onClick={() => handleChangeVisible(index, isActive)}
						>
							<CustomIcon name={name} icon={icon} />
						</IconButton>
					);
				})}
			</Box>

			{isOpen && list()}
		</Box>
	);
});
