import { makeAutoObservable } from 'mobx';
import { makePersistable } from 'mobx-persist-store';
import { getDefaultCamera } from '../helpers/defaultData';

const idCounter = {
	sectorId: 0,
	zoneId: 0,
	cameraId: 0,
	recItemId: 0,
	recTimeId: 0,
	presetsId: 10,
};

const videoResolution = ['720', '480', '360'];

const recognition = [
	{ id: idCounter.recItemId++, name: 'Ships', isSelected: false },
	{ id: idCounter.recItemId++, name: 'People', isSelected: false },
	{ id: idCounter.recItemId++, name: 'Pollution', isSelected: false },
	{ id: idCounter.recItemId++, name: 'Foreign objects', isSelected: false },
];

const reconnectTime = [
	{ id: idCounter.recTimeId++, name: '5 minutes', isActive: true, isDisabled: false },
	{ id: idCounter.recTimeId++, name: '10 minutes', isActive: false, isDisabled: false },
	{ id: idCounter.recTimeId++, name: '15 minutes', isActive: false, isDisabled: false },
];

const presets = [
	{ id: idCounter.presetsId++, name: `Preset №${idCounter.presetsId}`, isActive: true },
	{ id: idCounter.presetsId++, name: `Preset №${idCounter.presetsId}`, isActive: false },
	{ id: idCounter.presetsId++, name: `Preset №${idCounter.presetsId}`, isActive: false },
];

const controlMenu = {
	recognition,
	reconnectTime,
	presets,
};

class viewsState {
	recordings = [];
	isUpdateData = false;

	selectCamera = 'camera 1';
	selectTranslation = {name: '', url: ''};
	selectRecord = {name: '', url: ''};

	selectedCameraId = null;
	selectedPresetId = null;

	isArchive = false;
	isFullScreenView = false;

	removedItemsArchive = [];

	isConfirmDeleteRecords = false;

	isShowControlMenu = false;
	menuNameControl = 'Polygon Menu';

	isPresetSelected = false;

	reversSortRecords = 1;
	sortNameRecords = 'date';

	selectUrl = '';
	showWindows = 4;
	streams = [
		{ id: idCounter.cameraId++, isWorking: true, name: 'поток1', urls: ['720', '480', '360'], isSelected: true },
		{ id: idCounter.cameraId++, isWorking: true, name: 'поток2', urls: ['720', '480', '360'], isSelected: true },
		{ id: idCounter.cameraId++, isWorking: true, name: 'поток3', urls: ['720', '480', '360'], isSelected: true },
		{ id: idCounter.cameraId++, isWorking: true, name: 'поток4', urls: ['720', '480', '360'], isSelected: true },
		{ id: idCounter.cameraId++, isWorking: true, name: 'поток5', urls: ['720', '480', '360'], isSelected: true },
	];

	isVisibleModalAddPreset = false;

	cameras = [];

	wsSound = '';

	constructor() {
		makeAutoObservable(this);
	}

	setReversSortRecords = (value) => {
		this.reversSortRecords = value;
	};

	setSortNameRecords = (name) => {
		this.sortNameRecords = name;
	};

	setSelectTranslation = (obj) => {
		this.selectTranslation = obj;
	};

	setSelectRecord = (obj) => {
		this.selectRecord = obj;
	};

	setWsSound = (soket) => {
		this.wsSound = soket;
	};

	changeUpdateData = () => {
		this.isUpdateData = !this.isUpdateData;
	};

	setRecordings = (arr) => {
		this.recordings = arr;
	};

	setConfirmDeleteRecords = (boolean) => {
		this.isConfirmDeleteRecords = boolean;
	};

	setRemovedItemsArchive = (idRecord) => {
		const index = this.removedItemsArchive.findIndex((id) => id === idRecord);
		const indexRecords = this.recordings.findIndex(({ id }) => id === idRecord);

		const { isDelete } = this.recordings[indexRecords];
		this.recordings[indexRecords].isDelete = !isDelete;
		this.removedItemsArchive =
			index === -1
				? [...this.removedItemsArchive, idRecord]
				: this.removedItemsArchive.filter((item) => item !== this.removedItemsArchive[index]);
	};

	setRemovedItemsTableArchive = (recordsId = []) => {
		this.removedItemsArchive = [];

		recordsId.forEach((record) => {
			this.removedItemsArchive = [...this.removedItemsArchive, record];
		});
	};

	selectAllRecords = () => {
		const isSelectAll = this.removedItemsArchive.length === this.recordings.length;

		if (isSelectAll) return;

		this.removedItemsArchive = this.recordings.map((record) => {
			record.isDelete = true;
			return record.id;
		});
	};

	clearRemovedItemsArchive = () => {
		this.removedItemsArchive = [];
		this.recordings.forEach((record) => {
			record.isDelete = false;
		});
	};

	setShowControlMenu = (boolean) => {
		this.isShowControlMenu = boolean;
	};

	setNameMenuControl = (menu) => {
		this.menuNameControl = menu;
	};

	setArchive = (boolean) => {
		this.isArchive = boolean;
	};

	setSelectCamera = (camera) => {
		this.selectCamera = camera;
	};

	setShowWindows = (i) => {
		this.showWindows = i;
	};

	setCameras = (cameras = []) => {
		this.cameras = cameras.map((camera) => ({ ...camera, controlMenu: { ...controlMenu } }));
	};

	setMonitorCameras = (cameras = []) => {
		this.cameras = cameras;
	};
  
	setCurCameraData = (camId = null, camera = {}) => {
		try {
			const index = this.cameras.findIndex(({ id }) => id === camId);
			const oldCamera = this.cameras[index];
	
			const editCamera = {
				...camera,
				controlMenu: {
					...camera.controlMenu,
					recognition: camera.controlMenu.recognition.map((rec, index) => ({
						...rec,
						isSelected: oldCamera.controlMenu.recognition[index]?.isSelected || false,
					})),
				},
			};

			this.cameras[index] = {...editCamera};
			this.cameras = [...this.cameras];
		} catch (e) {
			console.error(e);
		}
	};

	setFullScreenView = (boolean) => {
		this.isFullScreenView = boolean;
	};

	setUrlCameras = (url) => {
		this.selectUrl = url;
	};

	setSelectCameraId = (camId = null) => {
		this.selectedCameraId = camId;
	};

	setSelectPresetId = (presId = null) => {
		this.selectedPresetId = presId;
	};

	setAllCameras = (view) => {
		this.cameras.forEach((camera) => {
			camera.isShow = view;
		});
	};

	setStreams = (streams = []) => {
		this.streams = streams.map((stream) => ({ ...stream, isSelected: true }));
	};
	setCamerasSelected = (cameras) => {
		this.cameras = cameras;
	};
	setCamerasSelectedAll = (cameras, isSelected = false) => {
		this.cameras = cameras.map((camera) => ({ ...camera, isSelected: isSelected }));
	};

	setVisibleModalAddPreset = (isVisible = false) => {
		this.isVisibleModalAddPreset = isVisible;
	};

	changeVisibleModalAddPreset = () => {
		this.isVisibleModalAddPreset = !this.isVisibleModalAddPreset;
	};

	setControlMenuRecognitionChangeVisible = (camId, selectRecId) => {
		const camIndex = this.cameras.findIndex(({ id }) => id === camId);

		if (camIndex === -1) return;

		const { recognition } = this.cameras[camIndex].controlMenu;

		const recIndex = recognition.findIndex(({ id }) => id === selectRecId);
		const { isSelected } = recognition[recIndex];
		recognition[recIndex].isSelected = !isSelected;
	};

	setChangeReconnectTime = (camId, selectRecId) => {
		const camIndex = this.cameras.findIndex(({ id }) => id === camId);

		if (camIndex === -1) return;

		const { reconnectTime } = this.cameras[camIndex].controlMenu;

		reconnectTime.forEach((item) => {
			item.isActive = false;
		});
		const recIndex = reconnectTime.findIndex(({ id }) => id === selectRecId);
		const { isActive } = reconnectTime[recIndex];
		reconnectTime[recIndex].isActive = !isActive;
	};

	setSelectedPreset = (camId, selectPresId) => {
		const camIndex = this.cameras.findIndex(({ id }) => id === camId);

		if (camIndex === -1) return;

		const { presets } = this.cameras[camIndex].controlMenu;

		presets.forEach((item) => item.isActive = false);

		const presIndex = presets.findIndex(({ id }) => id === selectPresId);
		const { isActive } = presets[presIndex];
		presets[presIndex].isActive = !isActive;
	};
	
	setIsPresetSelected = (isSelect = false) => {
		this.isPresetSelected = isSelect;
	};

	changeIsPresetSelected = () => {
		this.isPresetSelected = !this.isPresetSelected;
	};

	addPreset = (camId, preset) => {
		const camIndex = this.cameras.findIndex(({ id }) => id === camId);

		if (camIndex === -1) return;

		const { presets } = this.cameras[camIndex].controlMenu;
		preset.id ||= this.getIncPresetId();

		presets.push(preset);
	};

	setPresetName = (camId, presId, name) => {
		const camIndex = this.cameras.findIndex(({ id }) => id === camId);

		if (camIndex === -1) return;

		const { presets } = this.cameras[camIndex].controlMenu;
		const presIndex = presets.findIndex(({ id }) => id === presId);

		if (presIndex === -1) return;

		presets[presIndex].name = name;
	};

	deletePreset = (camId, presId) => {
		try {
			const camIndex = this.cameras.findIndex(({ id }) => id === camId);
			const { presets } = this.cameras[camIndex].controlMenu;

			const presIndex = presets.findIndex(({ id }) => id === presId);
			presets.splice(presIndex, 1);
		} catch (error) {
			console.error(error);
		}
	};

	addZone = (camId, name, id = ++idCounter.zoneId) => {
		const index = this.cameras.findIndex(({ id }) => id === camId);

		if (index === -1) return;

		this.cameras[index].zone = { id, name };
	};

	addSector = (camId, name, id = ++idCounter.sectorId) => {
		const index = this.cameras.findIndex(({ id }) => id === camId);

		if (index === -1) return;

		this.cameras[index].sector = { id, name };
	};

	getIncPresetId = () => idCounter.presetsId++;
}

export default new viewsState();
