import React from 'react';
import { MapContainer, TileLayer, ZoomControl } from 'react-leaflet';
import styles from './RadarMap.module.scss';
import Ships from '../../Port/Map/ShipsOnMap/ShipsOnMap';
import PolygonsOnMap from '../../Port/Map/PolygonsOnMap/PolygonsOnMap';
import { observer } from 'mobx-react-lite';
import zonesState from '../../../store/zonesState';
import portState from '../../../store/portState';
import 'leaflet/dist/leaflet.css';
import { v4 as uuidv4 } from 'uuid';

const ControlZoom = () => {
	return <ZoomControl position='bottomright' />;
};

const RadarMap = observer(() => {
	const { zones, sectors } = zonesState;
	const { centerMap } = portState;

	const mapPath = `http://${window.location.host}/static/map/{z}/{x}/{y}.png`;

	const polygons = [
		{ id: 1, value: 'Zones', items: zones, color: '#04B900' },
		{ id: 2, value: 'Sectors', items: sectors, color: 'white' },
	];

	return (
		<MapContainer 
			className={styles.map} 
			center={centerMap} 
			zoomControl={false}
			zoom={16} 
			minZoom={11} 
			maxZoom={18}
		>
			<TileLayer url={mapPath} />

			<ControlZoom />

			<Ships />

			{polygons.map((item) => (
				<PolygonsOnMap key={uuidv4()} item={item} />
			))}
		</MapContainer>
	);
});

export default RadarMap;
