import React from 'react';
import styles from './ModalChoice.module.scss';
import { Modal } from '@mui/material';
import close from '../../../../assets/portsMenu/close.png';
import portState from '../../../../store/portState';
import zonesState from '../../../../store/zonesState';
import { observer } from 'mobx-react-lite';

const ModalChoice = observer(({ isModal, setShowModal }) => {
	const { setCreateZone, setCreateSector } = portState;

	const viewCreateZone = () => {
		setShowModal(false);
		setCreateZone(true);
	};

	const viewCreateSector = () => {
		setShowModal(false);
		setCreateSector(true);
	};

	return (
		<Modal open={isModal} closeAfterTransition onClose={() => setShowModal(false)}>
			<div className={styles.container}>
				<div className={styles.header}>
					What do you want to create?
					<img className={styles.closeIcon} src={close} onClick={() => setShowModal(false)} />
				</div>

				<div className={styles.content}>
					{!!zonesState.sectors.length &&
						<div className={styles.button}>
							<button
								className={styles.modBtn}
								onClick={viewCreateZone}
							>
								Create zone
							</button>
						</div>
					}

					<div className={styles.button}>
						<button
							className={styles.modBtn}
							onClick={viewCreateSector}
						>
							Create Sector
						</button>
					</div>
				</div>
			</div>
		</Modal>
	);
});

export default ModalChoice;
