import React, { useEffect, useState } from 'react';
import { observer } from 'mobx-react-lite';
import styles from './PortMenu.module.scss';
import MainMenu from './MainMenu/MainMenu.js';
import Symbols from './Symbols/Symbols.js';
import portState from '../../../store/portState';
import viewsState from '../../../store/viewsState';
import zonesState from '../../../store/zonesState';
import radarsState from '../../../store/radarsState';
import EditZone from './EditZone/EditZone.js';
import EditSector from './EditSector/EditSector';
import DrawerMenu from './DrawerMenu/DrawerMenu';
import {
	PORT_MENU_TITLE_AISES,
	PORT_MENU_TITLE_CAMERAS,
	PORT_MENU_TITLE_RADARS,
	PORT_MENU_TITLE_SECTORS,
	PORT_MENU_TITLE_SHIPS,
} from '../../../helpers/defaultData';

const SelectEditMenu = observer(({ children }) => {
	const { editZone, editSector } = zonesState;
	const isSelectEdit = editZone.id || editSector.id;

	return isSelectEdit ? (
		<>
			{editZone.id && <EditZone />}
			{editSector.id && <EditSector />}
		</>
	) : (
		children
	);
});

const InfoModule = observer(({ isShowInfo, children }) => {
	return isShowInfo ? <Symbols /> : children;
});

const PortMenu = observer(({ setShowModal }) => {
	const { ships, activeMenu, setPortMenuTitle, setPortMenuList } = portState;
	const { cameras } = viewsState;
	const { radars, aises } = radarsState;
	const { zones, sectors } = zonesState;

	const [data, setData] = useState([]);
	const [createSector, setCreateSector] = useState(false);
	const [isShowInfo, setShowInfo] = useState(false);

	const setMenuData = (title = '', data = [], isCreate = false) => {
		setPortMenuTitle(title);
		setPortMenuList(data);
		setData(data);
		setCreateSector(isCreate);
		setShowInfo(false);
	};

	const openInfo = () => {
		setCreateSector(false);
		setShowInfo(true);
	};

	useEffect(() => {
		const menu = {
			ships: () => setMenuData(PORT_MENU_TITLE_SHIPS, ships),
			cameras: () => setMenuData(PORT_MENU_TITLE_CAMERAS, cameras),
			radars: () => setMenuData(PORT_MENU_TITLE_RADARS, radars),
			ais: () => setMenuData(PORT_MENU_TITLE_AISES, aises),
			zones: () => setMenuData(PORT_MENU_TITLE_SECTORS, sectors, true),
			info: () => openInfo(),
			default: () => setMenuData('No Any Elements'),
		};

		menu[activeMenu || 'default']();
	}, [activeMenu, zones, sectors, ships, radars, aises]);

	const getResetData = () => [...data];

	return (
		<div className={styles.container}>
			<DrawerMenu />

			{activeMenu && activeMenu !== 'All' && (
				<div className={styles.containerMenu}>
					<InfoModule isShowInfo={isShowInfo}>
						<SelectEditMenu>
							<MainMenu
								dataList={data}
								resetData={getResetData}
								createSector={createSector}
								setShowModal={setShowModal}
							/>
						</SelectEditMenu>
					</InfoModule>
				</div>
			)}
		</div>
	);
});

export default PortMenu;
