import React, { useState } from 'react';
import PlayerViews from './PlayerViews/PlayerViews.jsx';
import styles from './ItemPlayerModal.module.scss';
import { observer } from 'mobx-react-lite';

const ItemPlayer = observer(({ idPlayer, record, setShowModal }) => {
	const [isShowTable, setShowTable] = useState(false);
	const [handleQuality, setHandleQuality] = useState(0);

	const {name, url} = record;

	const handleShowTable = () => {
		setShowTable(!isShowTable);
	};

	return (
		<div className={styles.item}>
			<PlayerViews
				idPlayer={idPlayer}
				name={name}
				url={url}
				setShowModal={setShowModal}
				quality={handleQuality}
				setHandleQuality={setHandleQuality}
				handleShowTable={handleShowTable}
			/>
		</div>
	);
});

export default ItemPlayer;
