import {makeAutoObservable} from 'mobx';

class eventArchiveState{
	tableOneId = 0;
	tableTwoId = 0;
	tableThreeId = 0;
	tableFourId = 0;
	tableFiveId = 0;
	tableSixId = 0;

	eventsArchive = [[], [], [], [], [], []];
	availableDevices = [[], [], [], [], [], []];
	selectedDevices = [[], [], [], [], [], []];
	modDevices = [false, false, false, false, false, false];

	constructor() {
		makeAutoObservable(this);
	};

	setEventsArchive = (events) => {
		this.eventsArchive = events;
	};

	setAvailableDevices = (devices, num) => {
		this.availableDevices[num] = devices;
	};

	setSelectedDevices = (devices, num) => {
		this.selectedDevices[num] = devices;
	};

	setModDevices = (num) => {
		this.modDevices[num] = true;
	};

	getTableOneId = () => this.tableOneId++;
	getTableTwoId = () => this.tableTwoId++;
	getTableThreeId = () => this.tableThreeId++;
	getTableFourId = () => this.tableFourId++;
	getTableFiveId = () => this.tableFiveId++;
	getTableSixId = () => this.tableSixId++;
}

export default new eventArchiveState();