import React, {useState, useEffect} from 'react';
import L from 'leaflet';
import 'leaflet-rotatedmarker';
import { Marker } from 'react-leaflet';
import { observer } from 'mobx-react-lite';
import portState from '../../../../../store/portState';
import tanker from '../../../../../assets/ships/tankerShip.png';
import cargo from '../../../../../assets/ships/cargoShip.png';
import fishing from '../../../../../assets/ships/fishingShip.png';
import passenger from '../../../../../assets/ships/passengerShip.png';
import pilot from '../../../../../assets/ships/pilotShip.png';
import tourists from '../../../../../assets/ships/touristsShip.png';
import tow from '../../../../../assets/ships/towShip.png';
import other from '../../../../../assets/ships/otherShip.png';
import selectedShip from '../../../../../assets/ships/selectedShip.png';
import 'leaflet/dist/leaflet.css';

const changeIcon = (type = '') => {
	switch (type) {
	case 'Танкер':
		return tanker;
	case 'Грузовое судно':
		return cargo;
	case 'Рыболовное судно':
		return fishing;
	case 'Пассажирское судно':
		return passenger;
	case 'Лоцманское судно':
		return pilot;
	case 'Прогулочное судно':
		return tourists;
	case 'Буксир':
		return tow;
	case 'Буксирное судно':
		return tow;

	default:
		return other;
	}
};

const ShipItem = observer(({ ship, openInfoShip, viewInfoShip }) => {
	const {event, type} = ship;
	const {latitude, longitude, vesselDirection} = event;

	const {selectedShipMmsi} = portState;

	const [isSelected, setSelected] = useState(false);

	const shipIcon = (size, type) => new L.Icon({ 
		iconUrl: isSelected ? selectedShip : changeIcon(type), 
		iconSize: size 
	});

	useEffect(() => {
		if(selectedShipMmsi && (selectedShipMmsi !== ship.mmsi)){
			setSelected(false);
		}
		!openInfoShip && setSelected(false);
	}, [selectedShipMmsi, openInfoShip]);

	const handleSelected = () => {
		viewInfoShip(ship);
		setSelected((prev) => !prev);
	};

	return (
		<Marker
			icon={shipIcon([40, 12], type)}
			position={[latitude || 0, longitude || 0]}
			rotationAngle={parseInt(vesselDirection) + 270}
			rotationOrigin={'center'}
			zIndexOffset={1000}
			eventHandlers={{
				click: () => {
					handleSelected();
				},
			}}
		/>
	);
});

export default ShipItem;
