import { Button } from '@mui/material';
import React, { useState } from 'react';
import { useEffect } from 'react';
import viewsState from '../../../../../store/viewsState';
import ModalEditPreset from '../ModalEditPreset/ModalEditPreset';
import styles from './AddPresetButton.module.scss';
import connectsState from '../../../../../store/connectsState';
import { prepareZonesData } from '../../../../../api/prepareData/prepareZonesData';
import { logServerResponse } from '../../../../../helpers/helpers';
import { apiMainGet, apiMainPost } from '../../../../../api/mainApi';

export default () => {
	const { confirmButtons, save } = styles;

	const modalPositionStyle = {
		position: 'absolute',
		top: '50%',
		left: '50%',
		transform: 'translate(-50%, -50%)',
	};

	const [isShow, setShow] = useState(false);
	const [zones, setZones] = useState([]);
	const [newPreset, setNewPreset] = useState({});

	useEffect(() => {
		const url = connectsState.linkAisZonePoints;
		const controller = new AbortController();

		getZones(url, controller);

		return () => {
			controller.abort();
		};
	}, []);

	
	useEffect(() => {
		if (!newPreset?.name) return;
		
		try {
			const { cameras, selectedCameraId } = viewsState;
			const { linkCvPresets } = connectsState;
			
			const { cameraKey } = cameras.find(({ id }) => id === selectedCameraId);
			const url = `${linkCvPresets}${cameraKey}/set_preset/`;
			
			addPreset(url, newPreset);
		} catch (error) {
			console.error(error);
		}
	}, [newPreset]);

	const getZones = (url, controller ) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const addPreset = (url, newPreset) => {
		apiMainPost(url, newPreset).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Добавление пресета - успешно';
			const errorMsg = 'Не удалось добавить пресет';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			setNewPreset({});
		});
	};

	const handleShowModal = () => {
		setShow(true);
	};

	const handleSave = (preset = { name: '', zoneId: null }) => {
		const { selectedCameraId } = viewsState;
		
		setNewPreset({
			name: preset.name,
			camera: selectedCameraId,
			zone: preset.zoneId,
		});
	};

	return (
		<div className={confirmButtons}>
			<Button className={save} onClick={handleShowModal}>
				Add preset
			</Button>

			<ModalEditPreset
				isShow={isShow}
				setShow={setShow}
				zones={zones}
				positionStyle={modalPositionStyle}
				title={'Add preset'}
				label={'Preset name'}
				btnNameOk={'Create preset'}
				handleOk={(preset = { name: '', zoneId: null }) => handleSave(preset)}
				errorMsg={'You cannot create a preset without a name'}
			/>
		</div>
	);
};
