import { Box, Button, List, ListItemButton, ListItemIcon, Typography } from '@mui/material';
import { observer } from 'mobx-react-lite';
import React from 'react';
import { useState } from 'react';
import { useEffect } from 'react';
import canvasState from '../../../../store/canvasState';
import viewsState from '../../../../store/viewsState';
import Polygons from '../../../Canvas/ChangeFigure/Polygons';
import ModalEditPolygon from './ModalEditPolygon/ModalEditPolygon';
import styles from './PolygonMenu.module.scss';
import connectsState from '../../../../store/connectsState';
import { logServerResponse } from '../../../../helpers/helpers';
import { apiMainDelete, apiMainGet, apiMainPatch, apiMainPost } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const EmptyMessage = ({ title = '', subTitle = '' }) => {
	return (
		<div className={styles.emptyMessage}>
			<div className={styles.erTitle}>{title}</div>
			<div className={styles.erTitle}>{subTitle}</div>
			<p className={styles.erMessage}>Report a problem</p>
		</div>
	);
};

const ConfirmButtons = ({ handleSave = () => {}, handleCancel = () => {} }) => {
	const { confirmButtons, save, cansel } = styles;

	return (
		<div className={confirmButtons}>
			<Button className={save} onClick={handleSave}>
				Save
			</Button>
			<Button className={cansel} onClick={handleCancel}>
				Cancel
			</Button>
		</div>
	);
};

const EditZonesButton = () => {
	const { confirmButtons, save } = styles;

	const handleChangeEditZones = () => {
		const { isCreatePolygon, setCreatePolygon } = canvasState;

		setCreatePolygon(!isCreatePolygon);
	};

	return (
		<div className={confirmButtons}>
			<Button className={save} onClick={handleChangeEditZones}>
				Edit zones
			</Button>
		</div>
	);
};

export default observer(() => {
	const { selectedCameraId, selectedPresetId } = viewsState;
	const { isCreatePolygon, saveDataTest } = canvasState;

	const [tempAreas, setTempAreas] = useState('');
	const [polygonId, setPolygonId] = useState(null);
	const [polygonTypes, setPolygonTypes] = useState([]);
	const [isModalEdit, setModalEdit] = useState(false);

	const polygons = saveDataTest?.[selectedCameraId]?.[selectedPresetId] ?? [];
	const { cameras } = viewsState;

	useEffect(() => {
		try {
			const camIndex = cameras.findIndex(({ id }) => id === selectedCameraId);
			const presIndex = cameras[camIndex].controlMenu.presets.findIndex(({ isActive }) => isActive);

			const { polygons } = cameras[camIndex].controlMenu.presets[presIndex];
			const newPolygons = polygons.map((polygon) => ({ ...polygon, points: calculateNewPoints(polygon.points) }));

			canvasState.setRawData(selectedCameraId, selectedPresetId, newPolygons);
		} catch (e) {
			console.error(e.message);
		}
	}, [selectedCameraId, selectedPresetId]);

	useEffect(() => {
		if (isModalEdit) return;

		const url = connectsState.linkGetPolygonTypes;
		const controller = new AbortController();

		getPolygonTypes(url, controller);

		return () => {
			controller.abort();
		};
	}, [isModalEdit]);

	useEffect(() => {
		if (!isCreatePolygon) return;

		const { canvas, currentPolygonNum } = canvasState;
		const areas = new Polygons(canvas);

		if (currentPolygonNum !== -1) {
			areas.setCurPolygon(currentPolygonNum);
			areas.polygonSelection();
		}

		setTempAreas(areas);

		return () => {
			canvasState.setCreatePolygon(false);
		};
	}, [selectedCameraId, selectedPresetId, canvasState.canvas, isCreatePolygon]);

	const getPolygonTypes = (url, controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение типов полигонов - успешно';
			const errorMsg = 'Не удалось получить типы полигонов';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			setPolygonTypes(data);
		});
	};

	const calculateNewPoints = (points = []) => {
		const { newSize } = canvasState.canvasReSize;

		const cameraWidth = 1920;
		const cameraHeight = 1080;

		const coefWidth = newSize.width / cameraWidth;
		const coefHeight = newSize.height / cameraHeight;

		return points.map((point) => ({ ...point, x: point.x * coefWidth, y: point.y * coefHeight }));
	};

	const handleSelectPolygon = (polId = null) => {
		const index = polygons.findIndex(({ id }) => id === polId);

		const { canvas } = canvasState;

		setPolygonId(polId);

		canvasState.setCurrentPolygonNum(index);
		canvasState.setPolygonSelect(true);

		const areas = new Polygons(canvas);
		areas.setCurPolygon(index);
		areas.polygonSelection();
	};

	const handleClickEdit = (e, polId = null) => {
		e.stopPropagation();

		setModalEdit(true);
		setPolygonId(polId);
	};

	const handleSave = () => {
		const { canvas, setAllPolygons, rawData, setCreatePolygon, setCurrentPolygonNum } = canvasState;

		const newPolygons = tempAreas.polygons
			.filter(({ id }) => {
				const index = rawData?.[selectedCameraId]?.[selectedPresetId]?.findIndex((tPol) => tPol.id === id);

				return index === -1;
			})
			.map((polygon) => ({
				name: polygon.getName(),
				cvPoints: polygon.getPoints(),
				startSize: polygon.getStartSize(),
				presetCv: selectedPresetId,
				type: polygon.getAttributeType(),
			}));

		const oldPolygons = tempAreas.polygons
			.filter(({ id }) => {
				const index = rawData?.[selectedCameraId]?.[selectedPresetId]?.findIndex((tPol) => tPol.id === id);

				return index !== -1;
			})
			.map((polygon) => ({
				id: polygon.id,
				name: polygon.getName(),
				cvPoints: polygon.getPoints(),
				startSize: polygon.getStartSize(),
				presetCv: selectedPresetId,
				type: polygon.getAttributeType(),
			}));

		const polygonsForSend = JSON.parse(JSON.stringify([...oldPolygons, ...newPolygons]));

		polygonsForSend.forEach(({ cvPoints }) => {
			cvPoints.forEach((point) => {
				const { newSize } = canvasState.canvasReSize;

				const FW = 1920;
				const FH = 1080;

				const NW = newSize.width;
				const NH = newSize.height;

				point.x = (point.x / NW) * FW;
				point.y = (point.y / NH) * FH;
			});
		});

		const url = connectsState.linkCreateChangePolygon;

		let isError = false;
		let maxTime = 300;

		for (let i = 0; i < polygonsForSend.length; ++i) {
			maxTime = 200 * (i + 1);

			setTimeout(() => {
				sendPolygon(url, polygonsForSend[i]);
			}, maxTime);

			if (isError) break;
		}

		const seccessMsg = 'Отправка полигонов - успешна';
		const errorMsg = 'Не удалось отправить полигоны';

		logServerResponse(isError, seccessMsg, errorMsg);

		if (isError) return;

		setTimeout(() => {
			setAllPolygons(selectedCameraId, selectedPresetId, tempAreas.polygons);
			setCreatePolygon(false);
			setCurrentPolygonNum(-1);
			setTempAreas(new Polygons(canvas));
			viewsState.changeUpdateData();
		}, maxTime);
	};

	const sendPolygon = (url, polygon) => {
		let isError = false;
		apiMainPost(url, polygon).then(({ error }) => (isError = error.isError));

		return isError;
	};

	const handleCancel = () => {
		const { canvas, setCreatePolygon, setCurrentPolygonNum } = canvasState;

		setCreatePolygon(false);
		setCurrentPolygonNum(-1);
		setTempAreas(new Polygons(canvas));
	};

	const modalEditPolygon = () => {
		const modalPositionStyle = {
			position: 'absolute',
			top: '50%',
			left: '50%',
			transform: 'translate(-50%, -50%)',
		};

		const polygon = polygons.find(({ id }) => id === polygonId);

		const handleSave = (name = '', type = {}) => {
			const { linkChangePolygon } = connectsState;
			const url = `${linkChangePolygon}${polygon.id}/`;

			const newPolygon = { name, type: type.id };

			changePolygon(url, newPolygon, type);
		};

		const changePolygon = (url, newPolygon, type) => {
			apiMainPatch(url, newPolygon).then(({ error }) => {
				const { isError } = error;

				const seccessMsg = 'Изменение полигона - успешно';
				const errorMsg = 'Не удалось изменить полигон';

				logServerResponse(isError, seccessMsg, errorMsg);

				if (isError) return;

				const { saveDataTest, canvas, setAllPolygons } = canvasState;

				const polygons = saveDataTest[selectedCameraId][selectedPresetId];
				const polygon = polygons.find(({ id }) => id === polygonId);

				const attributes = {
					type: type.id,
					rgba: { ...type.rgba },
				};
				polygon.setAttribute(attributes);
				polygon.setName(newPolygon.name);

				setAllPolygons(selectedCameraId, selectedPresetId, polygons);
				setTempAreas(new Polygons(canvas));
				viewsState.changeUpdateData();
			});
		};

		const handleDelete = () => {
			const { linkDeletePolygon } = connectsState;
			const url = `${linkDeletePolygon}${polygon.id}/`;

			apiMainDelete(url, polygon.id).then(({ error }) => {
				const { isError } = error;

				const seccessMsg = 'Удаление полигона - успешно';
				const errorMsg = 'Не удалось удалить полигон';

				logServerResponse(isError, seccessMsg, errorMsg);

				if (isError) return;

				try {
					const { canvas, deletePolygon } = canvasState;
					const index = polygons.findIndex(({ id }) => id === polygon.id);

					deletePolygon(selectedCameraId, selectedPresetId, index);
					setTempAreas(new Polygons(canvas));
					viewsState.changeUpdateData();
				} catch (e) {
					console.error(e);
				}
			});
		};

		return (
			<ModalEditPolygon
				isShow={isModalEdit}
				setShow={setModalEdit}
				positionStyle={modalPositionStyle}
				defText={polygon.getName()}
				polygon={polygon}
				polygonTypes={polygonTypes}
				title={'Edit'}
				label={'Polygon name'}
				btnNameOk={'Save Changes'}
				handleOk={(name = '', type = '') => handleSave(name, type)}
				btnCancelStyle={{ color: '#E20000' }}
				btnNameCancel={'Delete Polygon'}
				handleCancel={handleDelete}
				errorMsg={'You cannot create a preset without a name'}
			/>
		);
	};

	return (
		<div className={styles.polygonMenu}>
			{canvasState.isShowCanvas ? (
				<>
					<div style={{ height: 'calc(100% - 74px)' }}>
						<Box className={styles.titleBox}>
							<span className={styles.title}>Tracked Zones</span>
						</Box>

						<List className={styles.list} disablePadding>
							{polygons.map((polygon, index) => {
								const { currentPolygonNum } = canvasState;

								const isActive = currentPolygonNum === index;
								const name = polygon.getName();

								const inlineListItem = {
									height: 45,

									display: 'flex',
									justifyContent: 'space-between',

									padding: '8px 16px',
								};

								const { recName } = styles;
								const color = isActive ? 'hsl(31, 100%, 60%)' : 'var(--colorText)';

								const { listItemIcon, editText, settingsItem } = styles;
								const resTextStyle = `${editText} ${isActive && styles.active}`;

								return (
									<ListItemButton
										key={uuidv4()}
										style={inlineListItem}
										className={settingsItem}
										onClick={() => handleSelectPolygon(polygon.id)}
									>
										<Typography className={recName} style={{ color }}>
											{name}
										</Typography>

										{!isCreatePolygon && (
											<ListItemIcon className={`${listItemIcon} ${styles.settings}`}>
												<span
													className={resTextStyle}
													onClick={(e) => handleClickEdit(e, polygon.id)}
												>
													edit
												</span>
											</ListItemIcon>
										)}
									</ListItemButton>
								);
							})}
						</List>
					</div>

					<div className={styles.confirmButtonsPos}>
						{isCreatePolygon ? (
							<ConfirmButtons handleSave={handleSave} handleCancel={handleCancel} />
						) : (
							<EditZonesButton />
						)}

						{isModalEdit && modalEditPolygon()}
					</div>
				</>
			) : (
				<div style={{ height: 'calc(100% - 74px)' }}>
					<EmptyMessage title={'Preset not selected'} subTitle={'Editing prohibited'} />
				</div>
			)}
		</div>
	);
});
