import React from 'react';
import portState from '../../../../store/portState';
import { observer } from 'mobx-react-lite';
import ShipItem from './ShipItem/ShipItem';
import { v4 as uuidv4 } from 'uuid';

const ShipsOnMap = observer(({ openInfoShip, viewInfoShip }) => {
	const { ships } = portState;

	const mapShip = (ship) => (
		<ShipItem key={uuidv4()} ship={ship} openInfoShip={openInfoShip} viewInfoShip={viewInfoShip} />
	);

	return ships.map((ship) => ship.isShow && mapShip(ship));
});

export default ShipsOnMap;
