import React, { useState } from 'react';
import { IconButton, Box, Slider, styled } from '@mui/material';
import { VolumeOff, VolumeUp, Replay10, VolumeDown, Forward10 } from '@mui/icons-material';
import PauseOutlinedIcon from '@mui/icons-material/PauseOutlined';
import PlayArrowIcon from '@mui/icons-material/PlayArrow';
import styles from './Controls.module.scss';
import viewsState from '../../../../../store/viewsState';
import FullscreenExitSharpIcon from '@mui/icons-material/FullscreenExitSharp';
import { observer } from 'mobx-react-lite';

const TimeSlider = styled(Slider)({
	width: '95%',
	display: 'flex',
	borderRadius: '0px',
	height: '8px',
	color: '#F07C00',
	margin: '0 auto',
	padding: '5px 0',
	marginBottom: '10px',

	'& .MuiSlider-track': {
		border: 'none',
	},

	'& .MuiSlider-rail': {
		color: 'white',
		opacity: '0.5',
	},

	'& .MuiSlider-thumb': {
		color: 'white',
	},
});

const VolumeSlider = styled(Slider)({
	width: '12px',
	height: '60px',

	color: '#FF992C',

	'& .MuiSlider-rail': {
		color: '#CFCFCF',
		opacity: '0.7',
	},

	'& .MuiSlider-thumb': {
		width: '14px',
		height: '14px',
		color: 'white',
	},
});

const Controls = observer(
	({
		playing,
		played,
		onPlayPause,
		isMuted,
		duration,
		onVolumeChange,
		volume,
		onVolumeSeekDown,
		onSeek,
		onSeekMouseDown,
		onSeekMouseUp,
		elapsedTime,
		onRewind,
		onFastForward,
		setShowModal,
	}) => {
		const { setSelectRecord } = viewsState;

		const [isVolume, setVolume] = useState(false);

		const btnStyle = (isActive) => (isActive ? buttons.icons['&:active'] : buttons.icons);

		const handleVolume = () => {
			setVolume((prev) => !prev);
		};

		const handleCloseModal = () => {
			setShowModal(false);
			setSelectRecord({name: '', url: ''});
		};

		const buttons = {
			icons: {
				fontSize: '30px',
				color: '#FFFFFF',

				'&:active': {
					color: '#FF992C',
				},

				'&:hover': {
					color: '#FF992C',
				},
			},
			time: {
				display: 'flex',
				alignItems: 'center',
				margin: '8px 0',
				fontSize: '25px',
				color: 'white',
			},
			iconsTime: {
				fontSize: '30px',
				color: '#FFFFFF',

				'&:hover': {
					color: '#FF992C',
				},
			},
		};

		return (
			<div className={styles.controls}>
				<div className={styles.controlPlay}>
					<div className={styles.timeContrl}>
						<IconButton sx={buttons.iconsTime} onClick={onRewind}>
							<Replay10 />
						</IconButton>
						<IconButton sx={buttons.iconsTime} onClick={onPlayPause}>
							{playing ? <PauseOutlinedIcon /> : <PlayArrowIcon />}
						</IconButton>
						<IconButton sx={buttons.iconsTime} onClick={onFastForward}>
							<Forward10 />
						</IconButton>
						<Box sx={buttons.time}>
							{duration} / {elapsedTime}
						</Box>
					</div>

					<div>
						<IconButton sx={btnStyle(isVolume)} onClick={handleVolume}>
							{isVolume && (
								<div className={styles.sliderVolume}>
									<VolumeSlider
										orientation='vertical'
										value={volume * 100}
										onChange={onVolumeChange}
										onChangeCommitted={onVolumeSeekDown}
									/>
								</div>
							)}

							{isMuted ? <VolumeOff /> : volume > 0.5 ? <VolumeUp /> : <VolumeDown />}
						</IconButton>

						<IconButton sx={buttons.icons} onClick={handleCloseModal}>
							<FullscreenExitSharpIcon />
						</IconButton>
					</div>
				</div>

				<TimeSlider
					min={0}
					max={100}
					value={played * 100}
					onChange={onSeek}
					onMouseDown={onSeekMouseDown}
					onChangeCommitted={onSeekMouseUp}
				/>
			</div>
		);
	}
);

export default Controls;
