import React, { useEffect, useState } from 'react';
import styles from './Account.module.scss';
import Header from '../Header/Header';
import AccountCards from './Items/AccountCards/AccountCards';
import PdfFile from './Items/Card/PdfFile/PdfFile';
import { ClickAwayListener, IconButton } from '@mui/material';
import ModalAccount from './Items/ModalAccount/ModalAccount';
import connectsState from '../../store/connectsState';
import viewsState from '../../store/viewsState';
import { observer } from 'mobx-react-lite';
import CloseIcon from '../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/CloseIcon';
import { prepareCamerasFullData } from '../../api/prepareData/prepareCamerasData';
import { apiMainGet } from '../../api/mainApi';

const Account = observer(() => {
	const { cameras } = viewsState;
	const { linkCamerasList } = connectsState;

	const [isPdf, setPdf] = useState(false);
	const [isModal, setModal] = useState(false);
	const [indexTitle, setIndexTitle] = useState(null);
	const [itemId, setItemId] = useState(null);

	useEffect(() => {
		if (!linkCamerasList) return;

		const url = linkCamerasList;
		const controller = new AbortController();

		getCameras(url, controller);

		return () => {
			controller.abort();
		};
	}, [linkCamerasList]);

	const getCameras = (url, controller) => {
		apiMainGet(url, controller).then(({ error, data }) => {
			if (error.isError) {
				viewsState.setMonitorCameras([]);
				return;
			}

			const { setMonitorCameras } = viewsState;
			setMonitorCameras(prepareCamerasFullData(data));
		});
	};

	const openModal = (index = null, id = null) => {
		setModal(true);
		setIndexTitle(index);
		setItemId(id);
	};

	const closeModal = () => {
		setModal(false);
	};
	const addDevice = () => {
		setModal(false);
	};

	const handleShowPdf = () => {
		setPdf((prevShowPdf) => !prevShowPdf);
	};
	const anotherAdd = () => {};

	return (
		<div className={styles.parent}>
			<Header />

			<AccountCards showPdf={handleShowPdf} openModalAdd={openModal} cameras={cameras} anotherAdd={anotherAdd} />
			{isPdf && (
				<ClickAwayListener onClickAway={handleShowPdf}>
					<div className={styles.pdf}>
						<IconButton className={styles.pdfBtn} sx={{ color: '#FF992C' }} onClick={handleShowPdf}>
							<CloseIcon width={45} height={45} color={'#FF992C'} />
						</IconButton>
						<PdfFile />
					</div>
				</ClickAwayListener>
			)}
			<ModalAccount
				id={itemId}
				indexTitle={indexTitle}
				isModal={isModal}
				handleModal={closeModal}
				handleClose={addDevice}
			/>
		</div>
	);
});

export default Account;
