import React from 'react';
import styles from './ItemArchive.module.scss';
import viewsState from '../../../../store/viewsState';
import offCheck from '../../../../assets/portsMenu/checkboxOff.png';
import onCheck from '../../../../assets/portsMenu/checkboxOn.png';
import { observer } from 'mobx-react-lite';

const ItemArchive = observer(({ record, setShowModal }) => {
	const { setSelectRecord, setRemovedItemsArchive } = viewsState;

	const {id, camName, name, path, date, img, duration, isDelete} = record;

	const image = isDelete ? onCheck : offCheck;

	const handleChangeCamera = () => {
		setShowModal(true);
		setSelectRecord({name: camName, url: path});
	};
	
	const selectRecord = () => {
		setRemovedItemsArchive(id);
	};

	return (
		<div className={isDelete ? styles.itemFull : styles.item}>
			<div className={styles.block}>
				<img className={styles.iconCheck} src={image} onClick={selectRecord} />
				<div className={styles.nameItem}>{camName}</div>
				<img className={styles.player} src={img} onClick={handleChangeCamera} />

				<div className={styles.lowerInfo}>
					<div className={styles.info}>
						<div>{name}</div>
						<div>{date}</div>
					</div>
					<div>{duration}</div>
				</div>
			</div>
		</div>
	);
});

export default ItemArchive;
