import React from 'react';
import { Box } from '@mui/material';

export const IconVideo = ({ width = 24, height = 14, color = '#656565' }) => {
	return (
		<Box>
			<svg width={width} height={height} viewBox="0 0 24 16" fill="none" xmlns="http://www.w3.org/2000/svg">
				<path d="M23 3L16 8L23 13V3Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
				<path
					d="M14 1L3 1C1.89543 1 1 1.89543 1 3L1 13C1 14.1046 1.89543 15 3 15L14 15C15.1046 15 16 14.1046 16 13V3C16 1.89543 15.1046 1 14 1Z"
					stroke={color}
					strokeWidth="2"
					strokeLinecap="round"
					strokeLinejoin="round"
				/>
			</svg>
		</Box>
	);
};
