import React, { useEffect, useState } from 'react';
import styles from './EditPointsPanel.module.scss';
import { Box } from '@mui/material';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import pen from '../../../../../assets/portsMenu/pen.svg';
import TuneIcon from '@mui/icons-material/Tune';
import { transformCoordsToDMS, transformCoordsToDecimal } from '../../../../../helpers/helpers';

const defaultData = {
	isEditView: false,
	coords: {
		true: {
			lat: 0,
			lng: 0,
		},
		false: {
			lat: 0,
			lng: 0,
		},
	},
};

const EditPointsPanel = ({
	data = defaultData,
	actions = {
		save: () => {},
		change: () => {},
		changeFormat: () => {},
		setEditView: () => {},
	},
}) => {
	const { isEditView, coords } = data;
	const { setEditView, changeFormat } = actions;

	const [isViewFormatTime, setViewFormatTime] = useState(false);
	const [coordsTimeLat, setCoordsTimeLat] = useState({ deg: 0, min: 0, sec: 0 });
	const [coordsTimeLng, setCoordsTimeLng] = useState({ deg: 0, min: 0, sec: 0 });
	
	const btnLat = 'N';
	const btnLng = 'E';

	useEffect(() => {
		const { lat, lng } = coords[isEditView];

		if (isViewFormatTime) {
			setCoordsTimeLat(transformCoordsToDMS(lat));
			setCoordsTimeLng(transformCoordsToDMS(lng));
		} else {
			const point = {
				lat: transformCoordsToDecimal(coordsTimeLat),
				lng: transformCoordsToDecimal(coordsTimeLng),
			};
			changeFormat(point);
		}
	}, [isViewFormatTime]);

	const closeEditPoints = () => {
		setEditView(false);
		setViewFormatTime(false);
	};

	const visibleFormat = () => {
		setViewFormatTime((prev) => !prev);
	};

	const saveFormat = () => {
		if (isViewFormatTime) {
			const point = {
				lat: transformCoordsToDecimal(coordsTimeLat),
				lng: transformCoordsToDecimal(coordsTimeLng),
			};
			changeFormat(point, isViewFormatTime);
		} else {
			actions.save();
		}
	};

	const coordsLatChange = (e) => {
		const { name, value } = e.target;

		setCoordsTimeLat((prevCoords) => ({
			...prevCoords,
			[name]: value,
		}));
	};

	const coordsLngChange = (e) => {
		const { name, value } = e.target;

		setCoordsTimeLng((prevCoords) => ({
			...prevCoords,
			[name]: value,
		}));
	};

	const editCoords = () => {
		const { lat, lng } = coords[isEditView];

		return (
			<div className={`${styles.coords} ${isEditView && styles.edit} ${isViewFormatTime && styles.full}`}>
				<TuneIcon cursor='pointer' onClick={visibleFormat} />

				{isViewFormatTime ? (
					<>
						<div style={{ display: 'flex', alignItems: 'center' }}>
							Lat:
							<div>
								<input
									maxLength={2}
									name='deg'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLat['deg']}
									onChange={coordsLatChange}
								/>
								°
							</div>
							<div>
								<input
									maxLength={2}
									name='min'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLat['min']}
									onChange={coordsLatChange}
								/>
								&apos;
							</div>
							<div>
								<input
									maxLength={2}
									name='sec'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLat['sec']}
									onChange={coordsLatChange}
								/>
								&quot;
							</div>
							<button className={styles.btnToggle}>
								{btnLat}
							</button>
						</div>

						<div style={{ display: 'flex', alignItems: 'center' }}>
							Lng:
							<div>
								<input
									maxLength={2}
									name='deg'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLng['deg']}
									onChange={coordsLngChange}
								/>
								°
							</div>
							<div>
								<input
									maxLength={2}
									name='min'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLng['min']}
									onChange={coordsLngChange}
								/>
								&apos;
							</div>
							<div>
								<input
									maxLength={2}
									name='sec'
									className={`${styles.inputCoords} ${styles.formatTime}`}
									value={coordsTimeLng['sec']}
									onChange={coordsLngChange}
								/>
								&quot;
							</div>
							<button className={styles.btnToggle}>
								{btnLng}
							</button>
						</div>
					</>
				) : (
					<>
						<div>
							Lat:
							<input
								maxLength={8}
								name='lat'
								className={styles.inputCoords}
								value={lat}
								onChange={actions.change}
							/>
						</div>

						<div>
							Lng:
							<input
								maxLength={8}
								name='lng'
								className={styles.inputCoords}
								value={lng}
								onChange={actions.change}
							/>
						</div>
					</>
				)}

				<div>
					<CheckIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={saveFormat} />
					<CloseIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={closeEditPoints} />
				</div>
			</div>
		);
	};

	const visibleCoords = () => {
		const { lat, lng } = coords[isEditView];
		const { setEditView } = actions;

		return (
			<div className={styles.coords}>
				<Box sx={{ display: 'flex' }}>
					Lat:
					<div className={styles.pointsLabel}>{lat}</div>
				</Box>
				<Box sx={{ display: 'flex' }}>
					Lng:
					<div className={styles.pointsLabel}>{lng}</div>
				</Box>

				<img className={styles.pen} src={pen} onClick={() => setEditView(true)} />
			</div>
		);
	};

	return <>{isEditView ? editCoords() : visibleCoords()}</>;
};

export default EditPointsPanel;
