import React from 'react';
import { useState } from 'react';
import { useHistory } from 'react-router-dom';
import styles from './LowerMenu.module.scss';
import FolderOutlinedIcon from '@mui/icons-material/FolderOutlined';
import SelectCamerasView from './SelectCamerasView/SelectCamerasView';
import { cardsSelect } from '../data';
import ZoomOutMapOutlinedIcon from '@mui/icons-material/ZoomOutMapOutlined';
import VideoButtons from './VideoButtons/VideoButtons';
import { observer } from 'mobx-react-lite';
import {IconVideo} from '../../Records/LowerMenu/IconsLowerMenu/IconVideo';

const LowerMenu = observer(
	({
		handleViews,
		showWindows,
		lastPage,
		fullScreen,
		currentPage,
		changePage = () => {},
	}) => {
		const history = useHistory();

		const buttonActiveColor = '#FFFFFF';
		const buttonColor = '#CFCFCF';

		const [styleColor, setStyleColor] = useState(buttonColor);
		const [isShowSettings, setShowSettings] = useState(false);
		const [selectedIconId, setSelectedIconId] = useState(0);

		const handleClose = () => {
			setShowSettings(false);
		};
		const handleOpen = () => {
			setShowSettings(true);
		};
		const handleEnter = () => {
			setStyleColor(buttonActiveColor);
		};
		const handleLeave = () => {
			setStyleColor(buttonColor);
		};

		const changeTranslation = () => {
			history.push('/views/records');
		};

		const changePageAndFull = () => {
			return (
				<div className={styles.changePageAndFull}>
					<div className={styles.btnsCenter}>
						<VideoButtons
							currentPage={currentPage}
							handleChangePage={(n = null) => changePage(n)}
							lastPage={lastPage}
							fullScreen={fullScreen}
						/>
					</div>
					<div className={styles.rightContainer}>
						<button className={styles.arrowsIconBtn} onClick={fullScreen}>
							<ZoomOutMapOutlinedIcon sx={{ width: '32px', height: '32px' }} />
						</button>
					</div>
				</div>
			);
		};

		return (
			<div className={styles.parent}>
				{isShowSettings && (
					<SelectCamerasView
						close={handleClose}
						handleViews={handleViews}
						showWindows={showWindows}
						setSelectedIconId={setSelectedIconId}
						selectIconId={selectedIconId}
					/>
				)}

				<div className={styles.leftContainer}>
					<div
						className={isShowSettings ? styles.btnIconsActive : styles.btnIcons}
						onClick={handleOpen}
						onMouseEnter={handleEnter}
						onMouseLeave={handleLeave}
					>
						{cardsSelect[selectedIconId].icon(isShowSettings ? buttonActiveColor : styleColor)}
					</div>

					<div className={styles.stylesButtonActive}>
						<IconVideo color={'#FF992C'}/>

						<div>BROADCASTS</div>
					</div>
					<div className={styles.stylesButtonArch} onClick={changeTranslation}>
						<FolderOutlinedIcon />

						<div>ARCHIVE</div>
					</div>
				</div>

				{changePageAndFull()}
			</div>
		);
	}
);

export default LowerMenu;
