import React, { useState } from 'react';
import styles from './AllSelectedOnMap.module.scss';
import offCheck from '../../../../../../assets/portsMenu/checkboxOff.png';
import onCheck from '../../../../../../assets/portsMenu/checkboxOn.png';
import { checkAllSelected } from '../../../../../../helpers/helpers.js';

const AllSelectedOnMap = ({ title, items , action = () => {} }) => {
	const [isCheck, setCheck] = useState(checkAllSelected(items));

	const imageCheck = isCheck ? onCheck : offCheck;

	const handleClick = () => {
		setCheck((prevCheck) => {
			const check = !prevCheck;
			action(check);

			return check;
		});
	};

	return (
		<div className={styles.itemBox} onClick={handleClick}>
			{title}
			<img className={styles.iconCheck} src={imageCheck} />
		</div>
	);
};

export default AllSelectedOnMap;
