import React from 'react';
import styles from './DrawerMenu.module.scss';
import portState from '../../../../store/portState';
import viewsState from '../../../../store/viewsState';
import radarsState from '../../../../store/radarsState';
import zonesState from '../../../../store/zonesState';
import shipIcon from '../../../../assets/portsMenu/ships.png';
import cameraIcon from '../../../../assets/portsMenu/camera.png';
import radarIcon from '../../../../assets/portsMenu/radar.png';
import aisIcon from '../../../../assets/portsMenu/ais.png';
import zonesIcon from '../../../../assets/portsMenu/zones.png';
import viewsIcon from '../../../../assets/portsMenu/views.png';
import infoIcon from '../../../../assets/portsMenu/info.png';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const DrawerMenu = observer(() => {
	const { isViewAll, activeMenu, setActiveMenu, setViewAll } = portState;
	const { setAllCameras } = viewsState;
	const { setAllRadars, setAllAises } = radarsState;
	const { setAllZones, setAllSectors, setEditZone, setEditSector } = zonesState;

	const buttons = [
		{ id: 0, name: 'ships', icon: shipIcon, alt: 'Ship Icon', width: 38, height: 41 },
		{ id: 1, name: 'cameras', icon: cameraIcon, alt: 'Camera Icon', width: 42, height: 36 },
		{ id: 2, name: 'radars', icon: radarIcon, alt: 'Radar Icon', width: 42, height: 42 },
		{ id: 3, name: 'ais', icon: aisIcon, alt: 'Ais Icon', width: 36, height: 33 },
		{ id: 4, name: 'zones', icon: zonesIcon, alt: 'Zones Icon', width: 42, height: 42 },
		{ id: 5, name: 'info', icon: infoIcon, alt: 'Info Icon', width: 13, height: 30 },
	];

	const handleActive = (nameMenu = '') => {
		setActiveMenu(activeMenu !== nameMenu ? nameMenu : '');
		setEditSector({});
		setEditZone({});
	};

	const showAllItems = () => {
		setViewAll();
		setAllCameras(!isViewAll);
		setAllRadars(!isViewAll);
		setAllAises(!isViewAll);
		setAllZones(!isViewAll);
		setAllSectors(!isViewAll);
	};

	return (
		<div className={`${styles.btnPos} ${styles.right}`}>
			<div className={`${styles.itemIcon} ${isViewAll && styles.isViewAll}`} onClick={showAllItems}>
				<img style={{ width: 41, height: 26 }} src={viewsIcon} />
			</div>

			{buttons.map(({ name, icon, alt, width, height }) => {
				const isActive = activeMenu === name;
				const stylePostfix = isActive && styles.active;
				const resultStyle = `${styles.itemIcon} ${stylePostfix}`;

				return (
					<div key={uuidv4()} className={resultStyle} onClick={() => handleActive(name.toLowerCase())}>
						<img src={icon} alt={alt} style={{ width, height }} />
					</div>
				);
			})}
		</div>
	);
});

export default DrawerMenu;
