import React from 'react';
import styles from './ModalConfirm.module.scss';
import { Modal } from '@mui/material';
import viewsState from '../../../../store/viewsState';
import { observer } from 'mobx-react-lite';

const ModalConfirm = observer(({handleModal, deleteRecords}) => {
	const { isConfirmDeleteRecords } = viewsState;

	return (
		<Modal
			open={isConfirmDeleteRecords}
			closeAfterTransition
			Backdrop={{ timeout: 500 }}
			onClose={handleModal}
		>
			<div className={styles.container}>
				<div className={styles.header}>
					Are you sure you want to delete entries?
				</div>

				<div className={styles.buttons}>
					<div className={styles.cancel} onClick={handleModal}>Cancel</div>
					<div className={styles.delete} onClick={deleteRecords}>Delete</div>
				</div>
			</div>
		</Modal>
	);
});

export default ModalConfirm;