import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import { NavLink } from 'react-router-dom';
import { observer } from 'mobx-react-lite';
import { MobileDrawer } from '../Port/MobileDrawer/MobileDrawer';
import { headerHeight } from '../../globalStyles/vars.module.scss';
import Logo from './Images/logo';
import DarkLogo from './Images/darkLogo';
import { useThemeTwo } from '../../Theme/useTheme';
import HeaderNotifications from './HeaderNotifications/HeaderNotifications';
import Avatar from '../Account/data/Avatar';
import notificationsState from '../../store/notificationsState';
import styles from './Header.module.scss';
import { selectSound } from '../../helpers/helpers';
import connectsState from '../../store/connectsState';
import regSound from '../../assets/sounds/preryivistyiy-zvuk.wav';
import useSound from 'use-sound';

const EVENT_TYPE_NOTIFICATION = 'notification';

const useStyles = makeStyles((theme) => {
	return {
		test: {
			height: `${headerHeight}`,

			justifyContent: 'center',
			flexShrink: 0,

			position: 'fixed',
			top: 0,
			left: 'auto',
			right: 0,
			zIndex: 1100,

			backgroundColor: 'var(--bgTheme)',
			boxShadow: '0px 0px 0px 4px var(--bgThemeBorder)',
		},
		grow: {
			flexGrow: 1,
		},
		menuButton: {
			marginRight: theme.spacing(2),
		},
		accountIcon: {
			width: 48,
			height: 48,
			borderRadius: '50%',

			backgroundImage: `url(${Avatar})`,
			backgroundPosition: 'center',
			backgroundSize: 'cover',
			backgroundRepeat: 'no-repeat',
		},
		btnHome: {
			display: 'flex',

			fontSize: 16,
			fontWeight: 300,
			margin: 0,
			textDecoration: 'none',
			color: 'var(--colorText)',

			fontFamily: 'HalvarBreit',

			'&.active': {
				fontWeight: 600,
				color: '#FF992C',
			},
			'&:hover': {
				fontWeight: 600,
				color: '#FF992C',
			},
		},
		mobileDrawer: {
			display: 'none',

			[theme.breakpoints.down('sm')]: {
				display: 'flex',

				marginRight: -30,
				paddingLeft: 5,
			},
		},
	};
});

export default observer(({ mobileDrawer }) => {
	const theme = useThemeTwo();
	const classes = useStyles();

	const { events, newNotifications } = notificationsState;
	const { wsSound } = connectsState;

	const [notif, setNotif] = useState(0);
	const [newNotifEvents, setNewNotifEvents] = useState([]);
	const [sound, setSound] = useState(regSound);

	const [play] = useSound(sound);

	const playSound = (type = '') => {
		if (!type) return;

		const sound = selectSound(type);
		setSound(sound);
		play();
	};

	useEffect(() => {
		if (wsSound) getMessageFromSocket();

		setNotif(notificationsState.newNotifications);
		playSound(events[0]?.type_event);
	}, [newNotifEvents, wsSound, newNotifications, events]);

	const getMessageFromSocket = () => {
		wsSound.onmessage = (event) => {
			const response = JSON.parse(event.data);
			const editResponse = response.message.replace(/\\/g, '');
			const resultResponse = JSON.parse(editResponse);

			const { event_socket_type: type, event: eventSocket } = resultResponse;

			switch (type) {
			case EVENT_TYPE_NOTIFICATION: {
				const editEvent = {
					...eventSocket,
					id: notificationsState.getNotifId(),
					isNew: true,
				};

				setNewNotifEvents([editEvent, ...newNotifEvents]);
				notificationsState.addEvent(editEvent);
				break;
			}

			default:
				return;
			}
		};
	};

	return (
		<AppBar position="static" classes={{ root: classes.test }}>
			<Toolbar className={styles.toolBarStyle}>
				<NavLink className={classes.btnHome} to="/port">
					{theme === 'dark-mode' ? <DarkLogo /> : <Logo />}
				</NavLink>

				<div className={styles.linkBox}>
					<NavLink className={classes.btnHome} to="/port">
						Map
					</NavLink>
					<div className={styles.btnLinkHeader} />

					<NavLink className={classes.btnHome} to="/monitor">
						Monitor
					</NavLink>
					<div className={styles.btnLinkHeader} />

					<NavLink className={classes.btnHome} to="/views">
						Broadcast
					</NavLink>
					<div className={styles.btnLinkHeader} />

					<NavLink className={classes.btnHome} to="/archive/cards">
						Events
					</NavLink>
				</div>

				<div className={styles.leftItemsContainer}>
					<HeaderNotifications events={events} newNotifications={notif} />

					<NavLink className={styles.NavLinkItem} to="/account">
						<IconButton
							edge="end"
							aria-label="account of current user"
							aria-controls="primary-search-account-menu"
							aria-haspopup="true"
							color="inherit"
						>
							<Avatar />
						</IconButton>

						<div className={styles.btnIconStyle}>Service Soft</div>
					</NavLink>
				</div>

				<span className={classes.mobileDrawer}>
					<MobileDrawer drawer={mobileDrawer} />
				</span>
			</Toolbar>
		</AppBar>
	);
});
