import React from 'react';

export default ({ width = 11, height = 6, color = '#5F5F5F' }) => {
	return (
		<svg width={width} height={height} viewBox="0 0 11 6" fill="none" xmlns="http://www.w3.org/2000/svg">
			<path
				d="M1.47852 1L5.47852 5L9.47852 1"
				stroke={color}
				strokeWidth="1.5"
				strokeLinecap="round"
				strokeLinejoin="round"
			/>
		</svg>
	);
};
