import React, {useEffect, useState} from 'react';
import styles from './Card.module.scss';
import ControlIcons from '../ControlIcons/ControlIcons';
import TableCard from '../TableCard/TableCard';
import Chart from './../Charts/Chart';
import { observer } from 'mobx-react-lite';
import eventArchiveState from '../../../../store/eventArchiveState';
import { columns, nameCards } from '../../data';

const Card = observer(({ data, action = () => {} }) => {
	const { number, rows, userFilter, availableDevices, modDevices } = data;
	const [isTable, setTable] = useState(true);
	const [currentPage, setCurrentPage] = useState(1);

	const cardRows = rows.filter(row => userFilter.includes(row.deviceName));

	const rowsPerPage = 5;
	const lastRowIndex = currentPage * rowsPerPage;
	const currentRows = cardRows.slice(0, lastRowIndex);

	useEffect(() => {
		rows.forEach((row) => {
			const { deviceName } = row;
			const isDeviceExist = availableDevices.indexOf(deviceName) > -1;

			if (!isDeviceExist) availableDevices.push(deviceName);
		});

		eventArchiveState.setAvailableDevices(availableDevices, number);
		
		if (!modDevices) eventArchiveState.setSelectedDevices(availableDevices, number);
	}, [rows]);

	const handleChangeTable = () => {
		setTable(prevIsTable => !prevIsTable );
	};

	const addPage = () => {
		setCurrentPage(prevPage => prevPage + 1);
	};

	const handleScroll = (event) => {
		const { scrollHeight, scrollTop, clientHeight } = event.currentTarget;
		const scrollBottom = scrollHeight - scrollTop - clientHeight;

		if (scrollBottom < 50) addPage();
	};

	const table = {
		columns: columns[number],
		rows: currentRows,
		numberCard: number,
	};

	return (
		<div className={styles.card}>
			<div className={styles.head}>
				<p className={styles.nameCard}>{nameCards[number].name}</p>
				<div className={styles.icons}>
					<ControlIcons
						transferDataModal={() => action(number)}
						changeTable={handleChangeTable}
						isTable={isTable}
						numberCard={number}
					/>
				</div>
			</div>
			<div className={styles.wrapper} onScroll={handleScroll}>
				{rows.length
					? isTable
						? <TableCard isLargeTable={false} table={table} />
						: <Chart typeChart={nameCards[number].typeChart}/>
					: <p className={styles.noData}>No events</p>
				}
			</div>
		</div>
	);
});

export default Card;