import React, { useEffect, useState } from 'react';
import Header from '../../components/Header/Header';
import styles from './MapPage.module.scss';
import { Box } from '@mui/material';
import PortMenu from '../../components/Port/PortsMenu/PortMenu';
import Map from '../../components/Port/Map/Map.js';
import portState from '../../store/portState';
import zonesState from '../../store/zonesState';
import connectsState from '../../store/connectsState';
import viewsState from '../../store/viewsState';
import { observer } from 'mobx-react-lite';
import FullMap from '../../components/Port/PortsMenu/FullMap/FullMap.js';
import TimeSelectedPath from '../../components/Port/TimeSelectedPath/TimeSelectedPath';
import ModalChoice from '../../components/Port/PortsMenu/ModalChoice/ModalChoice';
import { checkNegativeServerAns, logServerResponse } from '../../helpers/helpers';
import { prepareCamerasFullData } from '../../api/prepareData/prepareCamerasData';
import { prepareZonesData } from '../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../api/prepareData/prepareSectorsData';
import { apiMainGet } from '../../api/mainApi';
import ErrorMessage from '../../helpers/ErrorMessage/ErrorMessage';

const FullMapContainer = observer(({ children }) => {
	const { isCreateZone, isCreateSector } = portState;
	const isShowFullMap = isCreateZone || isCreateSector;

	return isShowFullMap ? <FullMap /> : children;
});

const MapPage = observer(() => {
	const { activeMenu, setChangeShips } = portState;

	const { setZones, setSectors, setSectorsTypes, setZoneTypes } = zonesState;

	const { linkZones, linkTypesSector, linkCamerasList, linkTypesZone, linkFullSectors, wsShipsEvents } = connectsState;

	const [isModal, setIsModal] = useState(false);
	const [isReconnect, setIsReconnect] = useState(false);

	const isViewCalendar = activeMenu === 'ships';

	const oneSec = 1000;
	const wsReconDelay = 2 * oneSec;

	useEffect(() => {
		if (!linkZones) return;

		const controller = new AbortController();

		getZones(linkZones, controller);
		getFullSectors(linkFullSectors, controller);
		getSectorTypes(linkTypesSector, controller);
		getZoneTypes(linkTypesZone, controller);
		getCameras(linkCamerasList, controller);

		return () => {
			controller.abort();
		};
	}, [linkZones]);

	useEffect(() => {
		let socket = '';
		const intervalId = setInterval(() => {
			try {
				socket = new WebSocket(wsShipsEvents);

				socket.onmessage = (event) => getShipsFromSocket(event);
				socket.onerror = (e) => socketError(e);
				socket.onclose = (e) => socketClose(e);

				clearInterval(intervalId);
			} catch (error) {
				console.error(error);
			}
		}, wsReconDelay);

		return () => {
			try {
				socket.close();
			} catch (error) {
				console.error(error);
			}
			clearInterval(intervalId);
		};
	}, [isReconnect]);

	const getShipsFromSocket = (event) => {
		const response = JSON.parse(event.data);

		setChangeShips(response.data);
	};

	const socketError = (error) => {
		console.error(error.message);
	};

	const socketClose = (event) => {
		if (event.wasClean) return;

		setIsReconnect((prevIsBreak) => !prevIsBreak);
	};

	const getCameras = (url, controller) => {
		apiMainGet(url, controller).then(({ error, data }) => {
			if (error.isError) {
				viewsState.setMonitorCameras([]);
				return;
			}

			const { setMonitorCameras } = viewsState;
			setMonitorCameras(prepareCamerasFullData(data));
		});
	};

	const getZones = (url = '', controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '', controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const getSectorTypes = (url = '', controller) => {
		apiMainGet(url, controller).then(({ ans, data }) => {
			const isNegativeAns = checkNegativeServerAns(ans.status);
			if (isNegativeAns) return;

			setSectorsTypes(data);
		});
	};
	const getZoneTypes = (url = '', controller) => {
		apiMainGet(url, controller).then(({ ans, data }) => {
			const isNegativeAns = checkNegativeServerAns(ans.status);
			if (isNegativeAns) return;

			setZoneTypes(data);
		});
	};

	return (
		<div className={styles.parent}>
			<FullMapContainer>
				<Header />

				<Box className={styles.container}>
					<Map />

					<div className={styles.errorMessagePosition}>
						<ErrorMessage title='Warning'>
							<span>To display ships, connect to the AIS system or </span>
							<a
								className={styles.goToLookOut}
								href='https://lookout.ss24h.com'
								target='_blank'
								rel='noopener noreferrer'
							>
								LookOut
							</a>
						</ErrorMessage>
					</div>
				</Box>

				<Box className={`${styles.menu} ${styles.right}`}>
					<PortMenu setShowModal={setIsModal} />
				</Box>

				{isViewCalendar && <TimeSelectedPath />}
			</FullMapContainer>

			<ModalChoice isModal={isModal} setShowModal={setIsModal} />
		</div>
	);
});

export default MapPage;
