import React from 'react';
import styles from './VideoButtons.module.scss';
import NavigateBeforeOutlinedIcon from '@mui/icons-material/NavigateBeforeOutlined';
import NavigateNextOutlinedIcon from '@mui/icons-material/NavigateNextOutlined';

const VideoButtons = ({
	currentPage,
	lastPage,
	handleChangePage = () => {}
}) => {
	const inlineStyleButton = { width: '32px', height: '32px' };

	return (
		<div className={styles.centerContainer}>
			<button
				className={
					(currentPage === 1)
						? styles.arrowsIconBtnDisabled
						: styles.arrowsIconBtn
				}
				disabled={currentPage === 1}
				onClick={() => handleChangePage(-1)}
			>
				<NavigateBeforeOutlinedIcon style={inlineStyleButton} />
			</button>

			<div
				className={styles.textPage}
			>
				{currentPage}/{lastPage}
			</div>
			<button
				className={
					(currentPage === lastPage)
						? styles.arrowsIconBtnDisabled
						: styles.arrowsIconBtn
				}
				disabled={currentPage === lastPage}
				onClick={() => handleChangePage(1)}
			>
				<NavigateNextOutlinedIcon style={inlineStyleButton} />
			</button>
		</div>
	);
};

export default VideoButtons;