import React, { useState } from 'react';
import Box from '@mui/material/Box';
import styles from './TableCard.module.scss';
import cardState from '../../../../store/cardState';
import AudioPlayer from 'react-modular-audio-player';
import connectsState from '../../../../store/connectsState';
import { IconButton, Modal } from '@mui/material';
import CloseIcon from '../../../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/CloseIcon';
import { v4 as uuidv4 } from 'uuid';

const rearrangedPlayer = [
	{
		className: 'player',
		innerComponents: [
			{
				type: 'play',
				style: {
					justifyContent: 'center',
					filter: 'invert(100%)',
					background: '#0a81ff',
					borderRadius: '50%',
				},
			},
		],
	},
];

const TableCard = ({ table, isLargeTable = true, action = () => {} }) => {
	const [selectedRow, setSelectedRow] = useState(null);
	const [isOpen, setOpen] = useState(false);
	const [imgPath, setImgRath] = useState(null);
	const { wsArchiveEvents } = connectsState;

	const { columns, rows, numberCard } = table;
	const tableStyle = `${styles.table} ${isLargeTable ? styles.large : styles.small}`;
	const dataForTable01 = [];
	const dataForTable02 = [];
	const dataForTable03 = [];
	const dataForTable04 = [];
	const dataForTable05 = [];
	const dataForTable06 = [];

	const dataEventForMap = (points, ship, idSelectedRow) => {
		action({ points, ship });
		setSelectedRow(idSelectedRow);
	};

	const dataEventForReport = (report, idSelectedRow) => {
		action(report);
		setSelectedRow(idSelectedRow);
	};

	const RowColumn = ({ topCell = '', bottomCell = '' }) => {
		return (
			<td className={styles.bodyBig}>
				<p>{topCell}</p>
				<p>{bottomCell}</p>
			</td>
		);
	};

	const SoundAndImage = ({ type, image, sound }) => {
		const resource = `http${wsArchiveEvents.slice(2, 23)}`;

		const handleClickImg = () => {
			setOpen(true);
			setImgRath(`${resource}${image}`);
		};

		const selectSoundOrImage = {
			0: (
				<td onClick={handleClickImg}>
					<img
						className={isLargeTable ? styles.imgLargeTable : styles.imgSmallTable}
						src={`${resource}${image}`}
						alt="Ship photo"
					/>
				</td>
			),
			1: (
				<td>
					<div className={styles.audioPlayer}>
						<AudioPlayer
							audioFiles={[{ src: `${resource}${sound}` }]}
							rearrange={rearrangedPlayer}
							playerWidth="30px"
							iconSize="30px"
						/>
					</div>
				</td>
			),
			2: <></>,
		};

		return selectSoundOrImage[type];
	};

	rows.forEach((row) => {
		const {
			id,
			sector,
			deviceName,
			status,
			zoneName,
			mooringName,
			shipName,
			shipClass,
			eventType,
			time,
			vesselDirection,
			imo,
			mmsi,
			speed,
			sound,
			image,
			latitude,
			longitude,
			state,
			value,
			zonePoints,
			sectorPoints,
		} = row;

		const points = { zonePoints, sectorPoints };

		dataForTable01.push({
			id,
			firstTop: `${sector}, ${zoneName}`,
			firstBottom: deviceName,
			secondTop: eventType,
			thirdTop: shipClass,
			thirdBottom: `imo: ${imo}, mmsi: ${mmsi}`,
			time,
			shipClass,
			shipName,
			latitude,
			longitude,
			vesselDirection,
			points,
			speed,
			type: 2,
		});

		dataForTable02.push({
			id,
			firstTop: zoneName,
			firstBottom: deviceName,
			secondTop: eventType,
			thirdTop: shipClass,
			thirdBottom: imo,
			time,
			shipClass,
			shipName,
			latitude,
			longitude,
			vesselDirection,
			points,
			image,
			type: 0,
		});

		dataForTable03.push({
			id,
			firstTop: zoneName,
			firstBottom: mooringName,
			secondTop: status,
			thirdTop: shipClass,
			thirdBottom: imo,
			time,
			shipClass,
			shipName,
			latitude,
			longitude,
			vesselDirection,
			points,
			image,
			type: 0,
		});

		dataForTable04.push({
			id,
			firstTop: `${sector}, ${zoneName}`,
			firstBottom: deviceName,
			secondTop: eventType,
			thirdTop: shipClass,
			thirdBottom: `imo: ${imo}, mmsi: ${mmsi}`,
			time,
			shipClass,
			shipName,
			latitude,
			longitude,
			vesselDirection,
			points,
			speed,
			type: 2,
		});

		dataForTable05.push({
			id,
			firstTop: deviceName,
			secondTop: 'Sound',
			thirdTop: time,
			sound,
			type: 1,
		});

		dataForTable06.push({
			id,
			firstTop: deviceName,
			secondTop: state,
			thirdTop: value,
			time,
			type: 2,
		});
	});

	const resultTable = {
		0: dataForTable01,
		1: dataForTable02,
		2: dataForTable03,
		3: dataForTable04,
		4: dataForTable05,
		5: dataForTable06,
	};

	return (
		<div>
			<Modal open={isOpen} onClose={() => setOpen(false)}>
				<div>
					<div className={styles.closeBtn}>
						<IconButton onClick={() => setOpen(false)}>
							<CloseIcon width={45} height={45} color={'#FF992C'} />
						</IconButton>
					</div>

					<img className={styles.fullImg} src={imgPath} alt="Ship photo" />
				</div>
			</Modal>

			<Box className={styles.tableBox}>
				<table className={tableStyle}>
					<thead>
						<tr>
							{columns.map((column) => (
								<th key={uuidv4()} className={styles.headFont}>
									<p className={styles.bodyBig}>{column.headerName}</p>
								</th>
							))}
						</tr>
					</thead>
					<tbody>
						{resultTable[numberCard].map((row) => {
							const {
								firstTop,
								firstBottom,
								secondTop,
								thirdTop,
								thirdBottom,
								time,
								id,
								points,
								shipName,
								shipClass,
								latitude,
								longitude,
								vesselDirection,
								type,
								image,
								sound,
								speed,
							} = row;

							const ship = {
								shipClass,
								latitude,
								longitude,
								vesselDirection,
							};

							const report = { time };

							return (
								<tr
									key={uuidv4()}
									style={{
										height: 104,
										backgroundColor: id === selectedRow && 'hsla(31, 100%, 60%, 40%)',
									}}
									onDoubleClick={() => dataEventForMap(points, ship, id)}
									onClick={() => dataEventForReport(report, id)}
								>
									<RowColumn topCell={firstTop} bottomCell={firstBottom} />
									<RowColumn topCell={secondTop} bottomCell={time} />
									{numberCard !== 1 && <RowColumn topCell={thirdTop} bottomCell={thirdBottom} />}

									<SoundAndImage type={type} image={image} sound={sound} />

									{!cardState.isOpenMap && isLargeTable && (
										<>
											{numberCard === 1 && (shipName || vesselDirection) && (
												<>
													<RowColumn topCell={shipName} />
													<RowColumn topCell={vesselDirection} />
												</>
											)}
											{(numberCard === 0 || numberCard === 3) && (
												<>
													<RowColumn topCell={shipName} />
													<RowColumn
														topCell={vesselDirection}
														bottomCell={`Speed: ${speed}`}
													/>
												</>
											)}
											{numberCard <= 4 && latitude && (
												<RowColumn topCell={latitude} bottomCell={longitude} />
											)}
										</>
									)}
								</tr>
							);
						})}
					</tbody>
				</table>
			</Box>
		</div>
	);
};

export default TableCard;
