import React, { useState } from 'react';
import styles from './DeleteSector.module.scss';
import { styled } from '@mui/material';
import ArrowBackIosOutlinedIcon from '@mui/icons-material/ArrowBackIosOutlined';
import zonesState from '../../../../../store/zonesState';
import connectsState from '../../../../../store/connectsState';
import { observer } from 'mobx-react-lite';
import { logServerResponse } from '../../../../../helpers/helpers';
import { prepareSectorsData } from '../../../../../api/prepareData/prepareSectorsData';
import { prepareZonesData } from '../../../../../api/prepareData/prepareZonesData';
import { apiMainDelete, apiMainGet } from '../../../../../api/mainApi';

const ModArrow = styled(ArrowBackIosOutlinedIcon)({
	marginRight: '20px',
	cursor: 'pointer',
	color: '#5F5F5F',

	'&:hover': {
		color: '#FF992C',
	},
});

const DeleteSector = observer(({ close = () => {} }) => {
	const { editSector, setEditSector, setZones, setSectors } = zonesState;

	const [nameSector, setNameSector] = useState('');
	const resultDelete = editSector.name === nameSector;

	const getZones = (url) => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url) => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const deleteSector = (url) => {
		apiMainDelete(url).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Удаление сектора - успешно';
			const errorMsg = 'Не удалось удалить сектор';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const handleDeleteSector = (id, name) => {
		const { linkSectors, linkFullSectors, linkZones } = connectsState;

		if (name === nameSector) {
			const url = `${linkSectors}${id}/`;

			deleteSector(url);
			setEditSector({});
		}
		setTimeout(() => {
			getFullSectors(linkFullSectors);
			getZones(linkZones);
		}, 500);
	};

	return (
		<>
			<div className={styles.header}>
				<ModArrow onClick={close} />
				Deleting a sector
			</div>
			<div className={styles.warningDelete}>Are you sure you want to delete a sector - {editSector.name}?</div>
			<div className={styles.editDel}>
				<input
					className={styles.fieldTextDelete}
					placeholder='Enter zone name'
					onChange={(e) => setNameSector(e.target.value)}
				/>
			</div>
			<div className={styles.notDelete} onClick={close}>
				Come back
			</div>
			<br />
			<div
				className={`${styles.delete} ${resultDelete && styles.ok}`}
				onClick={() => handleDeleteSector(editSector.id, editSector.name)}
			>
				Delete sector
			</div>
		</>
	);
});

export default DeleteSector;
