import React from 'react';
import styles from './SelectedBtnArch.module.scss';
import viewsState from '../../../../store/viewsState';
import { observer } from 'mobx-react-lite';

export const SelectedBtnArch = observer(() => {
	const { removedItemsArchive, selectAllRecords, setConfirmDeleteRecords, clearRemovedItemsArchive } = viewsState;

	const removeRecords = () => {
		setConfirmDeleteRecords(true);
	};

	const selectAll = () => {
		selectAllRecords();
	};

	const clearItemsRemoved = () => {
		clearRemovedItemsArchive();
	};

	return (
		<div className={styles.container}>
			<div className={styles.selectBtn}>Allocated:</div>
			<div className={styles.selectText}>{removedItemsArchive.length} file(s)</div>
			<div className={styles.selectAll} onClick={selectAll}>Select all</div>
			<div className={styles.delete} onClick={removeRecords}>Удалить</div>
			<div className={styles.reset} onClick={clearItemsRemoved}>Reset</div>
		</div>
	);
});