import React from 'react';
import { Box } from '@mui/material';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom';
import styles from './ViewPage.module.scss';
import { observer } from 'mobx-react-lite';
import Translation from '../../components/Translation/Translation';
import Records from '../../components/Records/Records';
import Header from '../../components/Header/Header';
import viewsState from '../../store/viewsState';

export default observer(() => {
	const { path } = useRouteMatch();
	const { isFullScreenView } = viewsState;

	return (
		<Box className={styles.monitorPage}>
			{!isFullScreenView && <Header />}

			<Switch>
				<Route
					path={`${path}/translation`}
					component={Translation}
				/>
				<Route path={`${path}/records`} component={Records} />
				<Route exact path={path}>
					<Redirect to={`${path}/translation`} />
				</Route>
			</Switch>
		</Box>
	);
});