import React from 'react';
import styles from './ShowAllButton.module.scss';
import { Box } from '@mui/material';
import {CustomIcon} from '../../../../../../helpers/helpers.js';
import emptySelect from '../../../../../../assets/portsMenu/emptyCheckBox.png';
import selectedItem from '../../../../../../assets/portsMenu/noBorderCheckBox.png';

const ShowAllButton = ({ isSelectAll, action = () => {} }) => {
	const icon = isSelectAll ? selectedItem : emptySelect;

	return (
		<div className={styles.selectionBlock} onClick={action}>
			<Box className={styles.switchItem}>
				<CustomIcon icon={icon} name={'Switch Icon'} size={31} />
			</Box>
			<div className={styles.selectionItem}>Select All</div>
		</div>
	);
};

export default ShowAllButton;
