import { ClickAwayListener, IconButton, List, ListItemButton } from '@mui/material';
import React, { useState } from 'react';
import styles from './SortButton.module.scss';
import checkboxOffImg from '../../assets/portsMenu/checkboxOff.png';
import checkboxOnImg from '../../assets/portsMenu/checkboxOn.png';
import { CustomIcon } from '../helpers';
import { v4 as uuidv4 } from 'uuid';

const checkAllSelected = (filters = []) => {
	return (
		filters.reduce((isResultSelect, filter) => {
			return isResultSelect && filter.isSelected && checkAllSelected(filter.nested);
		}, true)
	);
};
const checkNestedSelect = (filters = []) => {
	return (
		filters.reduce((isResultSelect, filter) => {
			return isResultSelect || filter.isSelected || checkNestedSelect(filter.nested);
		}, false)
	);
};
const getCopy = (data) => JSON.parse(JSON.stringify(data));
const changeAllNestedSwitch = (filters = [], isSelected = true) => {
	filters.forEach((filter) => {
		filter.isSelected = isSelected;
		changeAllNestedSwitch(filter.nested, isSelected);
	});

	return filters;
};

const getNestedFilters = (filter = [], path = [], stopPoint = path.length - 1) => {
	const nestedFilters = path.reduce((prevFilter, pathIndex, index) => {
		const isLast = index === stopPoint;
		const curFilter = isLast
			? prevFilter
			: prevFilter[pathIndex].nested;

		return curFilter;
	}, filter);

	return nestedFilters;
};

const checkParentSelect = (path, filters) => {
	const testArrFilters = [];
	const parentPath = [...path];
	parentPath.pop();
	
	parentPath.reduce((nextFilter, index) => {
		testArrFilters.push(nextFilter[index]);
		
		return nextFilter[index].nested;
	}, filters);

	testArrFilters
		.reverse()
		.forEach((filter) => {
			filter.isSelected = checkNestedSelect(filter.nested);
		});
};


export default ({ icon = '', title = '', setDisplayedFilters = () => {}, action = () => {}, displayedFilters = [] }) => {
	const { container, iconButton, list } = styles;

	const [isShow, setShow] = useState(false);
	const [isAllSelected, setAllSelected] = useState(checkAllSelected(displayedFilters));

	const handleCloseBody = () => {
		setShow(() => false);
	};

	const handleChangeVisible = () => {
		setShow((prevShow) => !prevShow);
	};

	const handleSelectAll = () => {
		const isSelected = checkAllSelected(displayedFilters);

		changeAllNestedSwitch(displayedFilters, !isSelected);
		setAllSelected(!isSelected);
		action(displayedFilters);
	};

	const handleSelectItem = (path = []) => {
		const curFilters = getNestedFilters(displayedFilters, path);

		const lastPath = path[path.length - 1];
		const curFilter = curFilters[lastPath];

		const { isSelected, nested } = curFilter;
		curFilter.isSelected = !isSelected;

		curFilter.nested = changeAllNestedSwitch(nested, !isSelected);

		const stopPoint = path.length - 1;

		if (stopPoint) checkParentSelect(path, displayedFilters);

		setDisplayedFilters(getCopy(displayedFilters));
		setAllSelected(checkAllSelected(displayedFilters));
		action(displayedFilters);
	};

	const buttonBody = () => {
		const { body, bodyContainer, bodyTitle, nameItem } = styles;
		const allSelIcon = isAllSelected ? checkboxOnImg : checkboxOffImg;

		const nestedList = (data = [],  step = 0, path = []) => {

			return data.map((item, index) => {
				const { name, isSelected, nested } = item;
				
				const icon = isSelected ? checkboxOnImg : checkboxOffImg;
				const newPath = [...path, index];

				return (
					<div
						key={uuidv4()}
						className={`${!step && styles.nestedList}`}
					>
						<ListItemButton 
							className={styles.item}
							dense={true}
							
							style={{ marginLeft: `${step * 8 * 2}px` }}
							onClick={() => handleSelectItem(newPath)}
						>
							<CustomIcon icon={icon} size={24} />

							<span className={nameItem}>{name}</span>
						</ListItemButton>

						<List disablePadding>{nestedList(nested, step + 1, newPath)}</List>
					</div>
				);
			});
		};

		return (
			<ClickAwayListener onClickAway={handleCloseBody}>
				<div className={body}>
					<div className={bodyContainer}>
						<span className={bodyTitle}>{title}</span>

						<List className={list}>
							<ListItemButton className={`${styles.item} ${styles.all}`} onClick={handleSelectAll}>
								<CustomIcon icon={allSelIcon} size={24} />

								<span className={nameItem}>Все</span>
							</ListItemButton>

							{nestedList(displayedFilters)}
						</List>
					</div>
				</div>
			</ClickAwayListener>
		);
	};

	return (
		<div className={container}>
			<IconButton className={iconButton} sx={{
				'&:hover': {
					backgroundColor: 'hsl(31, 100%, 40%)'
				}
			}} 
			disableRipple 
			onClick={displayedFilters.length ? handleChangeVisible : () => {}}>
				{icon}
			</IconButton>

			{isShow && buttonBody()}
		</div>
	);
};
