import React, { useEffect, useRef } from 'react';
import './Map.css';
import ShipsOnMap from './ShipsOnMap/ShipsOnMap';
import { MapContainer, TileLayer, Polygon, Polyline, useMapEvents, ScaleControl } from 'react-leaflet';
import portState from '../../../store/portState';
import zonesState from '../../../store/zonesState';
import radarsState from '../../../store/radarsState';
import viewsState from '../../../store/viewsState';
import { observer } from 'mobx-react-lite';
import CoordsMouseMap from './CoordsMouseMap/CoordsMouseMap';
import MarkersOnMap from './MarkersOnMap/MarkersOnMap';
import imgCam from '../../../assets/images/camera1.png';
import PolygonsOnMap from './PolygonsOnMap/PolygonsOnMap';
import CirclesOnMap from './CirclesOnMap/CirclesOnMap';
import { v4 as uuidv4 } from 'uuid';
import 'leaflet/dist/leaflet.css';

const Map = observer(({ openInfoShip, viewInfoShip, predict = [], pathShip = [] }) => {
	const {
		isCreateZone,
		isCreateSector,
		formatDates,
		centerMap,
		setCenterMap,
		setConfirmCreateZone,
		setConfirmCreateSector,
	} = portState;
	const { zones, sectors, pointsZone, pointsSector, setPointsZone, setPointsSector } = zonesState;
	const { aises } = radarsState;
	const { cameras } = viewsState;
	const mapRef = useRef(null);
	const mapPath = `http://${window.location.host}/static/map/{z}/{x}/{y}.png`;

	const isResultViewPort = isCreateZone || isCreateSector;
	const isResultViewPathShips = !!formatDates.length;

	const markers = [{ id: 1, items: cameras, icon: imgCam, size: [40, 40], rotate: 260 }];

	const polygons = [
		{ id: 1, value: 'Zones', items: zones, color: '#04B900' },
		{ id: 2, value: 'Sectors', items: sectors, color: 'white' },
	];

	const circles = [{ id: 1, items: aises, size: 15000, color: 'orange', fill: 'none', dash: 20 }];

	useEffect(() => {
		setConfirmCreateZone(pointsZone.length > 2);
		setConfirmCreateSector(pointsSector.length > 2);
	}, [pointsZone, pointsSector]);

	const MapEvents = () => {
		useMapEvents({
			click({ latlng: { lat, lng } }) {
				isCreateZone && setPointsZone([...pointsZone, [lat, lng]]);
				isCreateSector && setPointsSector([...pointsSector, [lat, lng]]);
			},
			dragend() {
				if (mapRef.current) {
					const coordsCenter = mapRef.current.getCenter();
					setCenterMap([coordsCenter.lat, coordsCenter.lng]);
				}
			},
		});
		return <></>;
	};

	const PathShip = () => {
		const polylineData = {
			true: { data: pathShip, dashArray: 5, color: 'yellow' },
			false: { data: predict, dashArray: 10, color: 'green' },
		};

		const { data, dashArray, color } = polylineData[isResultViewPathShips];

		return <Polyline positions={data} weight={2} dashArray={dashArray} color={color} />;
	};

	return (
		<MapContainer
			ref={mapRef}
			center={centerMap}
			zoom={16}
			scrollWheelZoom={true}
			className='container'
			zoomControl={false}
			minZoom={11}
			maxZoom={18}
		>
			<TileLayer url={mapPath} />
			{/* <TileLayer url='https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png' /> */}

			{!isResultViewPort && (
				<>
					<ScaleControl metric={true} imperial={false} />

					<CoordsMouseMap />

					<ShipsOnMap openInfoShip={openInfoShip} viewInfoShip={viewInfoShip} />

					{markers.map((item) => (
						<MarkersOnMap key={uuidv4()} item={item} />
					))}

					{circles.map((item) => (
						<CirclesOnMap key={uuidv4()} item={item} />
					))}

					<PathShip />
				</>
			)}

			{polygons.map((item) => (
				<PolygonsOnMap key={uuidv4()} item={item} />
			))}

			<MapEvents />
			<Polygon positions={pointsZone} color='#04B900' />
			<Polygon positions={pointsSector} color='white' />
		</MapContainer>
	);
});

export default Map;
