import React from 'react';
import styles from './ModalDelete.module.scss';
import { Modal } from '@mui/material';
import connectsState from '../../../../../store/connectsState';
import viewsState from '../../../../../store/viewsState';
import { prepareCamerasFullData } from '../../../../../api/prepareData/prepareCamerasData';
import { logServerResponse } from '../../../../../helpers/helpers';
import { apiMainGet, apiMainPost } from '../../../../../api/mainApi';

const ModalDelete = ({ isModal, handleModal, handleClose, deleteId }) => {
	const getCameras = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			if (error.isError) {
				viewsState.setCameras([]);
				return;
			}

			const { setCameras } = viewsState;
			setCameras(prepareCamerasFullData(data));
		});
	};

	const handleDeleteDevice = () => {
		const { linkDelCamera, linkGetCameras } = connectsState;
		const parameters = { id: deleteId };

		apiMainPost(linkDelCamera, parameters).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Удаление устройства - успешно';
			const errorMsg = 'Не удалось удалить устройство';
			logServerResponse(isError, seccessMsg, errorMsg);
		});

		getCameras(linkGetCameras);
		handleClose();
	};

	return (
		<Modal open={isModal} closeAfterTransition Backdrop={{ timeout: 500 }} onClose={handleModal}>
			<div className={styles.container}>
				<div className={styles.content}>
					<p className={styles.titleStyle}>Delete device?</p>
				</div>
				<div className={styles.btnContainer}>
					<div className={styles.styleBtn} onClick={handleDeleteDevice}>
						Delete
					</div>
					<div className={styles.styleBtn} onClick={handleClose}>
						Cancel
					</div>
				</div>
			</div>
		</Modal>
	);
};

export default ModalDelete;
