import React from 'react';
import styles from './ZoneItemRadar.module.scss';
import { Box } from '@mui/material';
import zonesState from '../../../../../../store/zonesState';
import {CustomIcon} from '../../../../../../helpers/helpers.js';
import { observer } from 'mobx-react-lite';
import emptySelect from '../../../../../../assets/portsMenu/emptyCheckBox.png';
import selectedItem from '../../../../../../assets/portsMenu/noBorderCheckBox.png';

const ZoneItemRadar = observer(({ sector, zone }) => {
	const { setVisibleZone } = zonesState;
	const { id, name, isShow } = zone;

	const icon = isShow ? selectedItem : emptySelect;

	const clickItem = () => {
		setVisibleZone(sector, id, !isShow);
	};

	return (
		<div className={`${styles.List} ${styles.listItem}`}>
			<div className={styles.block} onClick={() => clickItem(id)}>
				<Box className={styles.switchItem}>
					<CustomIcon icon={icon} name={'Switch Icon'} size={31} />
				</Box>
				<div className={styles.ship}>{name}</div>
			</div>
		</div>
	);
});

export default ZoneItemRadar;
