import React, { useState } from 'react';
import styles from './EditSector.module.scss';
import ArrowBackIosOutlinedIcon from '@mui/icons-material/ArrowBackIosOutlined';
import { styled } from '@mui/material';
import pen from '../../../../assets/portsMenu/pen.png';
import CheckIcon from '@mui/icons-material/Check';
import CloseIcon from '@mui/icons-material/Close';
import portState from '../../../../store/portState';
import zonesState from '../../../../store/zonesState';
import connectsState from '../../../../store/connectsState';
import { checkAllSelected, logServerResponse } from '../../../../helpers/helpers.js';
import { observer } from 'mobx-react-lite';
import DeleteSector from './DeleteSector/DeleteSector';
import { prepareZonesData } from '../../../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../../../api/prepareData/prepareSectorsData';
import { apiMainGet, apiMainPut } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const ModArrow = styled(ArrowBackIosOutlinedIcon)({
	cursor: 'pointer',
	color: '#5F5F5F',

	'&:hover': {
		color: '#FF992C',
	},
});

const EditSector = observer(() => {
	const { zones, editSector, sectorTypes, setVisibleSector, setEditSector, setZones, setSectors } = zonesState;
	const { portMenuList, setCreateSector } = portState;
	const { linkZones, linkFullSectors, linkSectors } = connectsState;

	const [isEditInput, setEditInput] = useState(false);
	const [tempNameZone, setTempNameZone] = useState('');
	const [isDeleteSector, setDeleteSector] = useState(false);

	const resultCheck = checkAllSelected(portMenuList) && checkAllSelected(zones);

	const acceptEdit = () => {
		setEditInput(false);
		setEditSector({ ...editSector, name: tempNameZone });
	};

	const closeEditMenu = () => {
		setVisibleSector(editSector.id, resultCheck);
		setEditSector({});
	};

	const closeDeleteMenu = () => {
		setDeleteSector(false);
	};

	const closeEdit = () => {
		setEditInput(false);
	};

	const getZones = (url, controller = new AbortController()) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const updateSector = (link, sector) => {
		apiMainPut(link, sector).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Обновление данных сектора - успешно';
			const errorMsg = 'Не удалось обновить данные сектора';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const handleUpdateSector = (sector) => {
		updateSector(`${linkSectors}${editSector.id}/`, sector);

		setEditSector({});

		setTimeout(() => {
			getFullSectors(linkFullSectors);
			getZones(linkZones);
		}, 500);
	};

	const zoneEditsMenu = () => {
		return (
			<div className={styles.mainEdits}>
				<div className={styles.nameInput}>Sector type</div>

				<select
					className={styles.fieldText}
					value={editSector.type}
					onChange={(e) => setEditSector({ ...editSector, type: e.target.value })}
				>
					{sectorTypes.map(({ id, name }) => (
						<option key={uuidv4()} value={id}>
							{name}
						</option>
					))}
				</select>

				<div className={styles.editSize} onClick={() => setCreateSector(true)}>
					Edit sector
				</div>

				<br />

				<div className={styles.deleteEdit} onClick={() => setDeleteSector(true)}>
					Delete sector
				</div>
			</div>
		);
	};

	return (
		<>
			{isDeleteSector ? (
				<DeleteSector close={closeDeleteMenu} />
			) : (
				<>
					<div className={styles.header}>
						<ModArrow onClick={() => closeEditMenu()} />
						Sector Editing
					</div>

					{!isEditInput ? (
						<div className={styles.editName}>
							{editSector.name}
							<img className={styles.pen} src={pen} onClick={() => setEditInput(true)} />
						</div>
					) : (
						<div className={styles.edit}>
							<input
								className={styles.fieldTextEdit}
								maxLength="16"
								onChange={(e) => setTempNameZone(e.target.value)}
							/>
							<CheckIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={acceptEdit} />
							<CloseIcon sx={{ color: '#CFCFCF', cursor: 'pointer' }} onClick={closeEdit} />
						</div>
					)}

					{zoneEditsMenu()}

					<div className={styles.button}>
						<button className={styles.modBtn} onClick={() => handleUpdateSector(editSector)}>
							Save Changes
						</button>
					</div>
				</>
			)}
		</>
	);
});

export default EditSector;
