import React, { useEffect } from 'react';
import connectsState from '../../store/connectsState';

export default ({ children }) => {
	useEffect(() => {
		const localPath = window.location.host;

		const mainPath = `http://${localPath}/api/`;
		const translationPath = 'http://192.168.1.134:8000/';
		const mainPathWSDashboard = `ws://${localPath}/ws/`;

		connectsState.setWSArchiveEvents(`${mainPathWSDashboard}dashboard/`);
		connectsState.setWSSound(new WebSocket(`${mainPathWSDashboard}monitor/`));
		connectsState.setWSShipsEvents(`${mainPathWSDashboard}aisevents/`);

		connectsState.setLinkZones(`${mainPath}zonesais/`);
		connectsState.setLinkSectors(`${mainPath}sectorsais/`);
		connectsState.setLinkFullSectors(`${mainPath}fullaissectors/`);
		connectsState.setLinkTypesSector(`${mainPath}sectortypes/`);
		connectsState.setLinkTypesZone(`${mainPath}zonetypes/`);
		connectsState.setLinkGetLastShips(`${mainPath}getaliveships/2/`);
		connectsState.setLinkGetPointsSelectShip(`${mainPath}aisshippoints/`);
		connectsState.setLinkGetShipsClasses(`${mainPath}aisshipclasses/`);

		connectsState.setLinkAddStream(`${translationPath}camera_add`);
		connectsState.setLinkGetStreams(`${mainPath}cameras/`);
		connectsState.setLinkDelStream(`${translationPath}delete_stream`);
		connectsState.setLinkGetCameras(`${translationPath}camera_get`);
		connectsState.setLinkChangeTimeRecords(`${translationPath}camera_rec_time_change`);
		connectsState.setLinkDelCamera(`${translationPath}camera_del`);
		connectsState.setLinkUpdateCamera(`${translationPath}camera_update`);
		connectsState.setLinkGetRecords(`${translationPath}records_get/`);
		connectsState.setLinkDelRecords(`${translationPath}record_del`);

		connectsState.setLinkCameras(`${mainPath}cameras/`);
		connectsState.setLinkCameraFullInfo(`${mainPath}camerafullinfo/`);

		connectsState.setLinkCvPresets(`${mainPath}cameras/`);
		connectsState.setLinkPresetEdit(`${mainPath}cvperests/`);
		connectsState.setLinkPresetDelete(`${mainPath}cameras/`);
		connectsState.setLinkChangeActivePreset(`${mainPath}cameras/`);

		connectsState.setLinkPartolStart(`${mainPath}cameras/`);

		connectsState.setLinkCamerasList(`${mainPath}camerashortlist/`);
		connectsState.setLinkAisZonePoints(`${mainPath}aiszonenopoints/`);
		connectsState.setLinkGetPolygonTypes(`${mainPath}polygontypes/`);
		connectsState.setLinkChangePolygon(`${mainPath}cvpolygons/`);
		connectsState.setLinkDeletePolygon(`${mainPath}cvpolygons/`);
		connectsState.setLinkCreateChangePolygon(`${mainPath}cvpolygons/`);

		connectsState.setWsLinkCreatePreset(`ws://${localPath}/ws/monitor/`);

		connectsState.setLinkGetReport(`${mainPath}make_report/`);
	}, []);

	return <>{children}</>;
};
