import React, { useEffect, useState } from 'react';
import styles from './ModalCreateZone.module.scss';
import { Box, ListItemText } from '@mui/material';
import { Modal } from '@mui/material';
import { observer } from 'mobx-react-lite';
import CheckIcon from '@material-ui/icons/Check';
import zonesState from '../../../../../store/zonesState';
import connectsState from '../../../../../store/connectsState';
import close from '../../../../../assets/portsMenu/close.png';
import portState from '../../../../../store/portState';
import { logServerResponse } from '../../../../../helpers/helpers';
import { prepareZonesData } from '../../../../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../../../../api/prepareData/prepareSectorsData';
import { apiMainGet, apiMainPost } from '../../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const ModalCreateZone = observer(({ isModal, handleModal, handleClose }) => {
	const { shipsClasses } = portState;
	const { pointsZone, setZones, zoneTypes, sectors, setSectors } = zonesState;
	const { linkZones, linkFullSectors } = connectsState;

	const [nameZone, setNameZone] = useState('');
	const [typeZoneId, setTypeZoneId] = useState(zoneTypes[0]?.id ?? null);
	const [sectorId, setSectorId] = useState(sectors[0]?.id ?? null);
	const [selectedClasses, setSelectedClasses] = useState([]);
	const [isAllTypes, setIsAllTypes] = useState(false);

	useEffect(() => {
		setIsAllTypes(checkAllSelected());
	}, [selectedClasses]);

	const getZones = (url = '', controller = new AbortController()) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '', controller = new AbortController()) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const addZoneApi = (url, zone) => {
		apiMainPost(url, zone).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Добавление зоны - успешно';
			const errorMsg = 'Не удалось добавить зону';

			logServerResponse(isError, seccessMsg, errorMsg);
		});

		handleClose();

		setTimeout(() => {
			getFullSectors(linkFullSectors);
			getZones(linkZones);
		}, 500);
	};

	const handleCreateZone = () => {
		const zone = {
			name: nameZone,
			type: typeZoneId,
			aisPoints: pointsZone,
			sector: sectorId,
			blacklist: selectedClasses,
		};
		addZoneApi(linkZones, zone);
	};

	const handleSelectClass = (id) => {
		const index = selectedClasses.findIndex((item) => item === id);

		if (index === -1) {
			setSelectedClasses([...selectedClasses, id]);
		} else {
			setSelectedClasses(selectedClasses.filter((item) => item !== id));
		}
	};

	const checkAllSelected = () => {
		const numSelItems = selectedClasses.length;
		const numItems = shipsClasses.length;

		return numSelItems === numItems;
	};

	const handleAllTypes = () => {
		setIsAllTypes((prev) => !prev);

		if (!isAllTypes) {
			setSelectedClasses(shipsClasses.map(({ id }) => id));
		} else {
			setSelectedClasses([]);
		}
	};

	return (
		<Modal open={isModal} onClose={handleModal} closeAfterTransition>
			<div className={styles.container}>
				<div className={styles.header}>
					Zone settings
					<img className={styles.closeIcon} src={close} onClick={handleModal} />
				</div>

				<div className={styles.content}>
					<div className={styles.nameInput}>Zone name</div>
					<input className={styles.fieldText} maxLength="16" onChange={(e) => setNameZone(e.target.value)} />

					<div className={styles.nameInput}>Zone type</div>
					<select className={styles.fieldText} value={typeZoneId} onChange={(e) => setTypeZoneId(e.target.value)}>
						{zoneTypes.map(({ id, name }) => (
							<option key={uuidv4()} value={id}>
								{name}
							</option>
						))}
					</select>

					<div className={styles.nameInput}>Sector selection</div>
					<select className={styles.fieldText} value={sectorId} onChange={(e) => setSectorId(e.target.value)}>
						{sectors.map(({ id, name }) => (
							<option key={uuidv4()} value={id}>
								{name}
							</option>
						))}
					</select>

					<div className={styles.nameInput}>Prohibited ship classes</div>
					<Box className={styles.ListCameras}>
						<div className={styles.allSelect} onClick={handleAllTypes}>
							Select all
							{isAllTypes && <CheckIcon style={{ color: '#FF992C' }} />}
						</div>
						{shipsClasses.map(({ id, name }) => {
							const index = selectedClasses.findIndex((item) => item === id);
							const isTypeSelected = index !== -1;

							return (
								<div
									key={uuidv4()}
									className={styles.List}
									value={name}
									onClick={() => handleSelectClass(id)}
								>
									<ListItemText value={name} primary={name} />
									{isTypeSelected && <CheckIcon style={{ color: '#FF992C' }} />}
								</div>
							);
						})}
					</Box>

					<button className={styles.modBtn} onClick={handleCreateZone}>
						Create zone
					</button>
				</div>
			</div>
		</Modal>
	);
});

export default ModalCreateZone;
