import React, { useEffect, useState } from 'react';
import { BrowserRouter } from 'react-router-dom';
import { observer } from 'mobx-react-lite';
import authState from './store/authState.js';
import CreateServerConnections from './components/CreateServerConnections/CreateServerConnections.js';
import DesktopOnly from './helpers/DesktopOnly.js';
import NoAccessRouter from './routes/NoAccessRouter.js';
import UserAccessRouter from './routes/UserAccessRouter.js';

export default observer(() => {
	const [isAutorize, setIsAutorize] = useState(false);

	useEffect(() => {
		setIsAutorize(authState.isAutorize);
	}, [authState.isAutorize]);

	const routes = {
		true: <UserAccessRouter />,
		false: <NoAccessRouter />,
	};

	return (
		<BrowserRouter>
			<CreateServerConnections>
				<DesktopOnly>{routes[isAutorize]}</DesktopOnly>
			</CreateServerConnections>
		</BrowserRouter>
	);
});
