import React, { useState, useEffect, lazy, Suspense } from 'react';
import styles from '../SinginIn/SingIn.module.scss';
import backgroundImage from '../images/loading.jpg';

const Loading = lazy(() => import('../../Loading/Loading'));
const ContactUs = lazy(() => import('./ContactUs/ContactUs'));
const SigninForm = lazy(() => import('./SingInForm/SingInForm'));

export default () => {
	const [isLoad, setLoad] = useState(true);
	const loadTime = 3;

	useEffect(() => {
		setTimeout(() => {
			setLoad(false);
		}, loadTime * 1000);
	}, []);

	return (
		<Suspense fallback={<div>Loading...</div>}>
			{isLoad ? (
				<Loading />
			) : (
				<div className={styles.main} style={{ backgroundImage: `url(${backgroundImage})` }}>
					<ContactUs />
					<SigninForm />
				</div>
			)}
		</Suspense>
	);
};
