import { Box, Button, IconButton, InputAdornment, Modal, styled, TextField, Typography } from '@mui/material';
import React, { useState } from 'react';
import styles from './ModalEditPreset.module.scss';
import CloseIcon from '../../AdditionalControlPanel/IconConponents/CloseIcon';
import { useEffect } from 'react';
import { v4 as uuidv4 } from 'uuid';

const CssTextField = styled(TextField)({
	'& label.Mui-focused': {
		backgroundColor: 'transparent',
	},
	'& .MuiInputBase-root': {
		color: 'var(--colorText)',
		fontWeight: 400,
	},
	'& .MuiOutlinedInput-root': {
		'& fieldset': {
			borderRadius: 0,
			borderColor: '#c9c9c9',
		},
		'&:hover fieldset': {
			borderColor: '#c9c9c9',
		},
		'&.Mui-focused fieldset': {
			borderColor: '#c9c9c9',
		},
	},
});

const defZones = { id: -1, name: 'Default Zones' };

export default ({
	isShow = false,
	setShow = () => {},
	positionStyle = {},

	defText = '',
	zones = [],

	title = '',
	label = '',

	btnOkStyle = {},
	btnNameOk = '',
	handleOk = () => {},

	btnCancelStyle = {},
	btnNameCancel = '',
	handleCancel = () => {},

	errorMsg = '',
}) => {
	const [text, setText] = useState(defText);
	const [isError, setError] = useState(false);
	const [active, setActive] = useState(defZones);

	useEffect(() => {
		if (!zones.length) return;

		setActive({ ...zones[0] });
	}, [zones]);

	const textError = isError ? errorMsg : '';
	const isShowClearIcon = Boolean(text.length);

	const handleChangeText = (e) => {
		setText(() => e.target.value);
	};

	const handleClearTextField = () => {
		setText('');
	};

	const handleCreatePreset = () => {
		if (!text.length) {
			setError(true);
			return;
		}

		setShow(false);
		setDefaultState();
		handleOk({ name: text, zoneId: active.id });
	};

	const handleCanselCreatePreset = () => {
		setShow(false);
		setDefaultState();
		handleCancel();
	};

	const handleClose = () => {
		setShow(false);
		setDefaultState();
	};

	const handleChangeActive = (e) => {
		const index = zones.findIndex(({ id }) => id === +e.target.value);

		if (index === -1) return;
		setActive({ ...zones[index] });
	};

	const setDefaultState = () => {
		setText(defText);
		setError(false);
	};

	return (
		<Modal open={isShow} aria-labelledby="modal-edit-preset-title" aria-describedby="modal-edit-preset-description">
			<Box className={styles.container} sx={positionStyle}>
				<div className={styles.modalHeader}>
					<Typography className={styles.modalTitle}>{title}</Typography>

					<IconButton className={styles.iconButton} aria-label="Close Modal" edge="end" onClick={handleClose}>
						<CloseIcon />
					</IconButton>
				</div>

				<Box className={styles.contentPosition}>
					<Box mt={'18px'}>
						<Typography className={styles.inputLabel}>{label}</Typography>
						<CssTextField
							value={text}
							variant="outlined"
							size="small"
							margin="dense"
							autoComplete="off"
							fullWidth
							onChange={handleChangeText}
							error={isError}
							helperText={textError}
							InputProps={{
								endAdornment: isShowClearIcon && (
									<InputAdornment position="end">
										<IconButton
											className={styles.iconButton}
											aria-label="Clear Input"
											edge="end"
											onClick={handleClearTextField}
										>
											<CloseIcon width={32} height={32} />
										</IconButton>
									</InputAdornment>
								),
							}}
						/>

						<select
							style={{
								width: '100%',

								marginTop: '10px',
								padding: '8px 14px',
								border: '1px solid var(--bgThemeBorder)',

								color: 'var(--colorText)',
								fontSize: 16,
								fontFamily: 'Inter',

								MozAppearance: 'none',
								WebkitAppearance: 'none',
								appearance: 'none',

								background: 'var(--bgTheme)',
								backgroundImage: `
									linear-gradient(45deg, transparent 50%, gray 50%),
									linear-gradient(135deg, gray 50%, transparent 50%),
									linear-gradient(to right, #ccc, #ccc)
								`,
								backgroundPosition: `
									calc(100% - 20px) calc(1em + 2px),
									calc(100% - 15px) calc(1em + 2px),
									calc(100% - 2.5em) 0.5em
								`,
								backgroundSize: `5px 5px,
									5px 5px,
									1px 1.5em`,
								backgroundRepeat: 'no-repeat',
							}}
							defaultValue={active.id}
							onChange={handleChangeActive}
						>
							{zones.map(({ id, name }) => (
								<option key={uuidv4()} value={id}>
									{name}
								</option>
							))}
						</select>
					</Box>

					<Box className={styles.buttonBox}>
						{btnNameOk && (
							<Button className={styles.create} sx={btnOkStyle} fullWidth onClick={handleCreatePreset}>
								{btnNameOk}
							</Button>
						)}

						{btnNameCancel && (
							<Button
								className={styles.cansel}
								style={btnCancelStyle}
								fullWidth
								onClick={handleCanselCreatePreset}
							>
								{btnNameCancel}
							</Button>
						)}
					</Box>
				</Box>
			</Box>
		</Modal>
	);
};
