import React, { useState } from 'react';
import styles from './DownloadReport.module.scss';
import ReportHead from './ReportHead/ReportHead';
import Link from '@mui/material/Link';
import TableCard from '../Archive/Cards/TableCard/TableCard';
import { columnsFull, nameReports } from '../Archive/data';
import { NavLink } from 'react-router-dom';
import eventArchiveState from '../../store/eventArchiveState';
import connectsState from '../../store/connectsState';
import DatePicker from './DatePicker/DatePicker';
import { apiDownloadReportPost } from '../../api/apiAll';
import moment from 'moment';
import CheckIcon from '@material-ui/icons/Check';
import { ListItemButton } from '@mui/material';
import { prepareFileDownload } from '../../api/prepareData/prepareFileDownload';
import {
	Document,
	Packer,
	Paragraph,
	Table,
	TableCell,
	TableRow,
	WidthType,
	VerticalAlign,
	AlignmentType,
	TextRun,
} from 'docx';
import { saveAs } from 'file-saver';

const DownloadReport = () => {
	const dataRow = eventArchiveState.eventsArchive;
	const url = connectsState.linkGetReport;

	const [isChecked, setChecked] = useState(false);
	const [idTable, setIdTable] = useState(0);
	const [dataReport, setDataReport] = useState({});

	const dateNow = moment(new Date()).format('YYYY-MM-DD HH:mm:ss');
	let dateStart = moment(new Date()).format('YYYY-MM-DD');
	let dateEnd = moment(new Date()).add(1, 'day').format('YYYY-MM-DD');

	const cardRows = dataRow[idTable];
	const table = {
		columns: columnsFull[idTable],
		rows: dataRow[idTable],
		numberCard: idTable,
	};

	const mimeType = 'application/vnd.openxmlformats-officedocument.wordprocessingml.document';

	const headTextOne = 'Карточка происшествия №____';
	const headTextTwo = 'ИТСОТБ Архангельский филиал ФГУП "Росморпорт"';
	const bottomTextOne = 'Оператор отдела технического';
	const bottomTextTwo = 'наблюдения за акваторией (ОТНА)';
	const bottomTextThree = '_'.repeat(20);

	const reportType = {
		0: 'ais_zone_control_events',
		1: 'cv_events',
		2: '',
		3: 'ais_move_events',
		4: 'sound_events',
		5: 'uv_events',
		6: 'ships_in_areas_time',
	};

	const firstColumn = [
		'Дата, время предоставления информации',
		'Место получения информации об угрозе совершения/совершении АНВ',
		'Дата/время получения информации',
		'Наименование объекта транспортной инфраструктуры',
		'Адрес местонахождения ОТИ',
		'Количество ТС морского и внутреннего транспорта на ОТИ',
		'Угроза совершения АНВ',
		'Сведения о совершении АНВ в деятельности ОТИ',
		'Дополнительные сведения при получении информации об угрозе совершения АНВ',
	];

	const secondColumn = [dateNow, '', dataReport.time, '', '', '', '', '', ''];

	const handleCheck = () => {
		setChecked((prevCheck) => !prevCheck);
	};

	const changeNameTable = (id) => {
		setIdTable(id);
	};

	const handleSelectRow = (report) => {
		setDataReport(report);
	};

	const downloadXlsxFile = () => {
		const parameters = {
			report_type: reportType[idTable],
			start_date: dateStart,
			end_date: dateEnd,
		};

		const download = () => {
			apiDownloadReportPost(url, parameters).then(({ data, error }) => {
				const { isError } = error;
				if (isError) return;

				const fileName = `${reportType[idTable]}-${dateStart}-${dateEnd}.xlsx`;
				prepareFileDownload(data, fileName);
			});
		};

		return (
			<Link onClick={download} underline="none" color="#F07C00" component="button">
				СКАЧАТЬ ОТЧЕТ
			</Link>
		);
	};

	const downloadEventFile = () => {
		const saveDocumentToFile = (doc, fileName) => {
			Packer.toBlob(doc).then((blob) => {
				const docblob = blob.slice(0, blob.size, mimeType);
				saveAs(docblob, fileName);
			});
		};

		const generateWordDocument = (event) => {
			event.preventDefault();

			const cell = (text, size) => {
				return new TableCell({
					children: [
						new Paragraph({
							alignment: AlignmentType.CENTER,
							children: [new TextRun(text)],
						}),
					],
					width: { size: size, type: WidthType.PERCENTAGE },
					verticalAlign: VerticalAlign.CENTER,
					alignment: AlignmentType.CENTER,
				});
			};

			const rows = firstColumn.map((field, num) => {
				return new TableRow({
					children: [cell(`${++num}`, 10), cell(`${field}`, 25), cell(`${secondColumn[--num]}`, 120)],
				});
			});

			const doc = new Document({
				sections: [
					{
						children: [
							new Paragraph({
								alignment: AlignmentType.CENTER,
								children: [
									new TextRun(headTextOne),
									new TextRun({
										text: headTextTwo,
										break: 2,
									}),
									new TextRun({
										break: 3,
									}),
								],
							}),

							new Table({
								rows: rows,
							}),

							new Paragraph({
								children: [
									new TextRun({
										text: bottomTextOne,
										break: 2,
									}),
									new TextRun({
										text: bottomTextTwo,
										break: 1,
									}),
									new TextRun(' '.repeat(80)),
									new TextRun(bottomTextThree),
								],
							}),
						],
					},
				],
			});

			saveDocumentToFile(doc, `${dateNow}.docx`);
		};

		return (
			<Link onClick={generateWordDocument} underline="none" color="#F07C00" component="button">
				СКАЧАТЬ ОТЧЕТ
			</Link>
		);
	};

	const downloadFiles = {
		false: downloadXlsxFile(),
		true: downloadEventFile(),
	};

	const handleSelectDates = (selectedDates) => {
		const isFirstDateExist = selectedDates[0] !== null;
		const isSecondDateExist = selectedDates[1] !== null;

		if (isFirstDateExist) {
			dateStart = moment(selectedDates[0]).format('YYYY-MM-DD');
		}

		dateEnd = moment(selectedDates[+isSecondDateExist]).add(1, 'day').format('YYYY-MM-DD');
	};

	const tableWithData = () => {
		const tableIdWithoutData = 6;
		const isDataExist = !!cardRows.length && idTable < tableIdWithoutData;

		return (
			<div className={styles.table}>
				{isDataExist ? (
					<TableCard table={table} action={(selectedEvent) => handleSelectRow(selectedEvent)} />
				) : (
					<p className={styles.noData}>Нет событий</p>
				)}
			</div>
		);
	};

	return (
		<div className={styles.cardField}>
			<div className={styles.headReport}>
				<div className={styles.textAndSelect}>
					<p>Таблица</p>
					<ReportHead names={nameReports} idField={idTable} action={(id) => changeNameTable(id)} />

					<p>Период выгрузки</p>
					<DatePicker action={(selectedDates) => handleSelectDates(selectedDates)} />

					<ListItemButton onClick={handleCheck}>
						<p>Отчёт о событии</p>
						<div className={styles.check}>{isChecked && <CheckIcon style={{ color: '#FF992C' }} />}</div>
					</ListItemButton>
				</div>

				<div className={styles.icons}>
					{downloadFiles[isChecked]}
					<NavLink className={styles.modLink} to="/archive/cards">
						{' '}
						НАЗАД{' '}
					</NavLink>
				</div>
			</div>

			{tableWithData()}
		</div>
	);
};

export default DownloadReport;
