import React from 'react';
import { Polygon } from 'react-leaflet';
import { observer } from 'mobx-react-lite';
import 'leaflet/dist/leaflet.css';

import { v4 as uuidv4 } from 'uuid';

const PolygonsOnMap = observer(({ item }) => {
	const { items, color } = item;

	const mapPolygon = (aisPoints) => <Polygon key={uuidv4()} positions={aisPoints} color={color} />;

	return items.map(({ aisPoints, isShow }) => isShow && mapPolygon(aisPoints));
});

export default PolygonsOnMap;
