import React, { useState } from 'react';
import styles from './Archive.module.scss';
import { Grid, styled } from '@mui/material';
import { NavLink } from 'react-router-dom';
import cardState from '../../store/cardState';
import CardBig from './Cards/CardBig/CardBig';
import { observer } from 'mobx-react-lite';
import VideoButtons from './Cards/VideoButtons/VideoButtons';
import eventArchiveState from '../../store/eventArchiveState';
import Card from './Cards/Card/Card';
import { nameCards } from './data';
import { v4 as uuidv4 } from 'uuid';

const ModLink = styled(NavLink)({
	width: '250px',
	height: '50px',

	display: 'flex',
	justifyContent: 'center',
	alignItems: 'center',

	background: '#FF992C',
	borderRadius: '0',
	boxShadow: 'none',
	color: '#FFFFFF',
	cursor: 'pointer',
	textDecoration: 'none',

	'&:hover': {
		boxShadow: 'none',
		background: '#CC6900',
		color: '#FFFFFF',
	},
});

const Archive = observer(() => {
	const [numberCard, setNumberCard] = useState(0);
	const [currentPage, setCurrentPage] = useState(1);

	const cardsPage = nameCards.slice(6 * (currentPage - 1), 6 * currentPage);
	const rows = eventArchiveState.eventsArchive;
	const userFilter = eventArchiveState.selectedDevices;
	const availableDevices = eventArchiveState.availableDevices;
	const modDevices = eventArchiveState.modDevices;

	const cardBidData = {
		rows: rows[numberCard],
		userFilter: userFilter[numberCard],
		availableDevices: availableDevices[numberCard],
		modDevices: modDevices[numberCard],
	};

	const handleSelect = (number) => {
		setNumberCard(number);
		cardState.changeOpenModal();
	};

	const changePage = (n = null) => {
		setCurrentPage((prev) => prev + n);
	};

	return (
		<div>
			<div className={styles.headPage}>
				<p className={styles.namePage}>Event map</p>
				<ModLink to="/archive/downloadreport"> Download Report </ModLink>
			</div>
			{cardState.isOpenModal ? (
				<CardBig numberCard={numberCard} data={cardBidData} />
			) : (
				<div>
					<div className={styles.bodyPage}>
						<Grid className={styles.cards}>
							{cardsPage.map((card) => {
								const { id } = card;
								const cardData = {
									rows: rows[id],
									userFilter: userFilter[id],
									availableDevices: availableDevices[id],
									modDevices: modDevices[id],
									number: id,
								};

								return (
									<Card
										xs={10}
										key={uuidv4()}
										data={cardData}
										action={(selectedCard) => handleSelect(selectedCard)}
									/>
								);
							})}
						</Grid>
					</div>
					<div className={styles.footer}>
						<VideoButtons
							currentPage={currentPage}
							lastPage={1}
							handleChangePage={(n = null) => changePage(n)}
						/>
					</div>
				</div>
			)}
		</div>
	);
});

export default Archive;
