import React from 'react';
import styles from './ItemMenu.module.scss';
import { Box } from '@mui/material';
import { CustomIcon } from '../../../../../../helpers/helpers.js';
import { observer } from 'mobx-react-lite';
import zonesState from '../../../../../../store/zonesState';
import { checkAllSelected } from '../../../../../../helpers/helpers.js';
import ZoneItem from './ZoneItem/ZoneItem';
import portState from '../../../../../../store/portState';
import emptySelect from '../../../../../../assets/portsMenu/emptyCheckBox.png';
import selectedItem from '../../../../../../assets/portsMenu/noBorderCheckBox.png';
import { v4 as uuidv4 } from 'uuid';

const ItemMenu = observer(({ item, isZones, setSelectAll, setHandleItem }) => {
	const { portMenuList } = portState;
	const { setVisibleSector, setEditSector } = zonesState;
	const { id, name, mmsi, isShow, zones } = item;

	const icon = isShow ? selectedItem : emptySelect;

	const handleSelectItem = (itemId = null, itemMmsi = null) => {
		const index = mmsi
			? portMenuList.findIndex(({ mmsi }) => mmsi === itemMmsi)
			: portMenuList.findIndex(({ id }) => id === itemId);

		const tempItems = [...portMenuList];

		const { isShow } = tempItems[index];
		tempItems[index].isShow = !isShow;

		setSelectAll(checkAllSelected(tempItems));
		setHandleItem((prev) => !prev);
	};

	const handleSectorView = () => {
		setVisibleSector(id, true);
		setEditSector(item);
	};

	return (
		<>
			<div className={styles.List}>
				<div className={styles.block} onClick={() => handleSelectItem(id, mmsi)}>
					<Box className={styles.switchItem}>
						<CustomIcon icon={icon} name={'Switch Icon'} size={31} />
					</Box>
					<div className={styles.ship}>
						{name}
						{mmsi && <div className={styles.mmsi}>mmsi: {mmsi}</div>}
					</div>
				</div>

				{isZones && (
					<div className={styles.edit} onClick={handleSectorView}>
						edit
					</div>
				)}
			</div>

			{zones?.map((zone) => (
				<ZoneItem key={uuidv4()} sector={id} zone={zone} setHandleItem={setHandleItem} />
			))}
		</>
	);
});

export default ItemMenu;
