import React, { useState } from 'react';
import styles from './FullMap.module.scss';
import portState from '../../../../store/portState';
import zonesState from '../../../../store/zonesState';
import { observer } from 'mobx-react-lite';
import ModalCreateZone from './ModalCreateZone/ModalCreateZone.js';
import ModalHelp from './ModalHelp/ModalHelp';
import { Box } from '@mui/material';
import close from '../../../../assets/portsMenu/close2.png';
import Map from '../../Map/Map.js';
import CreateZone from './CreateZone/CreateZone';
import CreateSector from './CreateSector/CreateSector';
import ModalCreateSector from './ModalCreateSector/ModalCreateSector';

const FullMap = observer(() => {
	const { isCreateZone, isCreateSector, setCreateZone, setCreateSector } = portState;
	const { setPointsZone, setPointsSector } = zonesState;

	const [isModalCreate, setModalCreate] = useState(false);
	const [isModalHelp, setModalHelp] = useState(false);

	const handleModalCreate = () => {
		setModalCreate(false);
	};

	const handleModalHelp = () => {
		setModalHelp(false);
	};

	const handleClose = () => {
		setModalCreate(false);
		setCreateZone(false);
		setCreateSector(false);
		reset();
	};

	const reset = () => {
		setPointsZone([]);
		setPointsSector([]);
	};

	const handleShowInstruction = () => {
		setModalHelp(true);
	};

	const createElements = () => {
		return (
			<>
				{isCreateSector && (
					<>
						<CreateSector
							handleShowInstruction={handleShowInstruction}
							setModalCreate={setModalCreate}
							handleClose={handleClose}
						/>
						<ModalCreateSector
							isModal={isModalCreate}
							handleModal={handleModalCreate}
							handleClose={handleClose}
						/>
					</>
				)}
				{isCreateZone && (
					<>
						<CreateZone
							handleShowInstruction={handleShowInstruction}
							setModalCreate={setModalCreate}
							handleClose={handleClose}
						/>
						<ModalCreateZone
							isModal={isModalCreate}
							handleModal={handleModalCreate}
							handleClose={handleClose}
						/>
					</>
				)}
			</>
		);
	};

	return (
		<>
			<div className={styles.upper}>
				<div className={styles.back}>
					<Box display={'flex'} onClick={handleClose}>
						<Box color={'white'}>Cancel</Box>
						<img className={styles.imgClose} src={close} />
					</Box>
				</div>
			</div>

			<Map />

			{createElements()}

			<ModalHelp isModal={isModalHelp} handleModal={handleModalHelp} />
		</>
	);
});

export default FullMap;
