import React, { useState, useEffect } from 'react';
import styles from './RadarView.module.scss';
import { Box } from '@mui/material';
import RadarMap from './RadarMap/RadarMap.js';
import portState from '../../store/portState';
import { logServerResponse, objToArr } from '../../helpers/helpers';
import MenuRadar from './MenuRadar/MenuRadar';
import zonesState from '../../store/zonesState';
import connectsState from '../../store/connectsState';
import { prepareZonesData } from '../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../api/prepareData/prepareSectorsData';
import { apiMainGet } from '../../api/mainApi';
import ErrorMessage from '../../helpers/ErrorMessage/ErrorMessage';

const socket = new WebSocket(`ws://${window.location.host}/monitor/`);

const RadarView = () => {
	const { setShips } = portState;
	const { setZones, setSectors } = zonesState;
	const { linkZones, linkFullSectors } = connectsState;

	const [isMap, setMap] = useState(true);

	useEffect(() => {
		getShipsFromSocket();

		const controller = new AbortController();

		getZones(linkZones);
		getFullSectors(linkFullSectors, controller);

		return () => {
			controller.abort();
		};
	}, []);

	const getShipsFromSocket = () => {
		socket.onmessage = (event) => {
			const response = JSON.parse(event.data);
			const editResponse = response.message.replace(/\\/g, '');
			const resultResponse = JSON.parse(editResponse);

			const { event_socket_type: type, event: eventSocket } = resultResponse;

			if (type === 'aisevent') {
				setShips(objToArr(eventSocket));
			}
		};
	};

	const getZones = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '', controller) => {
		apiMainGet(url, controller).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const handleClickMap = () => {
		setMap((prevMap) => !prevMap);
	};

	return (
		<div className={styles.parent}>
			<div className={styles.container}>
				<RadarMap isView={isMap} />
				<div className={styles.errorMessagePosition}>
					<ErrorMessage title="Warning">
						<span>There is no connection to the radar</span>
					</ErrorMessage>
				</div>

				<Box className={styles.menu02}>
					<MenuRadar handleViewMap={handleClickMap} isMap={isMap} />
				</Box>
			</div>
		</div>
	);
};

export default RadarView;
