import React, { useEffect, useState } from 'react';
import { Redirect, Route, Switch, useRouteMatch } from 'react-router-dom';
import styles from './ArchivePage.module.scss';
import Header from '../../components/Header/Header';
import Archive from '../../components/Archive/Archive';
import DownloadReport from '../../components/DownloadReport/DownloadReport';
import { Box } from '@mui/material';
import eventArchiveState from '../../store/eventArchiveState';
import connectsState from '../../store/connectsState';
import { observer } from 'mobx-react-lite';

const EVENT_TYPE_CONTROL_ZONE_CV = 'cv_message';
const LAST_TYPE_CONTROL_ZONE_CV = 'latest_cv_message';
const EVENT_TYPE_CONTROL_ZONE_AIS = 'ais_message';
const EVENT_TYPE_AIS_MOVE = 'ais_move_message';
const EVENT_TYPE_SOUND_EVENT = 'sound_message';
const LAST_TYPE_SOUND_EVENT = 'latest_sound_message';
const EVENT_TYPE_EMERGENCY = 'emergency_message';
const LAST_TYPE_EMERGENCY = 'latest_emergency_message';
const LAST_TYPE_CONTROL_ZONE_AIS = 'latest_ais_zone_message';
const LAST_TYPE_AIS_MOVE = 'latest_ais_message';

const GET_SOUND_EVENTS = 'sound_message';
const GET_CV_EVENTS = 'cv_message';
const GET_EMERGENCY_EVENTS = 'emergency_message';
const GET_AIS_ZONE_EVENTS = 'ais_zone_message';
const GET_AIS_EVENTS = 'ais_message';

export default observer(() => {
	const { path } = useRouteMatch();

	const [isReconnect, setIsReconnect] = useState(false);
	const [dataOne, setDataOne] = useState([]);
	const [dataTwo, setDataTwo] = useState([]);
	const [dataFour, setDataFour] = useState([]);
	const [dataFive, setDataFive] = useState([]);
	const [dataSix, setDataSix] = useState([]);
	const emptyArray = [];

	const rows = [
		dataOne,
		dataTwo,
		emptyArray,
		dataFour,
		dataFive,
		dataSix,
		emptyArray,
		emptyArray,
		emptyArray,
		emptyArray,
		emptyArray,
		emptyArray,
	];

	eventArchiveState.setEventsArchive(rows);

	const commandBack = (type) => (
		JSON.stringify({
			type: 'get_last_data',
			message: { data_type: type },
		})
	);

	useEffect(() => {
		let socket = '';
		const intervalId = setInterval(() => {
			try {
				const { wsArchiveEvents } = connectsState;

				socket = new WebSocket(wsArchiveEvents);

				socket.onopen = () => socketOpen(socket);
				socket.onmessage = (e) => socketMessage(e);
				socket.onerror = (e) => socketError(e);
				socket.onclose = (e) => socketClose(e);

				clearInterval(intervalId);
			} catch (error) {
				console.error(error);
			}
		}, 2000);

		return () => {
			try {
				socket.close();
			} catch (error) {
				console.error(error);
			}
			clearInterval(intervalId);
		};
	}, [isReconnect]);

	const socketOpen = (socket) => {
		const commands = {
			sound_message: commandBack(GET_SOUND_EVENTS),
			cv_message: commandBack(GET_CV_EVENTS),
			emergency_message: commandBack(GET_EMERGENCY_EVENTS),
			ais_zone_message: commandBack(GET_AIS_ZONE_EVENTS),
			ais_message: commandBack(GET_AIS_EVENTS),
		};

		for (const type of Object.keys(commands)) {
			socket.send(commands[type]);
		}
	};
	const socketMessage = (event) => {
		try {
			const { event_type: type, data: eventSocket } = JSON.parse(event.data);
			const eventTypes = {
				[EVENT_TYPE_CONTROL_ZONE_AIS]: (aisEvents = {}) => getDataControlZoneAis(aisEvents),
				[LAST_TYPE_CONTROL_ZONE_AIS]: (lastAisEvents = {}) => getDataLastControlZoneAis(lastAisEvents),

				[EVENT_TYPE_CONTROL_ZONE_CV]: (cvEvents = {}) => getDataControlZoneCv(cvEvents),
				[LAST_TYPE_CONTROL_ZONE_CV]: (lastCvEvents = []) => getDataLastControlZoneCv(lastCvEvents),

				[EVENT_TYPE_AIS_MOVE]: (aisEvents = {}) => getDataMoveAis(aisEvents),
				[LAST_TYPE_AIS_MOVE]: (lastAisEvents = {}) => getDataLastMoveAis(lastAisEvents),

				[EVENT_TYPE_SOUND_EVENT]: (soundEvents = {}) => getDataSoundEvents(soundEvents),
				[LAST_TYPE_SOUND_EVENT]: (soundEvents = []) => getDataLastSoundEvents(soundEvents),

				[EVENT_TYPE_EMERGENCY]: (emergencyEvents = {}) => getDataEmergencyEvents(emergencyEvents),
				[LAST_TYPE_EMERGENCY]: (emergencyEvents = []) => getDataLastEmergencyEvents(emergencyEvents),
			};

			eventTypes[type](eventSocket);
		} catch (error) {
			console.error(error.message);
		}
	};
	const socketError = (error) => {
		console.error(error.message);
	};
	const socketClose = (event) => {
		if (!event.wasClean) setIsReconnect((prevIsBreak) => !prevIsBreak);
	};

	const splitDataAis = (aisEvents = {}) => {
		const { mmsi, imo, shipClass_name, name, last_event: lastEvent } = aisEvents;

		const defLastEvent = {
			device_name: '',
			type_name: '',
			latitude: 0,
			longitude: 0,
			dateTime: '',
			zone_name: '',
			sector_name: '',
			vesselDirection: null,
			speed: null,
			sector_aispoints: [],
			zone_aispoints: [],
		};

		const {
			device_name,
			type_name,
			latitude,
			longitude,
			dateTime,
			zone_name,
			sector_name,
			vesselDirection,
			speed,
			sector_aispoints,
			zone_aispoints,
		} = lastEvent ?? defLastEvent;

		const editFieldEvent = {
			sector: sector_name,
			deviceName: `${device_name}`,
			zoneName: zone_name,
			shipName: name,
			shipClass: shipClass_name,
			eventType: type_name,
			time: dateTime,
			vesselDirection: vesselDirection,
			imo,
			mmsi,
			latitude,
			longitude,
			speed,
			sectorPoints: sector_aispoints,
			zonePoints: zone_aispoints,
		};

		return editFieldEvent;
	};

	const getDataControlZoneAis = (aisEvents = {}) => {
		const editEvent = {
			id: eventArchiveState.getTableOneId(),
			...splitDataAis(aisEvents),
		};

		setDataOne(prevDataOne => [editEvent, ...prevDataOne]);

		return (editEvent);
	};

	const getDataControlZoneCv = (cvEvents = {}) => {
		const {
			camera,
			dateTime,
			event_type,
			image_base64,
			latitude,
			longitude,
			ship_class,
			ship_imo,
			ship_name,
			vesselDirection,
			presetCv,
		} = cvEvents;

		const defPresetCv = { zone_name: '', zone_points: [] };
		const { zone_name, zone_points } = presetCv ?? defPresetCv;

		const editFieldEvent = {
			deviceName: `${camera}`,
			zoneName: zone_name,
			shipName: ship_name,
			shipClass: ship_class,
			eventType: event_type,
			time: dateTime,
			vesselDirection: vesselDirection,
			imo: ship_imo,
			latitude: latitude ?? 0,
			longitude: longitude ?? 0,
			image: image_base64,
			zonePoints: zone_points,
		};

		const editEvent = {
			id: eventArchiveState.getTableTwoId(),
			...editFieldEvent,
		};

		setDataTwo(prevDataTwo => [editEvent, ...prevDataTwo]);
		return editEvent;
	};

	const getDataLastControlZoneCv = (lastCvEvents = []) => {
		const data = lastCvEvents.map((event) => getDataControlZoneCv(event));

		setDataTwo(data);
	};

	const getDataMoveAis = (aisEvents = {}) => {
		const editEvent = {
			id: eventArchiveState.getTableFourId(),
			...splitDataAis(aisEvents),
		};

		setDataFour(prevDataFour => [editEvent, ...prevDataFour]);

		return (editEvent);
	};

	const getDataSoundEvents = (soundEvents = {}) => {
		const { device, state, dateTime, sound } = soundEvents;

		const editFieldEvent = {
			deviceName: `${device}`,
			time: dateTime,
			state,
			sound,
		};

		const editEvent = {
			id: eventArchiveState.getTableFiveId(),
			...editFieldEvent,
		};

		setDataFive(prevDataFive => [editEvent, ...prevDataFive]);

		return editEvent;
	};

	const getDataLastSoundEvents = (soundEvents = []) => {
		const data = soundEvents.map((event) => getDataSoundEvents(event));

		setDataFive(data);
	};

	const getDataEmergencyEvents = (emergencyEvents = {}) => {
		const { dateTime, device, value, state } = emergencyEvents;

		const editFieldEvent = {
			deviceName: `${device}`,
			time: dateTime,
			state,
			value,
		};

		const editEvent = {
			id: eventArchiveState.getTableSixId(),
			...editFieldEvent,
		};

		setDataSix(prevDataSix => [editEvent, ...prevDataSix]);

		return editEvent;
	};

	const getDataLastEmergencyEvents = (emergencyEvents = []) => {
		const data = emergencyEvents.map((event) => getDataEmergencyEvents(event));

		setDataSix(data);
	};

	const getDataLastControlZoneAis = (lastAisEvents = []) => {
		const data = lastAisEvents.map((event) => getDataControlZoneAis(event));

		setDataOne(data);
	};

	const getDataLastMoveAis = (lastAisEvents = []) => {
		const data = lastAisEvents.map((event) => getDataMoveAis(event));

		setDataFour(data);
	};

	return (
		<Box className={styles.parent}>
			<Header />

			<Box className={styles.pageBody}>
				<Switch>
					<Route path={`${path}/cards`} component={Archive} />
					<Route path={`${path}/downloadreport`} component={DownloadReport} />
					<Route exact path={path}>
						<Redirect to={`${path}/cards`} />
					</Route>
				</Switch>
			</Box>
		</Box>
	);
});
