import React, { useState } from 'react';
import styles from './DeleteZone.module.scss';
import { styled } from '@mui/material';
import ArrowBackIosOutlinedIcon from '@mui/icons-material/ArrowBackIosOutlined';
import zonesState from '../../../../../store/zonesState';
import connectsState from '../../../../../store/connectsState';
import { observer } from 'mobx-react-lite';
import { logServerResponse } from '../../../../../helpers/helpers';
import { prepareZonesData } from '../../../../../api/prepareData/prepareZonesData';
import { prepareSectorsData } from '../../../../../api/prepareData/prepareSectorsData';
import { apiMainDelete, apiMainGet } from '../../../../../api/mainApi';

const ModArrow = styled(ArrowBackIosOutlinedIcon)({
	marginRight: '20px',
	cursor: 'pointer',
	color: '#5F5F5F',

	'&:hover': {
		color: '#FF992C',
	},
});

const DeleteZone = observer(({ close = () => {} }) => {
	const { editZone, setEditZone, setZones, setSectors } = zonesState;

	const [nameZone, setNameZone] = useState('');
	const resultDelete = editZone.name === nameZone;

	const getZones = (url) => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных зон - успешно';
			const errorMsg = 'Не удалось получить данные зон';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const zones = prepareZonesData(data);
			setZones(zones);
		});
	};

	const getFullSectors = (url = '') => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const deleteZone = (url) => {
		apiMainDelete(url).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Удаление зоны - успешно';
			const errorMsg = 'Не удалось удалить зону';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
	};

	const handleDeleteZone = (id, name) => {
		const { linkZones, linkFullSectors } = connectsState;
		
		if (name === nameZone) {
			const url = `${linkZones}${id}/`;

			deleteZone(url);
			setEditZone({});
		}
		setTimeout(() => {
			getFullSectors(linkFullSectors);
			getZones(linkZones);
		}, 500);
	};

	return (
		<>
			<div className={styles.header}>
				<ModArrow onClick={close} />
				Deleting a zone
			</div>
			<div className={styles.warningDelete}>Are you sure you want to delete the zone - {editZone.name}?</div>
			<div className={styles.editDel}>
				<input
					className={styles.fieldTextDelete}
					placeholder='Enter zone name'
					onChange={(e) => setNameZone(e.target.value)}
				/>
			</div>
			<div className={styles.notDelete} onClick={close}>
				Come back
			</div>
			<br />
			<div
				className={`${styles.delete} ${resultDelete && styles.ok}`}
				onClick={() => resultDelete && handleDeleteZone(editZone.id, editZone.name)}
			>
				Delete zone
			</div>
		</>
	);
});

export default DeleteZone;
