import React, { useState } from 'react';
import styles from './ModalAccount.module.scss';
import { Modal } from '@mui/material';
import close from '../../../../assets/portsMenu/close.png';
import connectsState from '../../../../store/connectsState';
import { logServerResponse } from '../../../../helpers/helpers';
import { apiMainPost } from '../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const ModalAccount = ({ isModal, handleModal, handleClose, indexTitle = 0, id = null }) => {
	const { linkAddStream } = connectsState;

	const [nameStream, setNameStream] = useState('');
	const [urlStream, setUrlStream] = useState('');
	const [nameCamera, setNameCamera] = useState('');
	const [urlRecord, setUrlRecord] = useState('');
	const [durationTime, setDurationTime] = useState('');
	const [storeTime, setStoreTime] = useState('');
	const [lat, setLat] = useState('');
	const [lng, setLng] = useState('');

	const fields = [
		{ id: 1, title: 'Stream name', name: nameStream, action: (e) => setNameStream(e.target.value) },
		{ id: 2, title: 'Stream address', name: urlStream, action: (e) => setUrlStream(e.target.value) },
		{ id: 3, title: 'Camera name', name: nameCamera, action: (e) => setNameCamera(e.target.value) },
		{ id: 4, title: 'Stream address to write', name: urlRecord, action: (e) => setUrlRecord(e.target.value) },
		{ id: 5, title: 'Recording duration', name: durationTime, action: (e) => setDurationTime(e.target.value) },
		{ id: 6, title: 'Record retention time', name: storeTime, action: (e) => setStoreTime(e.target.value) },
		{ id: 7, title: 'Latitude', name: lat, action: (e) => setLat(e.target.value) },
		{ id: 8, title: 'Longitude', name: lng, action: (e) => setLng(e.target.value) },
	];

	const handleUpdate = () => {
		const data = {
			id: id,
			name: nameCamera,
			app: nameStream,
			edge: urlStream,
			ip: urlRecord,
			record_time: durationTime,
			record_del_timestamp: storeTime,
			lat: lat,
			lon: lng,
		};
		closeModal(data);
	};
	const handlePost = () => {
		const data = {
			app: nameStream,
			edge: urlStream,
			name: nameCamera,
			ip: urlRecord,
			time: durationTime,
			del_timestamp: storeTime,
			lat,
			lon: lng,
		};
		closeModal(data);
	};
	const closeModal = (data = {}) => {
		apiMainPost(linkAddStream, data).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Изменение устройства - успешно';
			const errorMsg = 'Не удалось изменить устройство';

			logServerResponse(isError, seccessMsg, errorMsg);
		});
		setNameStream('');
		setUrlStream('');
		setNameCamera('');
		setUrlRecord('');
		setDurationTime('');
		setStoreTime('');
		handleClose();
		setLat('');
		setLng('');
	};

	const titleArray = [
		{ btn: 'Create', title: 'Add', actionBtn: handlePost },
		{ btn: 'Save', title: 'Edit', actionBtn: handleUpdate },
	];

	return (
		<Modal open={isModal} closeAfterTransition Backdrop={{ timeout: 500 }} onClose={handleModal}>
			<div className={styles.container}>
				<div className={styles.header}>
					{titleArray[indexTitle]?.title} device
					<img className={styles.closeIcon} src={close} onClick={handleClose} />
				</div>

				<div className={styles.content}>
					{fields.map(({ title, name, action }) => (
						<div key={uuidv4()}>
							<div className={styles.nameInput}>{title}</div>
							<input className={styles.fieldText} value={name} onChange={(e) => action(e)} />
						</div>
					))}

					<div className={styles.modeBtn} onClick={titleArray[indexTitle]?.actionBtn}>
						{titleArray[indexTitle]?.btn} device
					</div>
				</div>
			</div>
		</Modal>
	);
};

export default ModalAccount;
