import { makeAutoObservable } from 'mobx';
import { makePersistable } from 'mobx-persist-store';

class radarsState {
	selectedRadarId = null;

	radars = [];
	aises = [];

	constructor() {
		makeAutoObservable(this);
	}

	setRadars = (radars = []) => {
		this.radars = radars;
	};

	setAises = (arr) => {
		this.aises = arr;
	};

	setSelectedRadarId = (radarId = null) => {
		this.selectedRadarId = radarId;
	};

	setAllRadars = (view) => {
		this.radars.forEach((radar) => {
			radar.isShow = view
		});
	};
	setAllAises = (view) => {
		this.aises.forEach((ais) => {
			ais.isShow = view
		});
	};
}

export default new radarsState();
