import React, { useState } from 'react';
import styles from './ArchiveButtons.module.scss';
import { ClickAwayListener } from '@mui/material';
import { observer } from 'mobx-react-lite';
import viewsState from '../../../../store/viewsState';
import FilterItem from './FilterItem/FIlterItem';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import ExpandLessIcon from '@mui/icons-material/ExpandLess';

const iconStyles = { height: '20px', width: '25px' };

const Filter = observer(() => {
	const { cameras, setCamerasSelected, setCamerasSelectedAll } = viewsState;

	const [isShow, setShow] = useState(false);
	const [selectedMenuFilterItem, setSelectedMenuFilterItem] = useState('All cameras');

	const color = isShow ? '#ff992c' : 'var(--colorText)';
	const handleCloseFilter = () => setShow(false);

	const handleShowFilter = () => {
		setShow(isPrevShoing => !isPrevShoing);
	};

	const icon = (Icon) => <Icon sx={iconStyles} onClick={handleShowFilter} />;

	return (
		<div className={styles.container}>
			<div className={styles.rightField}>Список:</div>

			<div style={{ color: `${color}` }} className={styles.selectHeader} onClick={() => setShow(!isShow)}>
				{selectedMenuFilterItem}
				{icon(isShow ? ExpandLessIcon : ExpandMoreIcon)}
			</div>

			{isShow && (
				<ClickAwayListener onClickAway={handleCloseFilter}>
					<div>
						<FilterItem
							data={cameras}
							setData={setCamerasSelected}
							setAllData={setCamerasSelectedAll}
							setSelectedMenuFilterItem={setSelectedMenuFilterItem}
							selectedMenuFilterItem={selectedMenuFilterItem}
						/>
					</div>
				</ClickAwayListener>
			)}
		</div>
	);
});

export default Filter;
