import styles from './FilterItem.module.scss';
import { SearchOutlined } from '@material-ui/icons';
import { InputBase } from '@mui/material';
import CheckIcon from '@material-ui/icons/Check';
import React, { useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

const FILTER_ITEM_ALL_CAMERAS = 'All cameras';
const FILTER_ITEM_EMPTY = 'Empty';

const FilterItem = ({ data = [], setData, setAllData, setSelectedMenuFilterItem, selectedMenuFilterItem = '' }) => {
	const [userFilter, setUserFilter] = useState('');

	const handleSelectedAllCam = () => {
		const index = data.findIndex(({ isSelected }) => isSelected === false);
		const isSelected = index !== -1;
		setAllData(data, isSelected);
		setSelectedMenuFilterItem(isSelected ? FILTER_ITEM_ALL_CAMERAS : FILTER_ITEM_EMPTY);
	};
	const handleSelectCamera = (camId = null) => (e) => {
		e.stopPropagation();

		try {
			const index = data.findIndex(({ id }) => id === camId);
			const { isSelected } = data[index];

			data[index].isSelected = !isSelected;
			setData([...data]);

			const countSelected = data.filter(({ isSelected }) => isSelected).length;

			const titleMsg = {
				0: FILTER_ITEM_EMPTY,
				[data.length]: FILTER_ITEM_ALL_CAMERAS,
				count: countSelected
			};

			const title = titleMsg[countSelected] ?? titleMsg.count;
			setSelectedMenuFilterItem(title);
		} catch (error) {
			console.error(error.message);
		}
	};

	return (
		<div className={styles.listArchive}>
			<div className={styles.containerFilter}>
				<div className={styles.search} aria-label="Search">
					<SearchOutlined />
					<InputBase
						type="text"
						placeholder="Search"
						value={userFilter}
						onChange={(e) => setUserFilter(e.target.value)}
					/>
				</div>
				<div className={styles.btnListFilter} onClick={handleSelectedAllCam}>
					<div>All cameras</div>
					{selectedMenuFilterItem === FILTER_ITEM_ALL_CAMERAS && <CheckIcon style={{ color: '#FF992C' }} />}
				</div>
				{data
					.filter(({ name }) => name.toLowerCase().includes(userFilter.toLowerCase()))
					.map(({ id, name, isSelected }) => {
						const opacity = +isSelected;

						return (
							<div key={uuidv4()} className={styles.btnListFilter} onClick={handleSelectCamera(id)}>
								<div>{name}</div>
								<CheckIcon
									style={{
										color: '#FF992C',
										opacity,
										transition: 'opacity 0.3s',
									}}
								/>
							</div>
						);
					})}
			</div>
		</div>
	);
};

export default FilterItem;
