import React from 'react';

const IconGrid = ({ width = 32, height = 32, color = '#656565' }) => {
	return (
		<svg width={width} height={height} viewBox="0 0 32 32" fill="none" xmlns="http://www.w3.org/2000/svg">
			<path d="M29 24H3V30H29V24Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
			<path d="M29 13H3V19H29V13Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
			<path d="M29 2H3V8H29V2Z" stroke={color} strokeWidth="2" strokeLinecap="round" strokeLinejoin="round" />
		</svg>
	);
};

export default IconGrid;
