import React, { useState } from 'react';
import { Box, IconButton, Typography, ListItemIcon, ListItemText } from '@mui/material';
import styles from './MenuRadar.module.scss';
import { CustomIcon } from '../../../helpers/helpers';
import arrowImage from '../../../assets/portsMenu/arrowNew.png';
import closeImg from '../../../assets/portsMenu/close.png';
import emptySelect from '../../../assets/portsMenu/emptyCheckBox.png';
import selectedItem from '../../../assets/portsMenu/noBorderCheckBox.png';
import ControlRadar from './ControlRadar/ControlRadar';

const MenuRadar = ({ handleViewMap, isMap, classModule = 'right' }) => {
	const [isOpen, setOpen] = useState(false);
	const icon = isMap ? selectedItem : emptySelect;

	const handleChangeVisible = () => {
		setOpen(prevIsOpen => !prevIsOpen);
	};

	const handleCloseMenu = () => {
		setOpen(false);
	};

	const listMenu = () => {
		return (
			<Box className={styles.container}>
				<Box className={styles.titleBox}>
					<Typography className={styles.menuTitle}>Control menu</Typography>

					<IconButton className={styles.iconButton} onClick={handleCloseMenu}>
						<CustomIcon name={'Close image'} icon={closeImg} size={22} />
					</IconButton>
				</Box>

				<Box className={styles.item}>
					<ListItemText disableTypography primary={<Typography className={styles.itemText}>Map display</Typography>} />

					<ListItemIcon sx={{ justifyContent: 'end' }} onClick={handleViewMap}>
						<Box className={styles.switchItem}>
							<CustomIcon icon={icon} name={'Switch Icon'} size={31} />
						</Box>
					</ListItemIcon>
				</Box>

				<ControlRadar/>
			</Box>
		);
	};

	return (
		<Box className={styles.radarMenu}>
			<Box className={`${styles.btnPos} ${styles[classModule]}`}>
				<IconButton className={`${styles.iconButton} ${isOpen && styles.active}`} onClick={handleChangeVisible}>
					<CustomIcon name={'Open/Close Arrow'} icon={arrowImage} size={32} />
				</IconButton>
			</Box>

			{isOpen && listMenu()}
		</Box>
	);
};

export default MenuRadar;
