import React, { useState } from 'react';
import styles from '../SelectCamerasView/SelectCamerasView.module.scss';
import { ClickAwayListener } from '@mui/material';
import { cardsSelect } from '../../data';
import CheckIcon from '@material-ui/icons/Check';
import { v4 as uuidv4 } from 'uuid';

const SelectCamerasView = ({ handleViews, showWindows, close, setSelectedIconId, selectIconId }) => {
	const [btnHover, setBtnHover] = useState(null);

	const handleEnter = (index = null) => (e) => {
		e.stopPropagation();
		setBtnHover(index);
	};

	const handleSelectedItem = (iconId = null) => (e) => {
		e.stopPropagation();

		const index = cardsSelect.findIndex(({ id }) => id === iconId);
		const { action } = cardsSelect[index];
		
		handleViews(action);
		setSelectedIconId(index);
	};

	return (
		<ClickAwayListener onClickAway={close}>
			<div className={styles.boxCard}>
				<div className={styles.list}>
					{cardsSelect.map((item, index) => {
						const { id, icon, title, action } = item;

						const isSelected = selectIconId === index;
						const isHovered = btnHover === index;
						const isSelectedItem = isSelected || isHovered;

						const color = isSelectedItem ? '#FF992C' : 'var(--colorText)';

						const isShowWindow = showWindows === action;

						return (
							<div
								key={uuidv4()}
								className={styles.listBtn}
								onMouseEnter={handleEnter(id)}
								onMouseLeave={handleEnter(null)}
								onClick={handleSelectedItem(id)}
							>
								{icon(color)}

								<div className={styles.text}>{title}</div>
								{isShowWindow && <CheckIcon style={{ color: '#FF992C' }} />}
							</div>
						);
					})}
				</div>
			</div>
		</ClickAwayListener>
	);
};

export default SelectCamerasView;
