import React, { useState } from 'react';
import { IconButton } from '@mui/material';
import styles from './ControlIcons.module.scss';
import SelectCameras from '../SelectCameras/SelectCameras';
import FullWinIcon from '../Card/Static/FullWinIcon';
import FullWinOffIcon from '../Card/Static/FullWinOffIcon';
import SettingsIcon from '../Card/Static/SettingsIcon';
import MapIcon from '../Card/Static/MapIcon';
import cardState from '../../../../store/cardState';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const ControlIcons = observer(({ transferDataModal, numberCard }) => {
	const [isSetting, setSetting] = useState(false);
	const [selected, setSelected] = useState(null);
	const [hovered, setHovered] = useState(null);

	const handleSetting = () => {
		setSetting((prevIsSetting) => !prevIsSetting);
	};

	const handleSelectedItem = (index = null, name = '') => {
		const tSelected = selected === index ? null : index;
		const aSelected = Number.isInteger(tSelected) ? name : '';

		setSelected(tSelected);
		icons[index].action(aSelected);
	};

	const handleHoveredItem = (index = null) => {
		const hoveredIndex = selected === index ? null : index;
		setHovered(hoveredIndex);
	};

	const handleCloseModalAndMap = () => {
		cardState.changeOpenModal();
		cardState.changeOpenMap(false);
	};

	const icons = [
		{
			id: 1,
			name: 'map',
			icon: (color = '#878787') => <MapIcon color={color} />,
			action: () => cardState.changeOpenMap(true),
		},
		{
			id: 3,
			name: 'settings',
			icon: (color = '#878787') => <SettingsIcon color={color} />,
			action: handleSetting,
		},
		{
			id: 4,
			name: 'windowSize',
			icon: (color = '#878787') =>
				cardState.isOpenModal ? <FullWinOffIcon color={color} /> : <FullWinIcon color={color} />,
			action: cardState.isOpenModal ? () => handleCloseModalAndMap() : transferDataModal,
		},
	];

	return (
		<div className={styles.btns}>
			{isSetting && (
				<div className={styles.settings}>
					Device selection
					<SelectCameras numberCard={numberCard} />
				</div>
			)}

			{icons.map(({ name, icon }, index) => {
				const isSelected = selected === index;
				const isHovered = hovered === index;
				const isActiveItem = isSelected || isHovered;
				
				const color = isActiveItem ? '#FF992C' : '#878787';
				const isShow = cardState.isOpenModal || index !== 0;

				return (
					isShow && (
						<IconButton
							key={uuidv4()}
							onMouseEnter={() => handleHoveredItem(index)}
							onMouseLeave={() => setHovered(null)}
							onClick={() => handleSelectedItem(index, name)}
						>
							{icon(color)}
						</IconButton>
					)
				);
			})}
		</div>
	);
});

export default ControlIcons;
