import { Grid } from '@mui/material';
import React from 'react';
import styles from './Multimedia.module.scss';
import plusImg from '../../../../assets/portsMenu/plus.png';
import minusImg from '../../../../assets/portsMenu/minus.png';
import volumeImg from '../../../../assets/portsMenu/volume.png';
import ArrowButton from '../ArrowButton/ArrowButton';

const defaultActions = {
	aPlus: () => {},
	aMinus: () => {},
	aVolume: () => {},
	aStopMes: () => {},
};

export default ({ actions = defaultActions }) => {
	const size = 30;

	const { gridContainer, gridItem } = styles;
	const { aPlus, aMinus, aVolume, aStopMes } = actions;

	return (
		<Grid container className={gridContainer} spacing={1}>
			<Grid item className={gridItem} xs={12}>
				<ArrowButton
					icon={plusImg}
					alt={'Plus'}
					size={size}
					actions={{ down: () => aPlus('zoom_up'), up: () => aStopMes() }}
				/>
			</Grid>

			<Grid item className={gridItem} xs={12}>
				<ArrowButton
					icon={minusImg}
					alt={'Minus'}
					size={size}
					actions={{ down: () => aMinus('zoom_down'), up: () => aStopMes() }}
				/>
			</Grid>

			<Grid item className={gridItem} xs={12}>
				<ArrowButton
					icon={volumeImg}
					alt={'Volume'}
					size={size}
					actions={{ down: () => aVolume('volume_down'), up: () => aStopMes() }}
				/>
			</Grid>
		</Grid>
	);
};
