import React from 'react';
import { Divider } from '@mui/material';
import portState from '../../../../store/portState';
import styles from './Symbols.module.scss';
import close from '../../../../assets/portsMenu/close.png';
import tanker from '../../../../assets/ships/tankerShip.png';
import cargo from '../../../../assets/ships/cargoShip.png';
import fishing from '../../../../assets/ships/fishingShip.png';
import passenger from '../../../../assets/ships/passengerShip.png';
import pilot from '../../../../assets/ships/pilotShip.png';
import tourists from '../../../../assets/ships/touristsShip.png';
import tow from '../../../../assets/ships/towShip.png';
import other from '../../../../assets/ships/otherShip.png';
import selectedShip from '../../../../assets/ships/selectedShip.png';
import line1 from '../../../../assets/newEls/Line 12.png';
import line2 from '../../../../assets/newEls/Line 13.png';
import zone1 from '../../../../assets/newEls/Rectangle 69.png';
import zone2 from '../../../../assets/newEls/Rectangle 70.png';
import camera from '../../../../assets/newEls/cameraMenu.png';
import radar from '../../../../assets/newEls/radarMenu.png';
import ais from '../../../../assets/newEls/aisMenu.png';
import { v4 as uuidv4 } from 'uuid';

const ships = [
	{ img: tanker, name: 'Tanker' },
	{ img: cargo, name: 'Cargo' },
	{ img: fishing, name: 'Fishing' },
	{ img: passenger, name: 'Passenger' },
	{ img: pilot, name: 'Pilot' },
	{ img: tourists, name: 'Tourists' },
	{ img: tow, name: 'Tow' },
	{ img: other, name: 'Other' },
	{ img: selectedShip, name: 'Selected Ship' },
];

const lines = [
	{ img: line1, name: 'Direction 1' },
	{ img: line2, name: 'Direction 2' },
];

const zones = [
	{ img: zone1, name: 'Zone' },
	{ img: zone2, name: 'Dangerous area' },
];

const lower = [
	{ img: radar, name: 'Radar' },
	{ img: ais, name: 'AIS' },
];

const Symbols = () => {
	const { setActiveMenu } = portState;

	return (
		<div className={styles.container}>
			<div className={styles.header}>
				Conventions
				<img className={styles.closeIcon} src={close} onClick={() => setActiveMenu('')} />
			</div>

			<Divider className={styles.divider} />

			<div className={styles.ships}>
				{ships.map(({ img, name }) => (
					<div key={uuidv4()} className={styles.shipItem}>
						<img className={styles.shipIcons} src={img} />
						{name}
					</div>
				))}
			</div>

			<Divider className={styles.divider} />

			<div className={styles.zones}>
				{lines.map(({ img, name }) => (
					<div key={uuidv4()} className={styles.zoneItem}>
						<img className={styles.lineIcons} src={img} />
						{name}
					</div>
				))}
				{zones.map(({ img, name }) => (
					<div key={uuidv4()} className={styles.zoneItem}>
						<img className={styles.zoneIcons} src={img} />
						{name}
					</div>
				))}
			</div>

			<Divider className={styles.divider} />

			<div className={styles.lower}>
				<div className={styles.zoneItem}>
					<img className={styles.cameraIcons} src={camera} />
					Camera
				</div>
				{lower.map(({ img, name }) => (
					<div className={styles.zoneItem} key={name}>
						<img className={styles.lowerItems} src={img} />
						{name}
					</div>
				))}
			</div>
		</div>
	);
};

export default Symbols;
