import React, { forwardRef, useEffect, useRef, useState } from 'react';
import L from 'leaflet';
import 'leaflet-rotatedmarker';
import { Marker } from 'react-leaflet';
import 'leaflet-rotatedmarker';
import tanker from '../../../../../../assets/ships/tankerShip.png';
import cargo from '../../../../../../assets/ships/cargoShip.png';
import fishing from '../../../../../../assets/ships/fishingShip.png';
import passenger from '../../../../../../assets/ships/passengerShip.png';
import pilot from '../../../../../../assets/ships/pilotShip.png';
import tourists from '../../../../../../assets/ships/touristsShip.png';
import tow from '../../../../../../assets/ships/towShip.png';
import other from '../../../../../../assets/ships/otherShip.png';
import 'leaflet/dist/leaflet.css';

const changeIcon = (type = '') => {
	switch (type) {
	case 'Танкер':
		return tanker;
	case 'Грузовое судно':
		return cargo;
	case 'Рыболовное судно':
		return fishing;
	case 'Пассажирское судно':
		return passenger;
	case 'Лоцманское судно':
		return pilot;
	case 'Прогулочное судно':
		return tourists;
	case 'Буксир':
		return tow;
	case 'Буксирное судно':
		return tow;

	default:
		return other;
	}
};

const RotatedMarker = forwardRef(({ ...props }, forwardRef) => {
	const markerRef = useRef();
	const { rotationAngle } = props;

	useEffect(() => {
		const marker = markerRef.current;

		if (marker) marker.setRotationAngle(rotationAngle);
	}, [rotationAngle]);

	return (
		<Marker
			ref={(ref) => {
				markerRef.current = ref;
				if (forwardRef) forwardRef.current = ref;
			}}
			{...props}
		/>
	);
});

const ShipOnMap = ({ ship = {} }) => {
	const { shipClass, latitude, longitude, vesselDirection } = ship;
	const [direction, setDirection] = useState(300);

	useEffect(() => {
		setDirection(vesselDirection + 270);
	}, [vesselDirection]);

	const shipIcon = (iconSize, type) => new L.Icon({ iconUrl: changeIcon(type), iconSize });

	return (
		Number.isFinite(latitude) && (
			<RotatedMarker
				icon={shipIcon([40, 12], shipClass)}
				position={[latitude, longitude]}
				rotationAngle={direction}
				rotationOrigin="center"
			/>
		)
	);
};

export default ShipOnMap;
