import React, { useState } from 'react';
import style from './HeaderNotifications.module.scss';
import NotificationsIcon from '../../../assets/images/notificationIcon.svg';
import Badge from '@mui/material/Badge';
import { IconButton, List, ListItem, Popover, styled } from '@mui/material';
import { PopoverItem } from './PopoverItem/PopoverItem';
import { NavLink } from 'react-router-dom';
import notificationsState from '../../../store/notificationsState';
import { observer } from 'mobx-react-lite';

const HeaderNotifications = observer(({ events }) => {
	const CustomBadge = styled(Badge)({
		'& .MuiBadge-badge': {
			color: 'white',
			backgroundColor: '#FF992C',
		},
	});

	const { setAllViewEvents, newNotifications } = notificationsState;

	const [anchorEl, setAnchorEl] = useState(null);

	const handleClick = (event) => {
		setAnchorEl(event.currentTarget);
	};

	const closePopover = () => {
		setAnchorEl(null);
	};
	const open = Boolean(anchorEl);

	return (
		<div className={style.container}>
			<IconButton aria-label="show new notificationsState" onClick={handleClick}>
				<CustomBadge badgeContent={newNotifications} max={10}>
					{open ? (
						<img src={NotificationsIcon} alt="NotificationsIcon" />
					) : (
						<img src={NotificationsIcon} alt="NotificationsIcon" className={style.iconStyle} />
					)}
				</CustomBadge>
			</IconButton>
			<Popover
				className={style.popoverStyle}
				id={open ? 1 : undefined}
				elevation={0}
				open={open}
				anchorEl={anchorEl}
				onClose={closePopover}
				anchorOrigin={{
					vertical: 'bottom',
					horizontal: 'left',
				}}
			>
				<List className={style.list}>
					<ListItem className={style.listItem}>
						<div className={style.listTitle}>Events</div>
						<NavLink className={style.navLinkWatch} to={'/Archive'} onClick={setAllViewEvents}>
							see all
						</NavLink>
					</ListItem>

					{events
						.filter(({ isNew }) => isNew)
						.slice(0, 10)
						.map((event) => {
							const { id } = event;
							return (
								<PopoverItem key={`Notification-${id}`} event={event} handleClosePopover={closePopover} />
							);
						})}
				</List>
			</Popover>
		</div>
	);
});
export default HeaderNotifications;
