import React, { useState } from 'react';
import { Document, Page } from 'react-pdf/dist/esm/entry.webpack';
import pdf from '../../../data/pdf/ownersManual.pdf';
import styles from './PdfFile.module.scss';
import VideoButtons from '../../../VideoButtons/VideoButtons';

const PdfFile = () => {

	const [numPages, setNumPages] = useState(null);
	const [currentPage, setCurrentPage] = useState(1);

	const onDocumentLoadSuccess = ({ numPages }) => {
		setNumPages(numPages);
	};
	const handleChangePage = (n = null) => {
		setCurrentPage((prev) => prev + n);
	};

	return (
		<div className={styles.page}>
			<VideoButtons
				currentPage={currentPage}
				handleChangePage={handleChangePage}
				lastPage={numPages}
			/>
			<div className={styles.pdfContainer}>
				<Document
					file={pdf}
					onLoadSuccess={onDocumentLoadSuccess}
				>
					<Page pageNumber={currentPage} height={1920}/>
				</Document>
			</div>
		</div>
	);
};

export default PdfFile;