import React from 'react';
import { LineChart, ResponsiveContainer, Legend, Tooltip, Line, XAxis, YAxis, CartesianGrid } from 'recharts';
import { AXIS_LABEL_ZONE_1, AXIS_LABEL_ZONE_2, AXIS_LABEL_ZONE_3, dataChart } from '../../../data';
import styles from './ChartLine.module.scss';

const ChartLine = () => {
	const CustomizedDot = ({ cx, cy }) => {
		return <circle cx={cx} cy={cy} r={3} stroke="#FF992C" strokeWidth={1.5} fill="var(--bgTheme)" />;
	};

	return (
		<div className={styles.chart}>
			<ResponsiveContainer>
				<LineChart data={dataChart} margin="5">
					<CartesianGrid strokeDasharray="3 3" />
					<XAxis dataKey="name" />
					<YAxis />
					<Tooltip
						contentStyle={{
							background: 'var(--bgTheme)',
							border: '0.5px solid var(--bgThemeBorder)',
							color: 'var(--bgThemeTitle)',
						}}
					/>
					<Legend />
					<Line type="monotone" dataKey={AXIS_LABEL_ZONE_1} stroke="#CC6900" dot={<CustomizedDot />} />
					<Line type="monotone" dataKey={AXIS_LABEL_ZONE_2} stroke="#F07C00" dot={<CustomizedDot />} />
					<Line type="monotone" dataKey={AXIS_LABEL_ZONE_3} stroke="#FF992C" dot={<CustomizedDot />} />
				</LineChart>
			</ResponsiveContainer>
		</div>
	);
};
export default ChartLine;
