import React, { useState, useEffect } from 'react';
import styles from './MainMenu.module.scss';
import { TextField, Autocomplete, InputAdornment, Box, Divider, styled } from '@mui/material';
import close from '../../../../assets/portsMenu/close.png';
import portState from '../../../../store/portState';
import searchImg from '../../../../assets/portsMenu/search.png';
import { CustomIcon } from '../../../../helpers/helpers';
import { observer } from 'mobx-react-lite';
import { creareNestedFilters, getFilteredData } from '../../../../helpers/FiltersHelp.js';
import ListMenu from './ListMenu/ListMenu';
import SortButton from '../../../../helpers/SortButton/SortButton';
import SortedIcon from '../../../ControlMenu/CameraViewControlPanel/AdditionalControlPanel/IconConponents/SortedIcon';
import { PORT_MENU_TITLE_SECTORS, PORT_MENU_TITLE_SHIPS } from '../../../../helpers/defaultData';
import { v4 as uuidv4 } from 'uuid';

const MuiAutocomplete = styled(Autocomplete)({
	'.MuiAutocomplete-inputRoot': {
		'& .MuiAutocomplete-endAdornment': {
			top: '50%',

			transform: 'translate(0, -50%)',
		},
	},
	'.MuiFormControl-root': {
		'& .Mui-focused .MuiOutlinedInput-notchedOutline': {
			borderColor: '#FF992C',
		},
	},
	'& .MuiButtonBase-root.MuiAutocomplete-clearIndicator': {
		color: 'var(--bgThemeButtonIcon)',
	},
});

const CustomTextField = styled(TextField)({
	'& label.Mui-focused': {
		backgroundColor: 'transparent',
	},
	'& .MuiInputBase-root': {
		padding: '5px 8px 5px 12px',

		borderColor: 'var(--bgThemeBorder)',
		borderRadius: 0,

		fontFamily: 'Inter',
		color: 'var(--colorText)',
		backgroundColor: 'var(--bgTheme)',
	},
	'& .MuiOutlinedInput-root': {
		'& fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
		'&:hover fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
		'&.Mui-focused fieldset': {
			borderColor: 'var(--bgThemeBorder)',
		},
	},
});

const inlineStyles = {
	textFieldStyle: {
		padding: '5px 8px 5px 12px',
		borderRadius: 0,

		fontFamily: 'Inter',
		color: 'var(--colorText)',

		backgroundColor: 'var(--bgTheme)',
	},
};

const nameSorted = (title = '') => {
	const sortTypes = {
		[PORT_MENU_TITLE_SHIPS]: 'Vessel type',
		[PORT_MENU_TITLE_SECTORS]: 'Sector type',
		default: '',
	};

	return sortTypes[title || 'default'];
};

const MainMenu = observer(({ dataList, resetData, createSector, setShowModal }) => {
	const { portMenuTitle, portMenuList, setPortMenuList, setActiveMenu } = portState;

	const [filterData, setFilterData] = useState([]);

	const shipFilters = ['type'];

	useEffect(() => {
		const filters = creareNestedFilters(portMenuList, shipFilters);
		setFilterData(filters);
		setFilteredList(filters);
	}, [portMenuTitle]);

	const setFilteredList = (filters = []) => {
		const filterData = getFilteredData(dataList, filters, shipFilters);
		setPortMenuList(filterData);
	};

	const filterList = (e, value) => {
		setPortMenuList(value ? [value] : resetData());
	};

	return (
		<>
			<div className={styles.header}>
				{portMenuTitle}
				<img className={styles.closeIcon} src={close} onClick={() => setActiveMenu('')} />
			</div>

			<Divider className={styles.divider} />

			<div className={styles.boxAutocomplite}>
				<MuiAutocomplete
					disablePortal
					id="combo-box-demo"
					key={uuidv4()}
					options={portMenuList}
					sx={{ mt: 2, mb: 2, width: '100%' }}
					forcePopupIcon={false}
					getOptionLabel={(option) => option.name}
					renderInput={(params) => {
						return (
							<Box sx={{ display: 'flex', justifyContent: 'space-between' }}>
								<CustomTextField
									{...params}
									variant="outlined"
									fullWidth
									placeholder="Search"
									InputProps={{
										...params.InputProps,
										style: inlineStyles.textFieldStyle,
										startAdornment: (
											<InputAdornment position="start">
												<CustomIcon name={'Search image'} icon={searchImg} size={15} />
											</InputAdornment>
										),
									}}
								/>
							</Box>
						);
					}}
					onChange={filterList}
				/>

				<SortButton
					icon={<SortedIcon />}
					title={nameSorted(portMenuTitle)}
					displayedFilters={filterData}
					setDisplayedFilters={setFilterData}
					action={(filters = []) => setFilteredList(filters)}
				/>
			</div>

			<ListMenu isZones={createSector} />

			{createSector && (
				<div className={styles.button}>
					<button className={styles.modBtn} onClick={() => setShowModal(true)}>
						Create
					</button>
				</div>
			)}
		</>
	);
});

export default MainMenu;
