import React, { useState } from 'react';
import styles from './ControlRadar.module.scss';
import {
	Collapse,
	List,
	ListItemButton,
	ListItemText,
	Typography,
	RadioGroup,
	FormControlLabel,
	Radio,
	Box,
	styled,
} from '@mui/material';
import ExpandLess from '@mui/icons-material/ExpandLess';
import ExpandMore from '@mui/icons-material/ExpandMore';
import zonesState from '../../../../store/zonesState';
import ItemRadarMenu from './ItemRadarMenu/ItemRadarMenu';
import { observer } from 'mobx-react-lite';
import { v4 as uuidv4 } from 'uuid';

const ModFormControl = styled(FormControlLabel)({
	'&.MuiFormControlLabel-root': {
		margin: '10px',
		'&.MuiTypography-root': {
			margin: '10px',
			fontSize: '14px',
		},
	},
});

const events = ['Event 1', 'Event 2', 'Event 3', 'Event 4', 'Event 5'];
const contrasts = ['Low', 'Medium', 'High'];
const speed = ['8 rpm', '16 rpm', '20 rpm'];

const ControlRadar = observer(() => {
	const { sectors } = zonesState;
	const [isOpen, setOpen] = useState(false);
	const [isOpenEvents, setOpenEvents] = useState(false);
	const [isOpenSettings, setOpenSettings] = useState(false);
	const [valueContrast, setValueContrast] = useState('Low');
	const [valueSpeed, setValueSpeed] = useState('8 rpm');

	const handleClickZones = () => {
		setOpen((prevIsOpen) => !prevIsOpen);
	};
	const handleClickEvents = () => {
		setOpenEvents((prevOpen) => !prevOpen);
	};
	const handleClickSettings = () => {
		setOpenSettings((prevSet) => !prevSet);
	};

	return (
		<div className={styles.containerMenu}>
			<List className={styles.list}>
				<ListItemButton className={styles.rcItem} selected={isOpenEvents} onClick={handleClickEvents}>
					<ListItemText
						disableTypography
						primary={<Typography className={styles.itemText}>Events</Typography>}
					/>
					{isOpenEvents ? <ExpandLess /> : <ExpandMore />}
				</ListItemButton>

				<Collapse in={isOpenEvents}>
					<List component="div" disablePadding>
						{events.map((name) => (
							<Box key={uuidv4()} className={styles.item}>
								<ListItemText
									disableTypography
									primary={<Typography className={styles.itemText}>{name}</Typography>}
								/>
							</Box>
						))}
					</List>
				</Collapse>
			</List>

			<List className={styles.list}>
				<ListItemButton className={styles.rcItem} selected={isOpen} onClick={handleClickZones}>
					<ListItemText disableTypography primary={<Typography className={styles.itemText}>Zones</Typography>} />
					{isOpen ? <ExpandLess /> : <ExpandMore />}
				</ListItemButton>

				<Collapse in={isOpen}>
					<List component="div" disablePadding>
						{sectors.map((sector) => (
							<ItemRadarMenu key={uuidv4()} item={sector} />
						))}
					</List>
				</Collapse>
			</List>

			<List className={styles.list}>
				<ListItemButton className={styles.rcItem} selected={isOpenSettings} onClick={handleClickSettings}>
					<ListItemText
						disableTypography
						primary={<Typography className={styles.itemText}>Settings</Typography>}
					/>
					{isOpenSettings ? <ExpandLess /> : <ExpandMore />}
				</ListItemButton>

				<Collapse in={isOpenSettings}>
					<List component="div" disablePadding>
						<Box className={styles.item}>
							<ListItemText
								disableTypography
								primary={<Typography className={styles.itemText}>Contrast</Typography>}
							/>
						</Box>
						<RadioGroup
							aria-labelledby="demo-controlled-radio-buttons-group"
							name="controlled-radio-buttons-group"
							value={valueContrast}
							onChange={(e) => setValueContrast(e.target.value)}
						>
							{contrasts.map((name) => (
								<ModFormControl
									key={uuidv4()}
									className={styles.itemSelect}
									value={name}
									control={<Radio style={{ color: '#FF992C' }} />}
									label={name}
								/>
							))}
						</RadioGroup>

						<Box className={styles.item}>
							<ListItemText
								disableTypography
								primary={<Typography className={styles.itemText}>Rotational speed</Typography>}
							/>
						</Box>
						<RadioGroup
							aria-labelledby="demo-controlled-radio-buttons-group"
							name="controlled-radio-buttons-group"
							value={valueSpeed}
							onChange={(e) => setValueSpeed(e.target.value)}
						>
							{speed.map((name) => (
								<ModFormControl
									key={uuidv4()}
									className={styles.itemSelect}
									value={name}
									control={<Radio style={{ color: '#FF992C' }} />}
									label={name}
								/>
							))}
						</RadioGroup>
					</List>
				</Collapse>
			</List>
		</div>
	);
});

export default ControlRadar;
