import React, { useState } from 'react';
import styles from './CustomTable.module.scss';
import { Box, Collapse, InputBase, List } from '@mui/material';
import ItemTable from './ItemTable/ItemTable';
import { SearchOutlined } from '@material-ui/icons';
import ModalDelete from '../../ModalAccount/ModalDelete/ModalDelete';
import ExpandMore from '@mui/icons-material/ExpandMore';
import { v4 as uuidv4 } from 'uuid';

export const buttonsStyle = {
	explainMore: {
		color: '#CFCFCF',
		width: '32px',
		height: '32px',
		marginRight: '15px',
		cursor: 'pointer',
		'&:active': {
			color: '#FF992C',
		},
		'&:hover': {
			color: '#FF992C',
		},
	},
};

const CustomTable = ({ title = '', dataCameras = [], actions }) => {
	const { update } = actions;

	const [isExpended, setIsExpended] = useState(false);
	const [userFilter, setUserFilter] = useState('');
	const [isModalDel, setModalDel] = useState(false);
	const [deleteId, setDeleteId] = useState(null);

	const handleExpandClick = () => setIsExpended((prevExp) => !prevExp);
	const closeModalDel = () => {
		setModalDel(false);
	};
	const handleDeleteDevice = (id = null) => {
		setModalDel(true);
		setDeleteId(id);
	};

	return (
		<div className={styles.gridContainer}>
			<ModalDelete isModal={isModalDel} deleteId={deleteId} handleClose={closeModalDel} />
			<div className={styles.head}>
				<p className={styles.title}>{title}</p>
				<div>
					<div className={styles.iconBox}>
						<ExpandMore
							sx={buttonsStyle.explainMore}
							expand={isExpended ? 1 : undefined}
							onClick={handleExpandClick}
							aria-expanded="button show more"
							aria-label="show more"
						/>
					</div>
				</div>
			</div>
			<Collapse in={isExpended} timeout="auto" unmountOnExit>
				<div className={styles.wrapper}>
					<Box className={styles.table}>
						<div className={styles.box}>
							<Box className={styles.searchContainer} aria-label="Search">
								<Box className={styles.box} aria-label="Search">
									<SearchOutlined className={styles.searchContainerIcon} />
									<InputBase
										className={styles.searchInput}
										style={{ color: 'var(--colorText)' }}
										type="text"
										placeholder="Search"
										onChange={(e) => setUserFilter(e.target.value)}
									/>
								</Box>
							</Box>
						</div>
						<List>
							{dataCameras.length ? (
								dataCameras
									.filter(({ name }) => name.toLowerCase().includes(userFilter.toLowerCase()))
									.map((camera) => {
										return (
											<ItemTable
												key={uuidv4()}
												item={camera}
												isShowSettings={false}
												handleDeleteDevice={handleDeleteDevice}
												action={update}
											/>
										);
									})
							) : (
								<p className={styles.listLink}>No connected devices</p>
							)}
						</List>
					</Box>
				</div>
			</Collapse>
		</div>
	);
};

export default CustomTable;
