import React, { useState, useEffect } from 'react';
import styles from './Records.module.scss';
import viewsState from '../../store/viewsState';
import { ThemeProvider, createTheme } from '@mui/material';
import { useTheme } from '../../Theme/darkTheme';
import PlayersArchive from './PlayersArchive/PlayersArchive';
import ItemPlayerModal from './ItemPlayerModal/ItemPlayerModal';
import ModalViews from '../../helpers/ModalViews/ModalViews';
import LowerMenu from './LowerMenu/LowerMenu';
import { observer } from 'mobx-react-lite';
import connectsState from '../../store/connectsState';
import { prepareCamerasFullData } from '../../api/prepareData/prepareCamerasData';
import { apiMainGet } from '../../api/mainApi';

const Records = observer(() => {
	const { selectRecord, clearRemovedItemsArchive } = viewsState;

	const [isTable, setTable] = useState(false);
	const [isShowModal, setShowModal] = useState(false);
	const [indexIcon, setIndexIcon] = useState(0);
	const [isLoad, setIsLoad] = useState(true);

	const mode = useTheme();

	const darkTheme = createTheme({
		palette: {
			mode: mode,
		},
	});

	useEffect(() => {
		if (!isLoad) return;

		const url = connectsState.linkCamerasList;
		const controller = new AbortController();

		getCameras(url, controller);

		return () => {
			controller.abort();
		};
	}, [isLoad, connectsState.linkCamerasList]);

	const getCameras = (url, controller) => {
		apiMainGet(url, controller).then(({ error, data }) => {
			if (error.isError) {
				viewsState.setMonitorCameras([]);
				return;
			}

			const { setMonitorCameras } = viewsState;
			
			setMonitorCameras(prepareCamerasFullData(data));
			setIsLoad(false);
		});
	};

	const handleTableArchive = () => {
		setTable(+!isTable);
		setIndexIcon(+!isTable);
		clearRemovedItemsArchive();
	};

	return (
		<ThemeProvider theme={darkTheme}>
			<div className={styles.parent}>
				<PlayersArchive setShowModal={setShowModal} isTable={isTable} />

				<LowerMenu
					isShowSorted={!isTable}
					indexIcon={indexIcon}
					handleTableArchive={handleTableArchive}
				/>

				<ModalViews isModal={isShowModal} setShowModal={setShowModal}>
					<ItemPlayerModal
						record={selectRecord}
						setShowModal={setShowModal}
					/>
				</ModalViews>
			</div>
		</ThemeProvider>
	);
});

export default Records;
