import { checkNegativeServerAns } from '../helpers/helpers';

export const apiMainGet = async (url = '', controller = new AbortController()) => {
	const serverData = {
		ans: { status: null, statusText: '' },
		error: { isError: false, errorText: '' },
		data: [],
	};

	const options = {
		method: 'GET',
		mode: 'cors',
		signal: controller.signal,
	};

	try {
		const answer = await fetch(url, options);

		const { status, statusText } = answer;
		const { ans } = serverData;

		serverData.ans = { ...ans, status, statusText };

		if (checkNegativeServerAns(status)) {
			throw new Error(`Fetch error ${status}: ${statusText}`);
		}

		serverData.data = await answer.json();
	} catch (error) {
		serverData.error = { isError: true, errorText: error.message };
	}

	return serverData;
};

export const apiMainPost = async (url = '', parameters = {}) => {
	const serverData = {
		ans: { status: null, statusText: '' },
		error: { isError: false, errorText: '' },
	};

	const options = {
		method: 'POST',
		headers: {
			'Content-Type': 'application/json',
			Accept: 'application/json',
		},
		body: JSON.stringify(parameters),
	};

	try {
		const answer = await fetch(url, options);

		const { status, statusText } = answer;
		const { ans } = serverData;
		serverData.ans = { ...ans, status, statusText };

		if (checkNegativeServerAns(status)) {
			throw new Error(`Fetch error ${status}: ${statusText}`);
		}
	} catch (error) {
		serverData.error = { isError: true, errorText: error.message };
	}

	return serverData;
};

