const counter = { eventsId: 0 };

class notificationsState {
	newNotifications = 0;
	notifId = 0;
	events = [];

	setView = (eventId = null, isState = false) => {
		const notification = this.events.find(({id}) => id === eventId);
		notification.isNew = isState;

		this.checkNewNotifications();
	};

	setAllViewEvents = () => {
		this.events.forEach(event => event.isNew = false);
		this.checkNewNotifications();
	}
	checkNewNotifications = () => {
		let countNewNotifications = 0;
		this.events.forEach(({ isNew }) =>
			countNewNotifications += isNew);
		this.newNotifications = countNewNotifications;
	};
	setEvent = (event) => {
		const {id, type_event, date} = event;
		return {id, typeEvent: type_event, date};
	};

	addEvent = (event) => {
		const {id, type_event, date} = event;
		this.events.unshift(event);
		this.checkNewNotifications();
		return {id, typeEvent: type_event, date};
	};
	getNotifId = () => this.notifId++;

}

export default new notificationsState();