import React from 'react';
import styles from './ItemRadarMenu.module.scss';
import { Box } from '@mui/material';
import { CustomIcon } from '../../../../../helpers/helpers.js';
import { observer } from 'mobx-react-lite';
import ZoneItemRadar from './ZoneItemRadar/ZoneItemRadar';
import zonesState from '../../../../../store/zonesState';
import emptySelect from '../../../../../assets/portsMenu/emptyCheckBox.png';
import selectedItem from '../../../../../assets/portsMenu/noBorderCheckBox.png';
import { v4 as uuidv4 } from 'uuid';

const ItemRadarMenu = observer(({ item }) => {
	const { setVisibleSector } = zonesState;
	const { id, name, isShow, zones } = item;

	const icon = {
		true: selectedItem,
		false: emptySelect,
	};

	return (
		<>
			<div className={styles.List}>
				<div className={styles.block} onClick={() => setVisibleSector(id, !isShow)}>
					<Box className={styles.switchItem}>
						<CustomIcon icon={icon[isShow]} name={'Switch Icon'} size={31} />
					</Box>
					<div className={styles.ship}>{name}</div>
				</div>
			</div>

			{zones?.map((zone) => {
				return <ZoneItemRadar key={uuidv4()} sector={id} zone={zone} />;
			})}
		</>
	);
});

export default ItemRadarMenu;
