import React, { useState } from 'react';
import styles from './ModalCreateSector.module.scss';
import { Modal } from '@mui/material';
import { observer } from 'mobx-react-lite';
import zonesState from '../../../../../store/zonesState';
import connectsState from '../../../../../store/connectsState';
import close from '../../../../../assets/portsMenu/close.png';
import { logServerResponse } from '../../../../../helpers/helpers';
import { prepareSectorsData } from '../../../../../api/prepareData/prepareSectorsData';
import { apiMainGet, apiMainPost } from '../../../../../api/mainApi';
import { v4 as uuidv4 } from 'uuid';

const ModalCreateSector = observer(({ isModal, handleModal, handleClose }) => {
	const { pointsSector, sectorTypes, setSectors } = zonesState;
	const { linkSectors } = connectsState;

	const [nameSector, setNameSector] = useState('');
	const [typeSector, setTypeSector] = useState(sectorTypes[0]?.id ?? null);

	const getFullSectors = (url) => {
		apiMainGet(url).then(({ data, error }) => {
			const { isError } = error;

			const seccessMsg = 'Получение данных о секторах - успешно';
			const errorMsg = 'Не удалось получить данные о секторах';

			logServerResponse(isError, seccessMsg, errorMsg);

			if (isError) return;

			const sectors = prepareSectorsData(data);
			setSectors(sectors);
		});
	};

	const addSectorApi = (link, zone) => {
		apiMainPost(link, zone).then(({ error }) => {
			const { isError } = error;

			const seccessMsg = 'Добавление сектора - успешно';
			const errorMsg = 'Не удалось добавить сектор';

			logServerResponse(isError, seccessMsg, errorMsg);
		});

		handleClose();

		setTimeout(() => {
			const { linkFullSectors } = connectsState;

			getFullSectors(linkFullSectors);
		}, 500);
	};

	const handleCreateSector = () => {
		const sector = {
			name: nameSector,
			type: typeSector,
			aisPoints: pointsSector,
		};
		addSectorApi(linkSectors, sector);
	};

	return (
		<Modal open={isModal} onClose={handleModal} closeAfterTransition>
			<div className={styles.container}>
				<div className={styles.header}>
					Sector Settings
					<img className={styles.closeIcon} src={close} onClick={handleModal} />
				</div>

				<div className={styles.content}>
					<div className={styles.nameInput}>Sector name</div>
					<input className={styles.fieldText} maxLength="16" onChange={(e) => setNameSector(e.target.value)} />

					<div className={styles.nameInput}>Sector type</div>

					<select className={styles.fieldText} value={typeSector} onChange={(e) => setTypeSector(e.target.value)}>
						{sectorTypes.map(({ id, name }) => (
							<option key={uuidv4()} value={id}>
								{name}
							</option>
						))}
					</select>

					<button className={styles.modBtn} onClick={handleCreateSector}>
						Create Sector
					</button>
				</div>
			</div>
		</Modal>
	);
});

export default ModalCreateSector;
