import React, { useRef, useState, useEffect } from 'react';
import { Box, Modal } from '@mui/material';
import styles from './CameraViewPage.module.scss';
import ControlMenu from '../../components/ControlMenu/ControlMenu';
import viewsState from '../../store/viewsState';
import { observer } from 'mobx-react-lite';
import ModalAddPreset from '../../components/ControlMenu/CameraViewControlPanel/Presets/ModalAddPreset/ModalAddPreset';
import useWindowDimensions from '../../useHooks/useWindowDimensions';
import canvasState from '../../store/canvasState';
import { headerHeight } from '../../globalStyles/vars.module.scss';
import Polygons from '../../components/Canvas/ChangeFigure/Polygons';
import connectsState from '../../store/connectsState';

export default observer(() => {
	const { cameras, selectedCameraId, isPresetSelected, isVisibleModalAddPreset, isShowControlMenu } = viewsState;
	const { isCreatePolygon, isShowCanvas } = canvasState;

	const canvasRef = useRef(null);

	const [width, height] = useWindowDimensions();

	const [isReconnect, setIsReconnect] = useState(false);
	const [stream, setStream] = useState('');

	const isShowRightMenu = isFinite(selectedCameraId);

	const h = height - headerHeight.replace(/\D/g, '');
	const changeValue = width - 332 * isShowControlMenu;
	const boxSize = { w: changeValue, h };

	const newHeightCanvas = (9 / 16) * changeValue;
	const newWidthCanvas = (16 / 9) * h;
	const compareHeight = newHeightCanvas < h;
	const boxSizeCanvas = { w: compareHeight ? changeValue : newWidthCanvas, h: compareHeight ? newHeightCanvas : h };

	const msInSec = 1000;
	const wsReconDelay = 2 * msInSec;

	useEffect(() => {
		const index = cameras.findIndex(({ id }) => id === selectedCameraId);
		if (index === -1) return;

		const url = cameras[index].strims?.ai ?? '';

		if (!url) return;

		setStream(url);
	}, [selectedCameraId]);

	useEffect(() => {
		if (!isShowCanvas) return;

		canvasState.setCanvas(canvasRef.current);
	}, [selectedCameraId, isShowCanvas]);

	useEffect(() => {
		const { w, h } = boxSizeCanvas;
		if (w !== 0) canvasState.setCanvasReSize(w, h);

		if (isShowCanvas) new Polygons(canvasState.canvas);
	}, [width, height, changeValue]);

	useEffect(() => {
		if (!isShowCanvas) return;
		if (!isPresetSelected) return;

		new Polygons(canvasState.canvas);
	}, [isShowControlMenu, isShowCanvas, selectedCameraId, isPresetSelected]);

	useEffect(() => {
		let socket = '';
		const intervalId = setInterval(() => {
			try {
				const { cameras, selectedCameraId } = viewsState;
				const { wsLinkCreatePreset } = connectsState;

				const { cameraKey } = cameras.find(({ id }) => id === selectedCameraId);

				const wsLink = `${wsLinkCreatePreset}${cameraKey}/`;
				socket = new WebSocket(wsLink);

				socket.onmessage = (e) => socketMessage(e);
				socket.onerror = (e) => socketError(e);
				socket.onclose = (e) => socketClose(e);

				clearInterval(intervalId);
			} catch (error) {
				console.error(error);
			}
		}, wsReconDelay);

		return () => {
			try {
				socket.close();
			} catch (error) {
				console.error(error.message);
			}
			clearInterval(intervalId);
		};
	}, [selectedCameraId, isReconnect]);

	const socketMessage = (event) => {
		try {
			const { command } = JSON.parse(event.data);

			const actions = {
				preset_updated: () => viewsState.changeUpdateData(),
				preset_removed: () => viewsState.changeUpdateData(),
				camera_updated: () => viewsState.changeUpdateData(),
			};

			actions[command]();
		} catch (error) {
			console.error(error.message);
		}
	};
	const socketError = (error) => {
		console.error(error.message);
	};
	const socketClose = (event) => {
		if (!event.wasClean) setIsReconnect((prevIsBreak) => !prevIsBreak);
	};

	const handleCloseModal = () => {
		viewsState.setVisibleModalAddPreset(false);
	};

	return (
		<div className={styles.newContainer}>
			<Box className={styles.cameraVievPage}>
				<Box className={styles.container}>
					<Box sx={{ position: 'relative' }}>
						<div
							style={{
								width: boxSize.w,
								height: boxSize.h,

								display: 'flex',
								justifyContent: 'center',
								alignItems: 'center',

								background: '#000',
							}}
						>
							<div
								style={{
									width: boxSizeCanvas.w,
									height: boxSizeCanvas.h,
									backgroundImage: `url(${stream})`,
									backgroundRepeat: 'no-repeat',
									backgroundPosition: 'center',
									backgroundSize: 'cover',
								}}
							/>
						</div>

						{isShowCanvas && (
							<>
								<Box className={styles.canvasBox}>
									<canvas ref={canvasRef} width={boxSizeCanvas.w} height={boxSizeCanvas.h} />
								</Box>

								<span className={styles.tempButton}>
									{`Polygons Mode: ${isCreatePolygon ? 'Edit' : 'Display'}`}
								</span>
							</>
						)}
					</Box>
				</Box>

				<Modal
					aria-labelledby="transition-modal-title"
					aria-describedby="transition-modal-description"
					open={isVisibleModalAddPreset}
					onClose={handleCloseModal}
				>
					<Box className={styles.modalAddPresetBox}>
						<ModalAddPreset />
					</Box>
				</Modal>
			</Box>

			{isShowRightMenu && (
				<Box className={styles.menu02}>
					<ControlMenu />
				</Box>
			)}
		</div>
	);
});
