import React, { useEffect, useRef, useState } from 'react';
import { Box, Icon, IconButton, Typography } from '@mui/material';
import styles from './ControlMenu.module.scss';
import closeImg from '../../assets/portsMenu/close.png';
import CameraViewControlPanel from './CameraViewControlPanel/CameraViewControlPanel';
import AdditionalControlPanel from './CameraViewControlPanel/AdditionalControlPanel/AdditionalControlPanel';
import ReconnectTime from './CameraViewControlPanel/ReconnectTime/ReconnectTime';
import viewsState from '../../store/viewsState';
import Presets from './CameraViewControlPanel/Presets/Presets';
import arrowImage from '../../assets/portsMenu/arrowNew.png';

import PolygonMenu from './CameraViewControlPanel/PolygonMenu/PolygonMenu';
import useWindowDimensions from '../../useHooks/useWindowDimensions';
import canvasState from '../../store/canvasState';
import AiIcon from './CameraViewControlPanel/AdditionalControlPanel/IconConponents/AiIcon';
import PrcIcon from './CameraViewControlPanel/AdditionalControlPanel/IconConponents/PrcIcon';
import { observer } from 'mobx-react-lite';
import ClockIcon from './CameraViewControlPanel/AdditionalControlPanel/IconConponents/ClockIcon';

const CustomIcon = ({ icon, name, size = 42 }) => (
	<Icon sx={{ fontSize: size }}>
		<img src={icon} alt={name} />
	</Icon>
);

const BUTTON_NAME_POLYGON_MENU = 'Polygon Menu';
const BUTTON_NAME_RECONNECT_TIME = 'Reconnect Time';
const BUTTON_NAME_PRESETS = 'Presets';

export default observer(({ openIcon = arrowImage, classModule = 'right' }) => {
	const refCont = useRef();
	const refTitle = useRef();
	const refFooter = useRef();

	const {isShowControlMenu, menuNameControl} = viewsState;
	const {setShowControlMenu, setNameMenuControl} = viewsState;

	const [width, height] = useWindowDimensions();
	
	const [contentHeight, setContentHeight] = useState('100%');

	useEffect(() => {
		if (!isShowControlMenu) return;

		const { clientHeight: contentCH } = refCont.current;
		const { clientHeight: titleCH } = refTitle.current;
		const { clientHeight: footerCH } = refFooter.current;

		setContentHeight(contentCH - (titleCH + footerCH));
	}, [isShowControlMenu, width, height]);

	useEffect(() => {
		const isCorrectMenu = menuNameControl === BUTTON_NAME_POLYGON_MENU;
		canvasState.setCanvasVisible(isCorrectMenu);
	}, [menuNameControl]);
	
	const buttonPanel = [
		{
			id: 1,
			name: BUTTON_NAME_POLYGON_MENU,
			icon: (color = 'var(--bgButtonBorder)') => <AiIcon color={color} />,
			action: () => handleClickAdditMenu(BUTTON_NAME_POLYGON_MENU),
			isDisabled: false,
		},
		{
			id: 2,
			name: BUTTON_NAME_PRESETS,
			icon: (color = 'var(--bgButtonBorder)') => <PrcIcon color={color} />,
			action: () => handleClickAdditMenu(BUTTON_NAME_PRESETS),
			isDisabled: false,
		},
		{
			id: 3,
			name: BUTTON_NAME_RECONNECT_TIME,
			icon: (color = 'var(--bgButtonBorder)') => <ClockIcon color={color} />,
			action: () => handleClickAdditMenu(BUTTON_NAME_RECONNECT_TIME),
			isDisabled: false,
		},
	];

	const handleChangeVisible = () => {
		setShowControlMenu(!isShowControlMenu);
	};

	const handleCloseMenu = () => {
		setShowControlMenu(false);
	};

	const handleClickAdditMenu = (name = '') => {
		setNameMenuControl(name);
	};

	function list() {
		const { container, titleBox, iconButton, menuTitle, footerBox } = styles;
		const { selectedCameraId, cameras } = viewsState;

		const index = cameras.findIndex(({ id }) => id === selectedCameraId);

		if (!cameras[index]) {
			viewsState.setShowControlMenu(false);
			return;
		}

		const { reconnectTime, presets } = cameras[index].controlMenu;

		const buttonIndex = buttonPanel.findIndex(({ name }) => name === menuNameControl);

		const title = 'Control menu';
		const additMenuComponents = {
			[BUTTON_NAME_POLYGON_MENU]: <PolygonMenu />,
			[BUTTON_NAME_RECONNECT_TIME]: (
				<ReconnectTime
					items={reconnectTime}
					action={(selectRecId = null) => handleChangeReconnectTime(selectRecId)}
				/>
			),
			[BUTTON_NAME_PRESETS]: (
				<Presets
					items={presets}
					actions={{
						selectId: (selectPresId = null) => handleChangeSelectedPreset(selectPresId),
						addPreset: (preset = {}) => handleAddPreset(preset),
						changeName: (presId = null, name = '') => handleChangeName(presId, name),
						deletePreset: (presId = null) => handleDeletePreset(presId),
					}} 
				/>
			),
		};

		const handleChangeReconnectTime = (selectRecId = null) => {
			viewsState.setChangeReconnectTime(selectedCameraId, selectRecId);
		};

		const handleChangeSelectedPreset = (selectPresId = null) => {
			viewsState.setSelectPresetId(selectPresId);
			viewsState.setSelectedPreset(selectedCameraId, selectPresId);
		};

		const handleAddPreset = (preset = {}) => {
			viewsState.addPreset(selectedCameraId, preset);
		};

		const handleChangeName = (presId = null, name = '') => {
			viewsState.setPresetName(selectedCameraId, presId, name);
		};

		const handleDeletePreset = (presId = null) => {
			viewsState.deletePreset(selectedCameraId, presId);
			
			canvasState.setCanvasVisible(viewsState.isPresetSelected);
		};

		return (
			<Box className={container} ref={refCont}>
				<Box className={titleBox} ref={refTitle}>
					<Typography className={menuTitle}>{title}</Typography>

					<IconButton className={iconButton} onClick={handleCloseMenu} disableRipple>
						<CustomIcon name={'Close image'} icon={closeImg} size={22} />
					</IconButton>
				</Box>

				<div style={{ height: contentHeight }}>
					{additMenuComponents[menuNameControl]}
				</div>

				<Box className={footerBox} ref={refFooter}>
					<AdditionalControlPanel buttonPanel={buttonPanel} menuIndex={buttonIndex} />
					<CameraViewControlPanel />
				</Box>
			</Box>
		);
	}

	return (
		<Box className={styles.searchMenu}>
			<Box className={`${styles.btnPos} ${styles[classModule]}`}>
				<IconButton className={`${styles.iconButton} ${isShowControlMenu && styles.active}`} onClick={handleChangeVisible}>
					<CustomIcon name={'Open/Close Arrow'} icon={openIcon} size={32} />
				</IconButton>
			</Box>

			{isShowControlMenu && list()}
		</Box>
	);
});
